import React, { useState } from "react";
import { Link, useLocation } from "react-router-dom";
import { connect, useSelector } from "react-redux";
import Logo from "../Logo";
import YandexMapThree from "../../features/modal/YandexMapModalThree";
import { openModal } from "../../../actions/modal";
import getLocation from "../../features/getAddress";
import ChangeLanguageTwo from "../../features/ChangeLanguageTwo";
import { useTranslation } from "react-i18next";
import SearchFormThree from "../partials/SearchFormThree";
import HeaderFixedMenu from "../Menus/Mobile/HeaderFixedMenu";
import market from "../../../config/market";
import { getCartCount } from "../../../services";
import BackButton from "../BackButton";
import { LinkComponent } from "../Menus/Desktop/LinkComponent";
function Header(props) {
	const { t } = useTranslation();

	const verticalMenu = useSelector((state) => state?.menu?.menuHorizontal);
	const [menuCatalog, setMenuCatalog] = useState(false);
	const {
		openModal,
		marketData: { settings },
		location: { address },
	} = props;
	const cartlist = useSelector((state) => state.cartlist.cart);
	const { productList } = useSelector((state) => state.choosenProducts);
	const { token } = useSelector((state) => state.user.tokenData);
	const icons = market.config.icons;
	const location = useLocation();
	function openMapModal(e) {
		openModal("map");
		e.preventDefault();
	}
	if (!address) {
		getLocation();
	}

	return (
		<header onClick={() => setMenuCatalog(false)} className={`header-four`}>
			<div className="header-top">
				<div className="container">
					<div className="header-left mr-5">
						<Link
							to={`${process.env.PUBLIC_URL}`}
							className="locationBut"
						>
							<p
								onClick={openMapModal}
								className="d-flex align-items-center"
							>
								<img
									className="mr-2"
									src={market.config.icons.location}
									alt="location"
								/>
								{address
									? `${address.administrativeArea}, ${address.name}`
									: t("address-not-selected")}
							</p>
						</Link>
					</div>
					<div className="header-right">
						{verticalMenu
							.slice(2)
							.reverse()
							.map((item, i) => (
								<div className="info" key={i}>
									<LinkComponent menu={item} />
								</div>
							))}

						<ChangeLanguageTwo />
					</div>
				</div>
			</div>

			<div className="container">
				<div className="header-middle sticky-header row">
					<div className="col-3 d-flex align-items-center">
						<div className="header-left">
							<Logo logo={settings.mainLogo} />
						</div>
						<button
							onClick={(e) => {
								e.stopPropagation();
								setMenuCatalog(!menuCatalog);
							}}
							className="btn-catalog btn btn-primary"
						>
							<i className="icon-bars"></i>
							<img
								src="/assets/images/vibo/menu.svg"
								alt="menu icon"
								width="25"
								height="25"
							/>
							<span>{t("catalog")}</span>
						</button>
					</div>
					<div className="col-9 d-flex align-items-center">
						{location.pathname === "/" ? null : (
							<BackButton icon={market.config.icons.backArrow} />
						)}
						<div className="header-center">
							<div className="search-wrapper d-flex align-items-center">
								<SearchFormThree />
							</div>
						</div>

						<div className="header-right">
							<Link className="header-right_icon" to="/markets">
								<img
									src={icons.markets}
									alt="market svg"
									width="25"
									height="25"
								/>
								<span className="icon-name">
									{t("bazaars")}
								</span>
							</Link>
							<Link
								className="header-right_icon"
								to={`${token ? "/profile" : "/login"}`}
							>
								<img
									src={icons.user}
									alt="userimage"
									width="25"
									height="25"
								/>
								<span className="icon-name">
									{token ? t("profile") : t("login")}
								</span>
							</Link>
							<Link
								className="header-right_icon"
								to="/choosen-products-list"
							>
								<div className="position-relative">
									<img
										src={icons.favourites}
										alt="heart"
										style={{
											width: "22px",
											height: "22px",
										}}
									/>
									{productList.length > 0 ? (
										<span>{productList.length}</span>
									) : null}
								</div>
								<span className="icon-name">
									{t("favourites")}
								</span>
							</Link>
							<Link
								className="header-right_icon"
								to={`${token ? "/checkout" : "/cart"}`}
							>
								<div className="position-relative">
									<img
										src={icons.cart}
										alt="cartsvgimage"
										width="25"
										height="25"
									/>
									{cartlist?.length ? (
										<span className="cart-count">
											{getCartCount(cartlist)}
										</span>
									) : null}
								</div>
								<span className="icon-name">{t("basket")}</span>
							</Link>

							{market.config.home === 4 &&
							process.env.REACT_APP_PLATFORM === "mobile" ? (
								<Link
									className="header-right_icon check_out"
									to={`${token ? "/checkout" : "/cart"}`}
								>
									<div className="position-relative">
										<img
											src={icons.cart}
											alt="cartsvgimage"
											width="25"
											height="25"
										/>
										{cartlist?.length ? (
											<span className="cart-count">
												{getCartCount(cartlist)}
											</span>
										) : null}
									</div>
									<span className="icon-name">
										{t("basket")}
									</span>
								</Link>
							) : (
								<ChangeLanguageTwo />
							)}
						</div>
					</div>
				</div>

				<YandexMapThree />
			</div>
			{menuCatalog ? (
				<div>
					<HeaderFixedMenu setMenuCatalog={setMenuCatalog} />
				</div>
			) : null}
		</header>
	);
}

function mapStateToProps(state) {
	return {
		marketData: state.marketData,
		phoneNumber: state.marketData.phoneNumber,
		user: state.user.user,
		location: state.user.location,
	};
}

export default connect(mapStateToProps, { openModal })(Header);
