import React, { useState } from "react";
import { Link } from "react-router-dom";
import { connect, useSelector } from "react-redux";
import { dt } from "../../../i18n";
import MediaImage from "../../features/product/MediaImage";
import { LinkComponent } from "../Menus/Desktop/LinkComponent";

function SocialLinks(socialLinks) {
	if (!socialLinks) {
		return "";
	} else {
		return Object.entries(socialLinks).map(([key, value]) => {
			return value ? (
				<a key={key} target="blank" href={value}>
					<img
						src={`/assets/images/vibo/${key}.svg`}
						alt={`${key} icon`}
						width="20"
						height="20"
					/>
				</a>
			) : null;
		});
	}
}

function Footer({ footer, settings }) {
	const marketData = useSelector((state) => state.marketData);
	const [collapse, setCollapse] = useState(0);
	const footerLogo = marketData.settings.footerLogo;
	const verticalMenu = useSelector((state) => state?.menu?.menuHorizontal);
	const payments = useSelector(
		(state) => state.marketData?.cashdesk?.paymentMethods
	);
	return (
		<footer className="footer footer-three">
			<div className="footer-middle">
				<div className="container">
					<div className="footer-info">
						<div className="widget widget-about">
							<div className="footer-logo">
								<MediaImage
									image={footerLogo}
									largeSize="300x_"
									mediumSize="150x_"
									smallSize="150x_"
									alt="Footer Logo"
								/>
							</div>
							<div className="widget-call d-none">
								<a
									href={`tel:${
										settings.phoneNumbers &&
										settings.phoneNumbers[0]
									}`}
								>
									{settings.phoneNumbers &&
										settings.phoneNumbers[0]}
								</a>
							</div>
							<div className="widget-email d-none">
								<a href={`email:${settings.email}`}>
									{settings.email}
								</a>
							</div>
							<div className="mt-auto mb-5 social-links-block d-none">
								<div className="footer-social-links d-flex align-items-center justify-content-start">
									{marketData.settings.socialLinks
										? SocialLinks(
												marketData.settings.socialLinks
										  )
										: null}
								</div>
							</div>
							<div className="payment-methods">
								<div className="payment-method">
									<img
										src="/assets/images/vibo/visa.png"
										alt="visa"
										width="50"
									/>
								</div>
								<div className="payment-method">
									<img
										src="/assets/images/vibo/cash.png"
										alt="cash"
										width="50"
									/>
								</div>
								<div className="payment-method">
									<img
										src="/assets/images/vibo/click.png"
										alt="click"
										width="50"
									/>
								</div>
								<div className="payment-method">
									<img
										src="/assets/images/vibo/payme.png"
										alt="payme"
										width="50"
									/>
								</div>
								<div className="payment-method">
									<img
										src="/assets/images/vibo/sberbank.png"
										alt="sberbank"
										width="50"
									/>
								</div>
							</div>
						</div>

						{footer &&
							footer.map((footerItem, i) =>
								i !== footer.length - 1 ? (
									<div key={i} className="widget">
										<div
											className="d-flex align-items-center justify-content-between"
											onClick={() =>
												setCollapse(
													collapse === i + 1
														? 0
														: i + 1
												)
											}
										>
											<h4 className="widget-title">
												{dt(footerItem.name)}
											</h4>
											<button className="footer-collapse text-white">
												{collapse !== i + 1 ? (
													<i className="icon-angle-down"></i>
												) : (
													<i className="icon-angle-up"></i>
												)}
											</button>
										</div>
										<ul
											className={`widget-list ${
												collapse !== i + 1
													? ""
													: "active"
											}`}
										>
											{footerItem.children &&
												footerItem.children.map(
													(child, i) => (
														<li key={i}>
															<LinkComponent
																menu={child}
															/>
														</li>
													)
												)}
										</ul>
									</div>
								) : (
									<div key={i} className="widget contacts">
										<div
											className="d-flex align-items-center justify-content-between"
											onClick={() =>
												setCollapse(
													collapse === i + 1
														? 0
														: i + 1
												)
											}
										>
											<h4 className="widget-title">
												{dt(footerItem.name)}
											</h4>
											<button className="footer-collapse text-white">
												{collapse !== i + 1 ? (
													<i className="icon-angle-down"></i>
												) : (
													<i className="icon-angle-up"></i>
												)}
											</button>
										</div>
										<ul
											className={`widget-list contact_list ${
												collapse !== i + 1
													? ""
													: "active"
											}`}
										>
											{footerItem.children.map(
												(child, i) => {
													return (
														<span
															key={i}
															className="contact_item"
														>
															<LinkComponent
																menu={child}
															/>
														</span>
														// <Link
														// 	to={child.slug}
														// 	className="contact_item"
														// 	key={i}
														// >
														// 	{dt(child.name)}
														// </Link>
													);
												}
											)}
											<div className="mt-auto mb-5 social-links-block">
												<div className="footer-social-links d-flex align-items-center justify-content-start">
													{marketData.settings
														.socialLinks
														? SocialLinks(
																marketData
																	.settings
																	.socialLinks
														  )
														: null}
												</div>
											</div>
										</ul>
									</div>
								)
							)}
					</div>
				</div>
			</div>

			<div className="footer-bottom">
				<div className="container">
					<div className="d-flex footer-bottom-wrap">
						<div className="bottom-links">
							{verticalMenu.slice(0, 2).map((item, i) => (
								<span className="bottom-item" key={i}>
									<Link to={item.slug}>{dt(item.name)}</Link>
								</span>
							))}
						</div>
						<div className="copy-right">
							<span>{dt(marketData.settings.copyRight)}</span>
						</div>

						<figure className="footer-payments d-none">
							{payments?.map((item, i) => (
								<img
									key={i}
									src={`https://app.ox-sys.com` + item.icon}
									alt="Payment methods"
									width="100%"
									height="100%"
								/>
							))}
						</figure>
					</div>
				</div>
			</div>
		</footer>
	);
}

function mapStateToProps(state) {
	return {
		footer: state.menu.footer,
		settings: state.marketData.settings,
		footerLogo: state.marketData.footerLogo,
	};
}

export default React.memo(connect(mapStateToProps)(Footer));
