import React, { useContext } from "react";
import SearchContext from "./SearchContext";
import Loader from "../../../features/loaders/loader";
import DynamicPlaceholder from "./DynamicInput";
import { useSelector } from "react-redux";
import market, { config } from "../../../../config/market";
import { useTranslation } from "react-i18next";
import { dt } from "../../../../i18n";

export default function SearchFormTwo({
	isInputClicked,
	setIsInputClicked,
	setSearch,
	search,
}) {
	const {
		searchFn,
		renderSearchPage,
		result,
		query,
		setResult,
		setHidden,
		value,
		inputId,
	} = useContext(SearchContext);
	const { t } = useTranslation();
	const { menuVertical } = useSelector((state) => state.menu);

	const menus = [];
	if (menuVertical) {
		for (let i = 0; i < menuVertical.length; i++) {
			menus.push(menuVertical[i].name);
		}
	}

	return (
		<div
			className="header-search header-search-extended header-search-visible header-search-no-round search-box"
			style={{ margin: "0" }}
		>
			<form method="get" onSubmit={(e) => renderSearchPage(e)} noValidate>
				<div
					className={`header-search-wrapper search-wrapper-wide search ${
						isInputClicked ? "" : "long-input-bg w-auto"
					}`}
				>
					<label
						htmlFor={inputId || "searchInput"}
						className="sr-only"
					>
						{t("search")}
					</label>
					<div
						className="serch-icon d-none"
						onClick={() => setIsInputClicked(!isInputClicked)}
					>
						<img src={market.config.icons.search} alt="search" />
					</div>
					{config.dynamicInput ? (
						<DynamicPlaceholder
							onFocus={() => setHidden(true)}
							onBlur={() => {
								setTimeout(() => {
									setHidden(false);
									setResult({
										loading: false,
										data: [],
										menu: [],
									});
								}, 500);
							}}
							words={dt(menus)}
							defaultValue={value}
							onChange={(e) => {
								searchFn(e.target.value.trim());
							}}
							type="search"
							className={`form-control pl-5`}
							autoComplete="off"
							id={inputId || "searchInput"}
							placeholder={t("search-products")}
							required
						/>
					) : (
						<input
							autoFocus={search}
							onFocus={() => {
								setHidden(true);
								setIsInputClicked(true);
							}}
							onBlur={() => {
								setTimeout(() => {
									setHidden(false);
									setIsInputClicked(false);
									setResult({
										loading: false,
										data: [],
										menu: [],
									});
								}, 500);
							}}
							defaultValue={value}
							onChange={(e) => {
								searchFn(e.target.value.trim());
							}}
							type="search"
							className={`form-control pl-5 my-input ${
								isInputClicked ? "" : "long-input"
							}`}
							autoComplete="off"
							id={inputId || "searchInput"}
							placeholder={t("search-products")}
							required
						/>
					)}

					<img
						src="/assets/images/vibo/search-icon.svg"
						className="d-none search-icon"
						alt="Search"
					/>
					<button
						disabled={query.length < 2}
						className="btn search-button"
						type="submit"
					>
						{result.loading ? (
							<Loader />
						) : (
							<h6>
								<span
									className={`only-desktop ${
										query.length > 2 ? `active-span` : ``
									}`}
								>
									{t("search")}
								</span>
								<img
									src={
										query.length > 2
											? market.config.icons.rArrow
											: isInputClicked
											? "/assets/images/wooz/x-buttan.svg"
											: market.config.icons.search
									}
									className={`only-mobile ${
										query.length > 2 ? `no-active-span` : ``
									}`}
									onClick={() => {
										setIsInputClicked(!isInputClicked);
									}}
									alt="search"
								/>
							</h6>
						)}
					</button>
				</div>
			</form>
			<div className="search-close-button d-none">
				<div
					className={`search-icon ${
						result.loading ? `loading-icon` : ``
					}`}
				>
					<img
						src="/assets/images/icons/baby_center/search.png"
						alt="search"
					/>
				</div>
				<button
					className="close-button"
					onClick={() => setSearch(!search)}
				>
					<img
						src="/assets/images/icons/baby_center/delete.png"
						alt="closer"
					/>
				</button>
			</div>
		</div>
	);
}
