import React, { useEffect } from "react";
import { Route, Switch } from "react-router-dom";
import Layout from "../components/layout/Layout";
import LoaderOverlay from "../components/features/loaders/LoaderOverlay";
import RiveLoader from "../components/features/loaders/RiveLoader";
import ScrollToTop from "../components/common/ScrollToTop";
import market from "../config/market";
import YandexMetrika from "../components/YandexMetrika";
import TagManager from "react-gtm-module";
import ReactGA from "react-ga";
import ReactPixel from "react-facebook-pixel";
import { useSelector } from "react-redux";

const App = React.lazy(() => import("./pages/app"));
const Product = React.lazy(() => import("./product"));
const ChoosenProducts = React.lazy(() => import("./choosenProducts"));
const CompareProducts = React.lazy(() => import("./compareProducts"));
const Profile = React.lazy(() => import("./profile"));
const Checkout = React.lazy(() => import("./checkout"));
const Cart = React.lazy(() => import("./cart"));
const AllCategories = React.lazy(() => import("./all-categories"));
const Login = React.lazy(() => import("./login"));
const Supplier = React.lazy(() => import("./supplier"));
const TradeMark = React.lazy(() => import("./trademark"));
const Suppliers = React.lazy(() => import("./suppliers"));
const TradeMarks = React.lazy(() => import("./trademarks"));
const Markets = React.lazy(() => import("./markets"));
const Category = React.lazy(() => import("./category"));
const Page = React.lazy(() => import("./static-page"));
const Blog = React.lazy(() => import("./blog"));
const Search = React.lazy(() => import("./search"));
const Home = React.lazy(() => import("./home"));
const Pages = React.lazy(() => import("./pages"));
const Logout = React.lazy(() => import("./logout"));
const TestPage = React.lazy(() => import("./test/TestPage"));
const Notfound = React.lazy(() => import("./pages/404"));
const OrderDetails = React.lazy(() => import("./order-details/"));

export default function AppRoot() {
	const { settings } = useSelector((state) => state.marketData);

	useEffect(() => {
		if (settings && window.location.hostname !== "localhost") {
			if (settings.googleTagManager) {
				const tagManagerArgs = {
					gtmId: settings.googleTagManager,
				};
				TagManager.initialize(tagManagerArgs);
				window.dataLayer.push({
					event: "pageview",
				});
			}

			if (settings.facebookPixel) {
				ReactPixel.init(settings.facebookPixel);
				ReactPixel.pageView();
			}
			if (settings && settings.googleAnalyticsUserId) {
				ReactGA.initialize({
					trackingId: settings.googleAnalyticsTrackingId,
					gaOptions: {
						name: "-",
						userId: settings.googleAnalyticsUserId,
					},
				});
				ReactGA.pageview(window.location.pathname);
			}
		}
	}, []);

	return (
		<React.Suspense
			fallback={
				market.config.loader ? (
					<RiveLoader />
				) : (
					<LoaderOverlay wrapper={true} />
				)
			}
		>
			<ScrollToTop />

			<YandexMetrika />

			<Layout>
				<Switch>
					<Route exact path="/" component={Home} />
					<Route exact path="/profile" component={Profile} />
					<Route
						exact
						path="/order-details/:token/:id"
						component={OrderDetails}
					/>
					<Route exact path="/login" component={Login} />
					<Route exact path="/checkout" component={Checkout} />
					<Route path="/app" component={App} />
					<Route exact path="/cart" component={Cart} />
					<Route path="/product" component={Product} />
					<Route path="/category" component={Category} />
					<Route path="/search" component={Search} />
					<Route path="/supplier" component={Supplier} />
					<Route path="/trademark" component={TradeMark} />
					<Route path="/suppliers" component={Suppliers} />
					<Route path="/trademarks" component={TradeMarks} />
					<Route path="/markets" component={Markets} />
					<Route path="/all-categories" component={AllCategories} />
					<Route path="/page" component={Page} />
					<Route
						path="/choosen-products-list"
						component={ChoosenProducts}
					/>
					<Route
						path="/compare-products-list"
						component={CompareProducts}
					/>
					<Route path="/logout" component={Logout} />
					<Route path="/pages" component={Pages} />
					<Route path="/test" component={TestPage} />
					<Route path="/blogs" component={Blog} />
					<Route path="/news" component={Blog} />
					<Route path="/article" component={Blog} />
					<Route path="/company" component={Blog} />
					<Route path="/stock" component={Blog} />
					<Route path="/education" component={Blog} />
					<Route path="/others" component={Blog} />
					<Route component={Notfound} />
				</Switch>
			</Layout>
		</React.Suspense>
	);
}
