import axios from "axios";
import { isDevServer } from "../../utils/project";
import mainCaller from "../mainCaller";
import { getSuppliers as getSuppliersOX } from "../supplierProducts";
import { getMenus as getMenusDev } from "../menu";
const { security_key, cache_server_url } = require("../../config/market");

export const main = (path, method = "GET") => {
	const options = {
		url:
			(cache_server_url || "https://cache.ox-sys.com") +
			`/api/data${path}`,
		headers: {
			Accept: "application/json",
			"security-key": security_key,
		},
		method: method,
	};

	return axios(options).then((r) => r.data);
};

export const isUpdate = (time) =>
	main(`/is-updated?lastUpdatedAt=${time}`, "GET");

export const updateCacheApi = () => main("", "PUT");

export const getMarketData = () => {
	if (isDevServer()) return mainCaller("", "GET");
	return main("/marketData");
};

export const getMenus = () => {
	if (isDevServer()) return getMenusDev();
	return main("/menus");
};

export const getSuppliers = () => {
	if (isDevServer()) return getSuppliersOX();
	return main("/suppliers");
};
export const getSuppliersList = (query) => {
	if (isDevServer()) return getSuppliersOX();
	return main(`/suppliers-list?${query}`);
};

export const getBlocks = () => main("/blocks");
const getBanners = () => main("/banners");
const getNews = () => main("/news");
const getSlides = () => main("/slides");
const getCollections = (type) =>
	main("/collections" + (type ? "?type=" + type : ""));

export const getOtherData = async () => {
	const news = await getNews();
	const banners = await getBanners();
	const slides = await getSlides();
	const collections = await getCollections();
	return {
		banners,
		slides,
		news,
		collections,
	};
};
