import React, { useState } from "react";
import { LazyLoadImage } from "react-lazy-load-image-component";
import { Link } from "react-router-dom";
import { divideArray } from "../../../../utils/array";
import { getImageUrl } from "../../../../utils/oxMethods";
import { dt } from "../../../../i18n";
import MediaImage from "../../../features/product/MediaImage";

export default function MenuBodyTwo(menu, activeMenu, setActiveMenu) {
	const [subMenuBody, setSubMenuBody] = useState([]);
	const [childMenu, setChildMenu] = useState([]);

	function menuList(children) {
		return (
			<ul>
				{children
					.sort((a, b) => a.sort - b.sort)
					.map(menu => {
						return (
							<li
								key={menu.id}
								style={{
									display: "flex",
									alignItems: "center",
									marginBottom: "3px"
								}}
							>
								{menu.image ? (
									<img
										className="sub-menu-img mr-1"
										src={getImageUrl(menu.image, "50x_")}
										alt=""
									/>
								) : null}
								<Link
									to={`/category/${menu.slug}`}
									onClick={() => setChildMenu([])}
								>
									{dt(menu.name)}
								</Link>
							</li>
						);
					})}
			</ul>
		);
	}

	const megaMenuChildren = children => {
		return (
			<div className="row">
				<div className="col-md-6">
					{divideArray(children)
						.sort((a, b) => a.sort - b.sort)
						.map(columnChildren => {
							return columnChildren
								.sort((a, b) => a.sort - b.sort)
								.map(menu => {
									return (
										<div
											style={{
												display: "flex",
												alignItems: "center"
											}}
											key={menu.id}
										>
											{menu.image ? (
												<img
													className="sub-menu-img mr-1"
													src={getImageUrl(
														menu.image,
														"150x_"
													)}
													alt=""
												/>
											) : null}
											<Link
												onClick={() => setChildMenu([])}
												className="menu-link"
												to={`/category/${menu.slug}`}
												onMouseMove={() =>
													setSubMenuBody(
														menu.children
													)
												}
											>
												{dt(menu.name)}
											</Link>
										</div>
									);
								});
						})}
				</div>
				<div className="col-md-6">{menuList(subMenuBody)}</div>
			</div>
		);
	};

	const megaMenuBody = menu => {
		return (
			<div className="megamenu megamenu-md">
				<div className="row no-gutters">
					<div className="col-md-5">
						<div className="menu-col">
							{megaMenuChildren(menu.children)}
						</div>
					</div>

					<div className="col-md-7">
						{menu.backgroundImage ? (
							<div className="banner banner-overlay">
								<Link
									to={`/category/${menu.slug}`}
									className="banner banner-menu"
								>
									<div
										style={{
											width: "100%",
											height: "500px",
											objectFit: "cover"
										}}
									>
										<LazyLoadImage
											src={getImageUrl(
												menu.backgroundImage,
												"800x_"
											)}
											effect="blur"
											alt={menu.slug}
										/>
									</div>
								</Link>
							</div>
						) : null}
					</div>
				</div>
			</div>
		);
	};

	return (
		<li
			key={menu.id}
			className={`main-category-mb ${
				activeMenu === menu.id ? "active" : ""
			}`}
			onClick={() => setActiveMenu(menu.id)}
		>
			<Link
				onMouseEnter={() => setChildMenu(menu.children)}
				to={
					menu.type === "static_page"
						? `page/${menu.slug}`
						: `/category/${menu.slug}`
				}
				className={menu.children.length > 0 ? "sf-with-ul" : "sf"}
			>
				{menu.image ? (
					<div className="category-menu-img-bg">
						<MediaImage
							largeSize="50x_"
							mediumSize="50x_"
							smallSize="100x_"
							image={menu.image}
							alt={menu.image.originalName}
						/>
					</div>
				) : (
					<div className="category-menu-img-bg"></div>
				)}
				<span>{dt(menu.name)}</span>
			</Link>
			{childMenu.length > 0 ? megaMenuBody(menu) : null}
		</li>
	);
}
