import React from "react";
import { connect } from "react-redux";
import market from "../../../config/market";
import { useTranslation } from "react-i18next";
import { dt } from "../../../i18n/index";
import { LinkComponent } from "../Menus/Desktop/LinkComponent";
import Logo from "../../common/Logo";
function Footer(props) {
	const { t } = useTranslation();
	const { container = "container", footer, settings } = props;

	return (
		<footer className="footer footer-one">
			<div className="footer-middle">
				<div className={container}>
					<div className="row">
						<div className="col-sm-6 col-lg-3">
							<div className="widget widget-about">
								<Logo
									logo={settings.footerLogo}
									className="footer-logo"
									width="180px"
									height="65px"
								/>

								<div className="widget-call">
									<i className="icon-phone"></i>
									{t("have-questions")} 24/7
									{settings.phoneNumbers
										? settings.phoneNumbers
												.slice(0, 2)
												.map((number, i) => (
													<a
														className="mt-1"
														key={i}
														href={`tel:${number}`}
													>
														{number}
													</a>
												))
										: null}
								</div>
							</div>
						</div>

						{footer &&
							footer.map((footer, index) => (
								<div key={index} className="col-sm-6 col-lg-3">
									<div className="widget">
										<h4 className="widget-title">
											{dt(footer.name)}
										</h4>
										<ul className="widget-list">
											{footer.children &&
												footer.children.map(
													(child, index) => (
														<li key={index}>
															<LinkComponent
																menu={child}
															/>
														</li>
													)
												)}
										</ul>
									</div>
								</div>
							))}
						<div className="col-sm-6 col-lg-3 mt-auto mb-5 social-links-block">
							<div className="footer-social-links d-flex align-items-center justify-content-start">
								{settings.socialLinks?.instagram && (
									<a
										className="mr-5"
										target="blank"
										href={settings.socialLinks?.instagram}
									>
										<img
											src="./assets/images/icons/instagram.png"
											alt="twitter icon"
											width="20"
											height="20"
										/>
									</a>
								)}
								{settings.socialLinks?.facebook && (
									<a
										className="mr-5"
										target="blank"
										href={settings.socialLinks?.facebook}
									>
										<img
											src="./assets/images/icons/facebook.png"
											alt="twitter icon"
											width="20"
											height="20"
										/>
									</a>
								)}
								{settings.socialLinks?.twitter && (
									<a
										className="mr-5"
										target="blank"
										href={settings.socialLinks?.twitter}
									>
										<img
											src="./assets/images/icons/youtube.png"
											alt="twitter icon"
											width="20"
											height="20"
										/>
									</a>
								)}
								{settings.socialLinks?.telegram && (
									<a
										className="mr-5"
										target="blank"
										href={settings.socialLinks?.telegram}
									>
										<img
											src="./assets/images/icons/telegram.png"
											alt="twitter icon"
											width="20"
											height="20"
										/>
									</a>
								)}
								{settings.socialLinks?.odnoklassniki &&
									market.config.home === 3 && (
										<a
											className="mr-5"
											target="blank"
											href={
												settings.socialLinks
													?.odnoklassniki
											}
										>
											<img
												src="./assets/images/icons/youtube.png"
												alt="youtube icon"
												width="20"
												height="20"
											/>
										</a>
									)}
								{settings.socialLinks?.linkedIn && (
									<a
										className="mr-5"
										target="blank"
										href={settings.socialLinks?.linkedIn}
									>
										<img
											src="./assets/images/icons/tik-tok.png"
											alt="twitter icon"
											width="20"
											height="20"
										/>
									</a>
								)}
							</div>
						</div>
					</div>
				</div>
			</div>

			<div className="footer-bottom">
				<div className={container}>
					<p className="footer-copyright">{settings.description}</p>
					<figure className="footer-payments">
						<img
							src={`${process.env.PUBLIC_URL}/assets/images/atlanta/cash.png`}
							alt="payment_cash"
						/>
						<img
							src={`${process.env.PUBLIC_URL}/assets/images/atlanta/click.png`}
							alt="payment_click"
						/>
						<img
							src={`${process.env.PUBLIC_URL}/assets/images/atlanta/oson.jpg`}
							alt="payment_oson"
						/>
						<img
							src={`${process.env.PUBLIC_URL}/assets/images/atlanta/payme.png`}
							alt="payment_payme"
						/>
					</figure>
				</div>
			</div>
		</footer>
	);
}

function mapStateToProps(state) {
	return {
		footer: state.menu.footer,
		settings: state.marketData.settings
	};
}

export default React.memo(connect(mapStateToProps)(Footer));
