import React, { useContext } from "react";
import SearchContext from "./SearchContext";
import Loader from "../../../features/loaders/loader";
import DynamicPlaceholder from "./DynamicInput";
import { useSelector } from "react-redux";
import { config } from "../../../../config/market";
import { useTranslation } from "react-i18next";
import { dt } from "../../../../i18n";

export default function SearchForm({ header, setSearch }) {
	const {
		searchFn,
		renderSearchPage,
		result,
		setResult,
		setHidden,
		value,
		inputId,
	} = useContext(SearchContext);
	const { t } = useTranslation();

	const { menuVertical } = useSelector((state) => state.menu);
	const menus = [];
	if (menuVertical) {
		for (let i = 0; i < menuVertical.length; i++) {
			menus.push(menuVertical[i].name);
		}
	}

	return (
		<div
			className="header-search header-search-extended header-search-visible header-search-no-round search-box"
			style={{ margin: "0" }}
		>
			<form method="get" onSubmit={(e) => renderSearchPage(e)}>
				<div className="header-search-wrapper search-wrapper-wide  search">
					<label htmlFor={inputId} className="sr-only">
						{t("search")}
					</label>
					{config.dynamicInput ? (
						<DynamicPlaceholder
							onFocus={() => setHidden(true)}
							onBlur={() => {
								setTimeout(() => {
									setHidden(false);
									setResult({
										loading: false,
										data: [],
										menu: [],
									});
								}, 500);
							}}
							words={dt(menus)}
							defaultValue={value}
							onChange={(e) => {
								searchFn(e.target.value.trim());
							}}
							type="search"
							className="form-control pl-5"
							autoComplete="off"
							id={inputId}
							required
						/>
					) : (
						<input
							onFocus={() => setHidden(true)}
							onBlur={() => {
								setTimeout(() => {
									setHidden(false);
									setResult({
										loading: false,
										data: [],
										menu: [],
									});
								}, 500);
							}}
							defaultValue={value}
							onChange={(e) => {
								searchFn(e.target.value.trim());
							}}
							type="search"
							className="form-control form-border pl-5"
							autoComplete="off"
							id={inputId}
							placeholder={t("search-products")}
							required
						/>
					)}
					<button
						disabled={result.loading}
						className="btn btn-primary"
						type="submit"
					>
						{result.loading ? (
							<Loader />
						) : header === 2 ? (
							<div className="d-flex align-items-center">
								<img
									className="icon-search"
									src="./assets/images/osto-icons/search-icon.png"
									alt="search icon"
									width="20"
									height="20"
								/>
								<button
									onClick={() => setSearch(false)}
									className="ml-4 btn-remove icon-close"
									style={{
										width: "10px",
										height: "10px",
									}}
								></button>
							</div>
						) : (
							<i className="icon-search"></i>
						)}
					</button>
				</div>
			</form>
		</div>
	);
}
