import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { connect, useSelector } from "react-redux";
import { getCartCount, getCartTotal } from "../../../services";
import { removeFromCartState, closeCartModal } from "../../../actions/cart";
import { getImageUrl } from "../../../utils/oxMethods";
import { formatterCurrency } from "../../../utils/string";
import { useTranslation } from "react-i18next";
import { removeToCartApi } from "../../../api/cart";
import market from "../../../config/market";

function getNameProductOrVariation(item) {
	if (item.product) {
		return item.product.name;
	}
	return item.data.variation.name;
}

function CartMenuTwo(props) {
	const {
		cartlist,
		removeFromCartState,
		closeCartModal,
		showCartList,
	} = props;
	const [dropCart, setDropCart] = useState([]);

	const { token } = useSelector((state) => state.user.tokenData);
	const { t } = useTranslation();
	let total = getCartTotal(cartlist);

	useEffect(() => {
		setDropCart(cartlist);
		setTimeout(() => {
			closeCartModal();
		}, 2000);
	}, [showCartList, cartlist]);

	function removeProduct(id, item) {
		removeFromCartState(id);
		if (token && item.id) {
			removeToCartApi(item.id, token);
		}
	}
	return (
		<div className="dropdown cart-dropdown">
			<Link
				style={{ width: "25px", height: "25px" }}
				to={`${process.env.PUBLIC_URL}${token ? "/checkout" : "/cart"}`}
				className="dropdown-toggle d-flex flex-column"
				role="button"
				data-toggle="dropdown"
				aria-haspopup="true"
				aria-expanded="false"
				data-display="static"
			>
				<div className="position-relative">
					<img
						src={market.config.icons.cart}
						alt="shopping bag"
						style={{
							width: "22px",
							// height: "22px"
						}}
					/>
					{cartlist.length ? (
						<span className="cart-count">
							{getCartCount(cartlist)}
						</span>
					) : null}
				</div>
				<h6>{t("basket")}</h6>
			</Link>

			<div
				className={`dropdown-menu dropdown-menu-right${
					!dropCart.length ? " text-center" : ""
				}${showCartList ? " visible-cart" : ""}`}
			>
				{!dropCart.length ? (
					<p>{t("empty-cart")}</p>
				) : (
					<>
						<div className="dropdown-cart-products">
							{dropCart.map((item, index) => {
								return (
									<div className="product" key={index}>
										<div className="product-detail-left">
											<div className="product-image-container">
												<Link
													to={
														`${process.env.PUBLIC_URL}/product/` +
														item.data.variation.id
													}
													className="product-image"
												>
													<img
														src={getImageUrl(
															item.data.variation
																.images[0]
														)}
														data-oi={
															item.data.variation
																.images[0]
														}
														alt="product"
														width="80"
														height="80"
													/>
												</Link>
											</div>
											<h4 className="product-title">
												<Link
													to={
														`${process.env.PUBLIC_URL}/product/` +
														item.data.variation.id
													}
												>
													{getNameProductOrVariation(
														item
													)}
												</Link>
											</h4>
										</div>

										<div className="product-detail-right">
											<span className="cart-product-info">
												{item.data.discount
													? item.data.newPrice
													: item.data.oldPrice}{" "}
												UZS
											</span>
											<span className="cart-product-qty">
												{" "}
												{item.count}
											</span>
											<button
												className="btn-remove p-0"
												style={{
													position: "relative",
													width: "max-content",
													fontSize: "12px",
												}}
												title="Remove Product"
												onClick={() =>
													removeProduct(
														item.data.variation.id,
														item
													)
												}
											>
												{t("delete")}
											</button>
										</div>
									</div>
								);
							})}
						</div>
						<div className="dropdown-cart-total">
							<span className="cart-total">
								{t("subtotal")} :
							</span>

							<span className="cart-total-price">
								{formatterCurrency(total)}
							</span>
						</div>

						<div className="dropdown-cart-action">
							<a
								href={`${process.env.PUBLIC_URL}/`}
								style={{ width: "100%" }}
								className="btn border mr-2"
							>
								{t("continue-shopping")}
							</a>
							<Link
								to={`${process.env.PUBLIC_URL}${
									token ? "/checkout" : "/cart"
								}`}
								style={{ width: "100%" }}
								className="btn btn-primary"
							>
								{t("enter-order")}
							</Link>
						</div>
					</>
				)}
			</div>
		</div>
	);
}

function mapStateToProps(state) {
	return {
		cartlist: state.cartlist.cart ? state.cartlist.cart : [],
		showCartList: state.cartlist.showCartList,
	};
}

export default connect(mapStateToProps, {
	removeFromCartState,
	closeCartModal,
})(CartMenuTwo);
