import React, { useEffect, useContext } from "react";
import SearchContext from "./SearchContext";
import { formatterCurrency } from "../../../../utils/string";
import { searchApi } from "../../../../api/products";
import { useTranslation } from "react-i18next";
export default function SearchResult() {
	const { query, result, setResult } = useContext(SearchContext);
	const { t } = useTranslation();
	useEffect(() => {
		if (query?.length >= 2) {
			setResult({ loading: true, data: [] });
			searchApi(query.toString()).then(({ variations }) => {
				setResult({
					loading: false,
					data: variations.length > 0 ? variations : false,
				});
			});
		}
	}, [query]);

	return (
		<>
			{result &&
				result.data &&
				result.data.length > 0 &&
				query &&
				query.length >= 2 && (
					<ul className="search-result p-3 m-0">
						<li className="p-3 text-center">
							<p className="m-0">
								{result.data.length} {t("results")}
							</p>
						</li>
						{result.data.map((variation) => (
							<li
								key={variation.variation.id}
								className="search-result-item mb-1 text-dark"
							>
								<a
									href={`product/${variation.variation.id}`}
									className="w-100 d-flex align-items-center"
								>
									<img
										src={
											variation.variation.images.length >
												0 &&
											variation.variation.images[0].urls
												.original
										}
										alt="img"
										width="50"
										height="50"
									/>
									<p className="result-variation-name ml-3 mb-0 p-0 text-dark">
										{variation.variation.name}
									</p>
									<div
										className="flex-grow-1 ml-3 text-dark"
										style={{
											width: "max-content",
											whiteSpace: "nowrap",
											textAlign: "right",
										}}
									>
										{variation.discount > 0 ? (
											<div className="search-result-price">
												<span className="new-price">
													{formatterCurrency(
														variation.newPrice
													)}
												</span>
												<span className="old-price text-through">
													{formatterCurrency(
														variation.oldPrice
													)}
												</span>
											</div>
										) : (
											<div>
												<span>
													{formatterCurrency(
														variation.newPrice
													)}
												</span>
											</div>
										)}
									</div>
								</a>
							</li>
						))}
					</ul>
				)}

			{result.data === false && (
				<ul className="search-result p-3 m-0">
					<li className="search-result-item border-0 text-center p-2">
						<p className="m-0">0 {t("results")}</p>
					</li>
				</ul>
			)}
		</>
	);
}
