import mainCaller from "./mainCaller";

const positions = {
	top: "top",
	main: "main",
	side: "side",
	footer: "footer"
};

export const getAllMenus = () => {
	return mainCaller("/menus", "GET");
};
export const getMenuNews = path => {
	return mainCaller(`/news-category?type=${path}`, "GET");
};

export const getMenuSearch = (path, query) => {
	return mainCaller(`/news?title=${query}&type=${path}`, "GET");
};

function getMenu(menuId) {
	if (!menuId) {
		return Promise.resolve([]);
	}
	return mainCaller(`/${menuId}/menu-items`, "GET");
}

export const getMenus = async () => {
	let menus = await getAllMenus();

	let horizontalMenu = menus.find(menu => menu.position === positions.main);
	let horizontal = horizontalMenu ? await getMenu(horizontalMenu.id) : [];

	let verticalMenu = menus.find(menu => menu.position === positions.side);
	let vertical = verticalMenu ? await getMenu(verticalMenu.id) : [];

	let footerMenu = menus.find(menu => menu.position === positions.footer);
	let footer = footerMenu ? await getMenu(footerMenu.id) : [];

	let topMenu = menus.find(menu => menu.position === positions.top);
	let top = topMenu ? await getMenu(topMenu.id) : [];

	return {
		top,
		horizontal,
		vertical,
		footer
	};
};

export const getFilterOfMenu = menuId => {
	return mainCaller(`/${menuId}/filter-data`, "GET");
};
