import i18n from "../../../i18n";

const validate = values => {
	const errors = {};
	if (!values.region) {
		// console.log(values.region);
	}
	if (!values.firstName) {
		errors.firstName = i18n.t("required");
	} else if (values.firstName.length < 2 || values.firstName.length > 50) {
		errors.firstName =
			"Поле должен состоять максимум из 15 или менее символов";
	}
	if (!values.lastName) {
		errors.lastName = i18n.t("required");
	}

	if (!values.phoneNumber) {
		errors.phoneNumber = i18n.t("required");
	} else if (
		!/^(\+998)?(\s)?\d{2}(\s)?\d{3}(\s)?\d{2}(\s)?\d{2}$/.test(
			values.phoneNumber
		)
	) {
		errors.phoneNumber = i18n.t("invalid-phone-number");
	}
	if (!values.pass1) {
		errors.pass1 = i18n.t("required");
	} else if (values.pass1.length < 4) {
		errors.pass1 = i18n.t("more4");
	}
	if (!values.pass2) {
		errors.pass2 = i18n.t("required");
	} else if (values.pass2.length < 4) {
		errors.pass2 = i18n.t("more4");
	} else if (values["pass1"] !== values["pass2"]) {
		errors.pass2 = i18n.t("invalid-inf");
	}
	return errors;
};

export default validate;
