import React, { Fragment } from "react";
import { useTranslation } from "react-i18next";
import { Field, reduxForm } from "redux-form";
import Loader from "../../../components/features/loaders/loader";
import market from "../../../config/market";

const otherField = props => {
	let {
		input,
		placeholder,
		type,
		className,
		meta: { touched, error }
	} = props;
	if (touched) {
		if (error) {
			className = className + " warning";
			// setLoadingPassword(false);
		}
	}

	let phoneNumber = "+998";

	return (
		<Fragment>
			{type === "tel" ? (
				<input
					{...input}
					placeholder={placeholder}
					className={className}
					value={input.value ? input.value : phoneNumber}
					onChange={e => {
						phoneNumber = `+${e.target.value.match(/\w/)}`;
						input.onChange(e);
					}}
					type={type}
				/>
			) : (
				<input
					{...input}
					placeholder={placeholder}
					className={className}
					onChange={e => {
						input.onChange(e);
					}}
					value={input.value}
					type={type}
				/>
			)}
			{touched && error && <span>{error}</span>}
		</Fragment>
	);
};

function ForgetPassword({
	open,
	setOpen,
	handleSubmit,
	loadingPassword
	// setLoadingPassword
}) {
	const { t } = useTranslation();

	return (
		<div
			className={open ? "forget-password_overlay" : "d-none"}
			onClick={e => {
				if (e.target.className === "forget-password_overlay")
					setOpen(false);
			}}
		>
			<div className="forget-password-content">
				<form onSubmit={handleSubmit} className="w-auto">
					<label htmlFor="phoneNumber">
						{t(
							market.config.b2b_auth
								? "username-email"
								: "phone-number"
						)}
					</label>
					<Field
						type={`${market.config.b2b_auth ? "text" : "tel"}`}
						className="form-control"
						id="phoneNumber"
						component={otherField}
						name="phoneNumber"
						pattern=".{13,13}"
						title={t("pass_error")}
						required
					/>
					<button
						type="submit"
						className="btn btn-outline-primary-2 w-100 mt-3"
					>
						{loadingPassword ? <Loader /> : "OK"}
					</button>
				</form>
			</div>
		</div>
	);
}

export default reduxForm({ form: "forgetPassword" })(ForgetPassword);
