import React, { useState } from "react";
import { useSelector } from "react-redux";
import market from "../../../../config/market";
import { getImageUrl } from "../../../../utils/oxMethods";
import { LinkComponent } from "./LinkComponent";

function ChildMenu({ menu, setChildMenu }) {
	return (
		<div
			className="vertical-menu-content"
			onMouseLeave={() => setChildMenu([])}
		>
			<ul className="child-menu-list row container">
				{menu.children.map((menu, i) => (
					<li key={i} className="child-menu-item col-md-3">
						<img src={getImageUrl(menu.image)} alt={menu.name} />
						<LinkComponent menu={menu} />
					</li>
				))}
			</ul>
		</div>
	);
}

export default function HorizontalMenuHeaderBottomTwo() {
	const { other } = useSelector((state) => state.menu);
	const [childMenu, setChildMenu] = useState([]);
	return (
		<ul className="menu-list" onMouseLeave={() => setChildMenu([])}>
			{other.map((menu, i) => (
				<li
					key={i}
					className={`menu-list-item ${
						childMenu.id === menu.id ? `active` : ``
					}`}
					onMouseEnter={() => setChildMenu(menu)}
					onClick={() => setChildMenu([])}
				>
					<LinkComponent menu={menu} />
					{menu.children.length > 0 ? (
						<img src={market.config.icons.rArrow} alt="arrow" />
					) : null}
				</li>
			))}
			{childMenu?.children?.length > 0 && (
				<div className="vertical-menu">
					<ChildMenu menu={childMenu} setChildMenu={setChildMenu} />
				</div>
			)}
		</ul>
	);
}
