import React from "react";
import { connect, useSelector } from "react-redux";
import { dt } from "../../../../src/i18n/index";
import { LinkComponent } from "../Menus/Desktop/LinkComponent";

function Footer(props) {
	const { container = "container", footer, settings } = props;
	const payments = useSelector(
		state => state.marketData?.cashdesk?.paymentMethods
	);
	return (
		<footer className="footer footer-four">
			<div className="footer-middle">
				<div className={container}>
					<div className="row">
						{footer &&
							footer.map((footer, index) => (
								<div key={index} className="col-sm-6 col-lg-2">
									<div className="widget">
										<h4
											className="widget-title"
											style={{ color: "#ffffff" }}
										>
											{dt(footer.name)}
										</h4>
										<ul className="widget-list">
											{footer.children &&
												footer.children.map(
													(child, index) => (
														<li
															key={index}
															style={{
																color: "#ffffff"
															}}
														>
															<LinkComponent
																menu={child}
															/>
														</li>
													)
												)}
										</ul>
									</div>
								</div>
							))}
						<div className="offset-lg-1 col-3">
							<div className="widget-call">
								<a
									style={{ color: "#ffffff" }}
									href={`tel:${settings.phoneNumbers &&
										settings.phoneNumbers[0]}`}
								>
									{settings.phoneNumbers &&
										settings.phoneNumbers[0]}
								</a>
							</div>
							<p className="footer-copyright">
								{dt(settings.description)}
							</p>
							<figure className="footer-payments">
								{payments?.slice(0, 3).map((item, i) => (
									<img
										key={i}
										src={
											`https://app.ox-sys.com` + item.icon
										}
										alt="Payment methods"
										width="100%"
										height="100%"
									/>
								))}
							</figure>
						</div>
					</div>
				</div>
			</div>

			<div className="footer-bottom">
				<div className="container">
					<p
						className="footer-copyright"
						style={{ color: "#ffffff", margin: "0 auto" }}
					>
						Цены указаны с учетом НДС, без учета стоимости доставки
						заказа. Copyright © {new Date().getFullYear()} Все права
						защищены.
					</p>
				</div>
			</div>
		</footer>
	);
}

function mapStateToProps(state) {
	return {
		footer: state.menu.footer,
		settings: state.marketData.settings,
		menu: state.marketData.settings
	};
}

export default React.memo(connect(mapStateToProps)(Footer));
