import { marketTypes } from "../constants/action-types";
import { myReducerPersist } from "../store/managerLocalStorage";

const initialState = {
	settings: {},
	title: "",
	logo: "",
	favicon: ""
};

const marketDataReducer = (state = initialState, action) => {
	switch (action.type) {
		case marketTypes.SET_MARKET_DATA:
			return {
				...state,
				...action.data
			};
		default:
			return state;
	}
};

export default myReducerPersist({ key: "ox-marketData" }, marketDataReducer);
