import React from "react";
import { connect } from "react-redux";
import market from "../../../config/market";
import { useTranslation } from "react-i18next";
import { dt } from "../../../i18n/index";
import { LinkComponent } from "../Menus/Desktop/LinkComponent";

function Footer(props) {
	const { t } = useTranslation();
	const { container = "container", footer } = props;

	return (
		<footer className="footer-five">
			<div className="footer-middle">
				<div className={container}>
					<div className="row">
						<div className="col-sm-6 col-lg-4">
							<div className="widget widget-about">
								<div className="question-and-payments d-flex">
									<div className="widget-call">
										<i className="icon-phone"></i>
										<p>{t("have-questions")} 24/7</p>
										<a
											href={`tel:${
												props.settings?.phoneNumbers
													? props.settings
															.phoneNumbers[0]
													: null
											}`}
										>
											{props.settings?.phoneNumbers
												? props.settings.phoneNumbers[0]
												: null}
										</a>
									</div>
								</div>
							</div>
						</div>

						{footer &&
							footer.map((footer, index) => (
								<div key={index} className="col-sm-6 col-lg-2">
									<div className="widget">
										<h4 className="widget-title">
											{dt(footer.name)}
										</h4>
										<ul className="widget-list">
											{footer.children &&
												footer.children.map(
													(child, index) => (
														<li key={index}>
															<LinkComponent
																menu={child}
															/>
														</li>
													)
												)}
										</ul>
									</div>
								</div>
							))}
					</div>
				</div>
			</div>

			<div className="footer-bottom">
				<div className={container}>
					<p className="footer-copyright">
						{props.settings?.description}
					</p>
					<div className="footer-social-links d-flex align-items-center">
						<p>Social Media</p>
						{props.settings?.socialLinks &&
							props.settings.socialLinks.instagram && (
								<a
									className="mr-5"
									target="blank"
									href={props.settings.socialLinks.instagram}
								>
									<img
										src="./assets/images/icons/instagram.png"
										alt="twitter icon"
										width="20"
										height="20"
									/>
								</a>
							)}
						{props.settings?.socialLinks &&
							props.settings.socialLinks.facebook && (
								<a
									className="mr-5"
									target="blank"
									href={props.settings.socialLinks.facebook}
								>
									<img
										src="./assets/images/icons/facebook.png"
										alt="twitter icon"
										width="20"
										height="20"
									/>
								</a>
							)}
						{props.settings?.socialLinks &&
							props.settings.socialLinks.twitter && (
								<a
									className="mr-5"
									target="blank"
									href={props.settings.socialLinks.twitter}
								>
									<img
										src="./assets/images/icons/youtube.png"
										alt="twitter icon"
										width="20"
										height="20"
									/>
								</a>
							)}
						{props.settings?.socialLinks &&
							props.settings.socialLinks.telegram && (
								<a
									className="mr-5"
									target="blank"
									href={props.settings.socialLinks.telegram}
								>
									<img
										src="./assets/images/icons/telegram.png"
										alt="twitter icon"
										width="20"
										height="20"
									/>
								</a>
							)}
						{props.settings?.socialLinks &&
							props.settings.socialLinks.odnoklassniki &&
							market.config.home === 3 && (
								<a
									className="mr-5"
									target="blank"
									href={
										props.settings?.socialLinks
											.odnoklassniki
									}
								>
									<img
										src="./assets/images/icons/youtube.png"
										alt="youtube icon"
										width="20"
										height="20"
									/>
								</a>
							)}
						{props.settings?.socialLinks &&
							props.settings.socialLinks.linkedIn && (
								<a
									className="mr-5"
									target="blank"
									href={props.settings.socialLinks.linkedIn}
								>
									<img
										src="./assets/images/icons/tik-tok.png"
										alt="twitter icon"
										width="20"
										height="20"
									/>
								</a>
							)}
					</div>
				</div>
			</div>
		</footer>
	);
}

function mapStateToProps(state) {
	return {
		footer: state.menu.footer,
		settings: state.marketData.settings,
		footerLogo: state.marketData.footerLogo
	};
}

export default React.memo(connect(mapStateToProps)(Footer));
