import React, { useState } from "react";
import { getImageUrl } from "../../../utils/oxMethods";
import ShimmerProduct from "../loaders/SimpleShimmer";

function MediaImage({
	url,
	image,
	largeSize = "800x_",
	mediumSize = "300x_",
	smallSize = "300x_",
	shimmerHeight = "100%",
	alt = "image"
}) {
	const [loading, setLoading] = useState(true);

	function onLoad() {
		setLoading(false);
	}

	return loading ? (
		<>
			<ShimmerProduct height={shimmerHeight} />
			<picture className="d-none">
				<source
					onLoad={onLoad}
					srcSet={url || getImageUrl(image, smallSize)}
					media="(max-width: 560px)"
				></source>
				<source
					onLoad={onLoad}
					srcSet={url || getImageUrl(image, mediumSize || mediumSize)}
					media="(max-width: 991px)"
				></source>
				<img
					onLoad={onLoad}
					src={url || getImageUrl(image, largeSize || largeSize)}
					alt={alt}
				></img>
			</picture>
		</>
	) : (
		<picture className="h-100 w-100">
			<source
				className="h-100 w-100"
				onLoad={onLoad}
				srcSet={url || getImageUrl(image, smallSize)}
				media="(max-width: 560px)"
			></source>
			<source
				className="h-100 w-100"
				onLoad={onLoad}
				srcSet={url || getImageUrl(image, mediumSize || mediumSize)}
				media="(max-width: 991px)"
			></source>
			<img
				className="h-100 w-100"
				onLoad={onLoad}
				src={url || getImageUrl(image, largeSize || largeSize)}
				alt={alt}
			></img>
		</picture>
	);
}

export default React.memo(MediaImage);
