import { getPrice } from "../../utils/oxMethods";

export const filterSells = (sells) => {
	const newSells = sells.filter(
		(sell) =>
			sell.sellRecords.filter((record) => record.count || !record.deleted)
				.length && sell
	);

	return newSells;
};
export const getMenuById = (menuList, id) => {
	return menuList.find((menu) => menu.id === parseInt(id));
};

export const getFranchiseData = (dataInfo) => {
	return {
		options: dataInfo,
	};
};
export const getDefaultShippingCalculateData = (
	sells,
	shippingServices,
	location
) => {
	const total = sells.reduce((prev, next) => {
		return (prev += getPrice(next.subtotal));
	}, 0);
	let defaultShippingService =
		shippingServices.find(
			(s) =>
				getPrice(s.totalPrice.max) >= total &&
				getPrice(s.totalPrice.min) <= total
		) || shippingServices[0];

	return {
		shippingService: defaultShippingService.id,
		data: {
			items: sells.map((sell) => ({
				sell: sell.id,
				shippingService: defaultShippingService.id,
			})),
			...location,
		},
	};
};

export const getShippingCalculateData = (
	sellIds,
	shippingService,
	date,
	location
) => {
	let data = {
		items: sellIds.map((id) => ({
			sell: id,
			shippingService: shippingService,
		})),
		...location,
	};
	if (date) {
		data.shippingTime = date.date + " " + date.time;
	}
	return data;
};

export const getShippingCalculateDataLocation = (
	sellIds,
	shippingService,
	pickupLocation,
	coordinate
) => {
	let data = {
		items: sellIds.map((sell) => ({
			sell: sell.id,
			shippingService: shippingService,
		})),
		pickupLocation: pickupLocation,
		...coordinate,
	};
	return data;
};

export const setShippingServiceToSellGroup = (shippingService, sellGroup) => ({
	...sellGroup,
	shippingService: shippingService,
});

export const setShippingServiceToSellsGroups = (shippingService, sellsGroups) =>
	sellsGroups.map((sg) => setShippingServiceToSellGroup(shippingService, sg));

export const removeSellGroupBySellIds = (sellIds, sellsGroups) =>
	sellsGroups.filter((sg) => !sg.sellIds.find((si) => sellIds.includes(si)));

export const getSellsOfSellGroup = (sells, sellGroup) =>
	sellGroup.sellIds
		.map((id) => sells.find((sell) => sell.id === id))
		.filter((s) => s);

export const calculateSellsTotal = (sells) =>
	sells.reduce(
		(sum, sell) => sum + (sell.available ? getPrice(sell.subtotal) : 0),
		0
	);

export const calculateSellsDiscount = (sells) =>
	sells.reduce(
		(sum, sell) => sum + (sell.available ? getPrice(sell.discount) : 0),
		0
	);

function isAllSellsUnAvailableOfSellGroup(sells, sellGroup) {
	return (
		sellGroup.sells?.filter((sell) => !sell?.available).length ===
		sellGroup.sellIds.length
	);
}

export const calculateShippingPrice = (sells, sellsGroups) =>
	sellsGroups.reduce(
		(sum, sellGroup) =>
			sum +
			(isAllSellsUnAvailableOfSellGroup(sells, sellGroup)
				? 0
				: sellGroup.shippingPrice),
		0
	);

function getSupplierOfSell(sell, extraDataSell, allPaymentMethods) {
	let locationId = sell.sellRecords[0].location;
	let location = extraDataSell.locations.find((l) => l.id === locationId);
	let supplier;

	// if own supplier
	if (!location?.supplier) {
		supplier = extraDataSell.suppliers.find((s) => !s.originalSubdomain);
		supplier.paymentMethods = allPaymentMethods;
	} else {
		supplier = extraDataSell.suppliers.find(
			(s) => s.id === location.supplier
		);
	}
	return supplier;
}

function removeExtraFromGatewayType(type) {
	return type && type.split("_")[0];
}

function isAvailablePaymentForSupplier(supplier, payment) {
	return supplier.paymentMethods.find(
		(pm) =>
			removeExtraFromGatewayType(payment.gatewayType) ===
				removeExtraFromGatewayType(pm.gatewayType) &&
			payment.cash === pm.cash &&
			payment.wallet === pm.wallet
	);
}
export const selectAvailableSellsForPaymentMethod = (
	sells,
	choosenMethod,
	allPaymentMethods,
	extraDataSell
) => {
	if (!choosenMethod)
		return sells.map((sell) => ({ ...sell, available: true }));

	return sells.map((sell) => ({
		...sell,
		available: isAvailablePaymentForSupplier(
			getSupplierOfSell(sell, extraDataSell, allPaymentMethods),
			choosenMethod
		)
			? true
			: false,
	}));
};

export const getAvailableSellsIds = (sells) =>
	sells.reduce(
		(sellIds, sell) => (sell.available ? [...sellIds, sell.id] : sellIds),
		[]
	);

export const replaceSellFromSells = (sell, sells) =>
	sells
		.map((s) => (s.id === sell.id ? setDeleteSell(sell) : s))
		.filter((sell) => !sell.delete);

export const evenSellsAvailable = (sells) =>
	sells.find((sell) => sell.available);

export const isAllSellRecordsDeleted = (sell) =>
	!sell.sellRecords.find((sr) => !sr.deleted);

function setDeleteSell(sell) {
	return isAllSellRecordsDeleted(sell) ? { ...sell, delete: true } : sell;
}

export const setSellsToSellGroup = (sells, sellGroup) => ({
	...sellGroup,
	sells: getSellsOfSellGroup(sells, sellGroup),
});

export const setSellsToSellsGroups = (sells, sellsGroups) =>
	sellsGroups
		.map((sellGroup) => setSellsToSellGroup(sells, sellGroup))
		.filter((sg) => sg.sells.length);

export function logger(sellsGroups) {
	sellsGroups.forEach((sg) => {
		sg.sells &&
			sg.sells.forEach((sell) => {
				console.log("sell", sell.id);
				console.log(
					"records",
					sell.sellRecords.map((r) => r.id)
				);
			});
	});

	return sellsGroups;
}

export function dividerPro(a, b) {
	let remain = a % b;
	return {
		result: (a - remain) / b,
		remain: remain,
	};
}

export function getSellLocation(locations, locationId) {
	return locations.find((l) => l.id === locationId)?.name;
}
