import React, { useState } from "react";
import { Link } from "react-router-dom";
import { connect, useSelector } from "react-redux";
import LoginModal from "../../features/modal/login-modal";
import YandexMap from "../../features/modal/YandexMapModal";
import { openModal } from "../../../actions/modal";
import getLocation from "../../features/getAddress";
import Logo from "../Logo";
import SearchForm from "../partials/SearchFormTwo/";
import DesktopMenuModal from "../Menus/Desktop/DesktopMenuModal";
import { useTranslation } from "react-i18next";
import market from "../../../config/market";
import SearchModal from "../../features/modal/SearchModal";
import CartMenuThree from "../partials/cart-menu-three";
import ChangeLanguageThree from "../../features/ChangeLangugeThree";
import { LinkComponent } from "../Menus/Desktop/LinkComponent";

function HeaderSix(props) {
	const {
		openModal,
		user,
		marketData,
		location: { address },
	} = props;
	const logo = marketData.settings.mainLogo;
	const { t } = useTranslation();
	const { productList } = useSelector((state) => state.choosenProducts);
	const { token } = useSelector((state) => state.user?.tokenData);
	const topMenu = useSelector((state) => state?.menu?.top);
	const horizontal = useSelector((state) => state?.menu?.menuHorizontal);
	const icons = market.config.icons;
	const [search, setSearch] = useState(false);
	const [isInputClicked, setIsInputClicked] = useState(false);
	function openLoginModal(e) {
		e.preventDefault();
		openModal("login");
	}
	function openMapModal(e) {
		if (window.webview) {
			window.sendMessageToApp("OPEN_LOCATION");
		} else {
			openModal("map");
			e.preventDefault();
		}
	}

	function openMenu(e) {
		e.preventDefault();
		const body = document.querySelector("body");
		body.classList.add("mmenu-active-two");
	}
	if (!address) {
		getLocation();
	}

	return (
		<header className="header-eight">
			<div className="header-middle sticky-header">
				<div className="container search">
					<div className="header-left">
						<Logo logo={logo} />
						<div className="header-six-ver-menu">
							<DesktopMenuModal />
							{window.innerWidth < 991 ? (
								<Link to="category" className="categroy-button">
									<img
										src={icons.categoryLight}
										alt="category"
									/>
								</Link>
							) : (
								<button
									onClick={openMenu}
									className="categroy-button d-flex justify-content-center align-items-center"
								>
									<img
										src={icons.categoryLight}
										alt="category"
									/>
									<span>{t("catalog")}</span>
								</button>
							)}
						</div>
					</div>
					<div className="header-center">
						<SearchForm
							inputId={"searchInputDesktop"}
							isInputClicked={isInputClicked}
							setIsInputClicked={setIsInputClicked}
						/>
					</div>
					<div className="header-right">
						<button
							className="input-opener only-mobile"
							onClick={() => setIsInputClicked(!isInputClicked)}
						>
							<img
								src={
									isInputClicked
										? icons.closer
										: icons.searchLight
								}
								alt="search"
							/>
						</button>
						<div className="dropdown cart-dropdown">
							{user.id ? (
								<Link className="profile-link" to="/profile">
									<img
										src={icons.user}
										alt=""
										style={{
											fontSize: "20px",
											height: "20px",
										}}
									/>
									<h6>{token ? t("profile") : t("login")}</h6>
								</Link>
							) : (
								<Link
									className="profile-link"
									to="#signin-modal"
									onClick={openLoginModal}
								>
									<img
										src={icons.user}
										alt="profile"
										style={{
											width: "20px",
											height: "20px",
										}}
									/>
									<h6>{t("login")}</h6>
								</Link>
							)}
						</div>
						<Link
							to="/choosen-products-list"
							className="choosen-products-icon"
							style={{
								fontSize: "24px",
							}}
						>
							<div className="position-relative">
								<img
									src={icons.favourites}
									alt="heart"
									style={{
										width: "22px",
										height: "22px",
									}}
								/>
								{productList.length > 0 ? (
									<span>{productList.length}</span>
								) : null}
							</div>
							<h6>{t("favourites")}</h6>
						</Link>
						<CartMenuThree />
						<ChangeLanguageThree />
					</div>
				</div>
				<div className="header-center mobile">
					<SearchForm
						inputId={"searchInputDesktop"}
						isInputClicked={isInputClicked}
						setIsInputClicked={setIsInputClicked}
					/>
				</div>
			</div>
			<div className="header-buttom-menu">
				<div className="for-container container">
					<div className="header-left justify-content-between">
						<div className="left-side d-flex align-items-center">
							<div
								className="location"
								style={{ padding: "10px 20px 10px" }}
							>
								<Link
									className="location-link"
									to={`${process.env.PUBLIC_URL}`}
									style={{ color: "black" }}
								>
									{" "}
									<p
										className="d-flex align-items-center"
										onClick={openMapModal}
										style={{ marginBottom: "0px" }}
									>
										<img
											src={market.config.icons.location}
											alt="location"
											className="mr-2"
										/>
										{address ? (
											<span>
												{address.administrativeArea},{" "}
												{address.name}
											</span>
										) : (
											<span>
												{t("no-address-selected")}
											</span>
										)}
									</p>
								</Link>
							</div>
							<div className="header-left-menu">
								{horizontal.map((item, i) => (
									<LinkComponent
										menu={item}
										key={i}
										className="info"
									/>
								))}
							</div>
						</div>
						<div className="header-right-menu">
							{topMenu.map((item, i) => (
								<LinkComponent
									menu={item}
									key={i}
									className="info"
								/>
							))}
						</div>
					</div>
				</div>
			</div>
			<LoginModal />
			<YandexMap />

			<SearchModal search={search} setSearch={setSearch} />
		</header>
	);
}

function mapStateToProps(state) {
	return {
		marketData: state.marketData,
		phoneNumber: state.marketData.phoneNumber,
		user: state.user.user,
		location: state.user.location,
	};
}

export default connect(mapStateToProps, { openModal })(HeaderSix);
