import React from "react";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { dt } from "../../../i18n";
import { getImageUrl } from "../../../utils/oxMethods";
import market from "../../../config/market";
import MediaImage from "../../../components/features/product/MediaImage";

export const MenuList = ({
	setMenuCatalog,
	subMenu,
	seeMore,
	slice,
	setMenuAllActive,
	menuAllActive,
}) => {
	const { t } = useTranslation();

	return (
		<ul className={`menu-list ${menuAllActive ? `active` : ``}`}>
			{menuAllActive
				? subMenu.map(
						(menu, i) =>
							menu && (
								<li key={i}>
									<Link
										onClick={() =>
											setMenuCatalog &&
											setMenuCatalog(false)
										}
										to={`/category/${menu.slug}`}
									>
										{dt(menu.name)}
									</Link>
								</li>
							)
				  )
				: subMenu.slice(0, slice).map(
						(menu, i) =>
							menu && (
								<li key={i}>
									<Link
										onClick={() =>
											setMenuCatalog &&
											setMenuCatalog(false)
										}
										to={`/category/${menu.slug}`}
									>
										{dt(menu.name)}
									</Link>
								</li>
							)
				  )}
			{seeMore && !menuAllActive && subMenu?.length > 10 ? (
				<Link
					to=""
					onClick={(e) => {
						setMenuAllActive(true);
						e.stopPropagation();
					}}
					className="see-more"
				>
					{t("see-all2")}
				</Link>
			) : null}
		</ul>
	);
};

export const MenuChildList = ({
	menuAllActive,
	setMenuAllActive,
	setMenuCatalog,
	setChildMenu,
	menuChild,
	seeMore = false,
	slice = 11,
}) => {
	// console.log(
	// 	menuChild.sort(function(a, b) {
	// 		return a.sort - b.sort;
	// 	})
	// );

	return (
		<ul className={`child-menu-list row ${menuAllActive ? `active` : ``}`}>
			{menuChild
				?.sort(function (a, b) {
					return a.sort - b.sort;
				})
				.map((subMenu) => (
					<li
						key={subMenu.id}
						className={`menu-item col-lg-4 col-md-3 col-6`}
					>
						<Link
							to={`/category/${subMenu.slug}`}
							onClick={() => {
								setMenuCatalog && setMenuCatalog(false);
								setChildMenu && setChildMenu(null);
							}}
							className="menu-link"
						>
							{market.config.home === 9 ? (
								<img
									src={getImageUrl(subMenu.image)}
									alt={subMenu.name}
								/>
							) : null}
							{dt(subMenu.name)}
						</Link>
						<MenuList
							setMenuCatalog={setMenuCatalog}
							slice={slice}
							seeMore={seeMore}
							subMenuSlug={subMenu.slug}
							subMenu={subMenu.children}
							setMenuAllActive={setMenuAllActive}
							menuAllActive={menuAllActive}
						/>
					</li>
				))}
		</ul>
	);
};

function HomeMenu({ setMenuChild, setMenuAllActive }) {
	const { menuVertical } = useSelector((state) => state.menu);
	const { t } = useTranslation();

	return (
		<div className="menu-wrapper">
			<h3 className="menu-heading">{t("catalog_products")}</h3>
			<ul className="menu-list">
				{menuVertical.slice(0, 12).map((menu) => {
					return (
						<li
							key={menu.id}
							className="menu-item"
							onMouseEnter={() => {
								setMenuAllActive(false);
								setMenuChild(menu.children);
							}}
						>
							<Link
								className="menu-link"
								to={`${
									menu.children.length > 0
										? "#"
										: `/category/${menu.slug}`
								}`}
							>
								{menu.image ? (
									<MediaImage
										image={menu.image}
										largeSize="50x_"
										mediumSize="50x_"
										smallSize="50x_"
										alt={menu.name}
									/>
								) : null}
								<span>{dt(menu.name)}</span>
							</Link>
							{menu.children.length > 0 ? (
								<i className="icon-angle-right"></i>
							) : null}
						</li>
					);
				})}
				<li
					className="menu-item"
					onMouseEnter={() => {
						setMenuChild("");
					}}
				>
					<Link className="menu-link" to={`/all-categories`}>
						<img
							src="/assets/images/vibo/all_categories.svg"
							alt={t("all_categories")}
						/>
						<span>{t("all_categories")}</span>
					</Link>
					<i className="icon-angle-right"></i>
				</li>
			</ul>
		</div>
	);
}

export default HomeMenu;
