import { setUserLocation } from "../../actions/user";
import { getAddressByCoordinate } from "../../api/mapAPI";
import { dispatch, getState } from "../../store";

async function getMessageLocaitionInfo(message) {
	const location = getState().user.location;
	const data = await getAddressByCoordinate(
		message.data.latitude,
		message.data.longitude
	);
	const m = {
		coordinate: {
			latitude: data.latitude,
			longitude: data.longitude
		},
		address: {
			...data
		},
		history: location.history ? [...location.history] : location.history
	};

	dispatch(setUserLocation({ ...m }));
}

const reciveMessageFromApp = message => {
	console.log("The message has been received");
	try {
		const messageJSON = JSON.parse(message);
		if (messageJSON?.type === "LOCATION")
			getMessageLocaitionInfo(messageJSON);
	} catch (error) {
		console.error(error);
	}
};

export default reciveMessageFromApp;
