import React from "react";
import { Link } from "react-router-dom";
import { connect, useDispatch } from "react-redux";
import HorizontalMenu from "../Menus/Desktop/HorizontalMenu";
import VerticalMenuTwo from "../Menus/Desktop/VerticalMenuTwo";
import CartMenu from "../partials/cart-menu";
import SearchForm from "../partials/SearchForm";
import { openModal } from "../../../actions/modal";
import YandexMapModal from "../../features/modal/YandexMapModal";
import getLocation from "../../features/getAddress";
import { useTranslation } from "react-i18next";
import ChangeLanguageTwo from "../../features/ChangeLanguageTwo";
import Logo from "../Logo";

function HeaderZero(props) {
	const { t } = useTranslation();
	const {
		user,
		token: { token },
		settings,
		location: { address }
	} = props;
	const dispatch = useDispatch();

	const footerLogo = settings?.footerLogo;

	function openMapModal(e) {
		e.preventDefault();
		dispatch(openModal("map"));
	}

	if (!address) {
		getLocation();
	}

	return (
		<header className="header-zero">
			<div className="header-top">
				<div className="container">
					<div className="header-left">
						<Link
							to={`${process.env.PUBLIC_URL}`}
							className="locationBut ml-2"
						>
							<p
								onClick={openMapModal}
								style={{ marginBottom: "0px" }}
							>
								<i className="i-icon icon-map-marker"></i>
								<span>
									{address
										? `${address.administrativeArea}, ${address.name}`
										: t("address-not-selected")}
								</span>
							</p>
						</Link>
					</div>
					<div className="header-right">
						<div className="auth mr-5">
							{user.id ? (
								<Link to="/profile">{t("profile")}</Link>
							) : (
								<Link to="/login">{t("enter_profile")}</Link>
							)}
						</div>
						<ChangeLanguageTwo />
						{settings.phoneNumbers?.slice(0, 2).map((number, i) => (
							<a key={i} href={`tel:${number}`}>
								<p
									style={{
										marginBottom: "0px",
										marginRight: "10px"
									}}
								>
									<i className="i-icon icon-phone"></i>{" "}
									{number}
								</p>
							</a>
						))}
					</div>
				</div>
			</div>
			<div className="header-middle">
				<div className="container">
					<div className="header-left desktop">
						<div className="desktop-logo">
							<Logo logo={settings.mainLogo} />
						</div>
						<div className="mobile-logo">
							<Logo logo={footerLogo} />
						</div>
					</div>

					<div className="header-center">
						<SearchForm />
					</div>

					<div className="header-right">
						<div className="dropdown cart-dropdown">
							<Link
								className="dropdown-toggle"
								to={token ? "/profile" : "/login"}
							>
								<div className="p-2">
									<img
										src="/assets/images/homeplus/profile.svg"
										alt="profile svg icon"
										width="20"
										height="20"
									/>
									<p>{token ? t("profile") : t("login")}</p>
								</div>
							</Link>
						</div>
						<div className="dropdown cart-dropdown">
							<Link
								className="dropdown-toggle"
								to="/choosen-products-list"
							>
								<div className="p-2">
									<img
										src="/assets/images/homeplus/favourite.svg"
										alt="heart svg icon"
										width="20"
										height="20"
									/>
									<p>{t("favourites")}</p>
								</div>
							</Link>
						</div>
						<CartMenu
							icon={
								<img
									src="/assets/images/homeplus/cart.svg"
									alt="cart svg icon"
									width="20"
									height="20"
								/>
							}
						/>
					</div>
				</div>
			</div>

			<div className="wrap-container sticky-header">
				<div className="header-bottom">
					<div className="container">
						<div className="mobile">
							<button
								className="mobile-menu-toggler"
								onClick={() =>
									document
										.querySelector("body")
										.classList.add("mmenu-active")
								}
							>
								<span className="sr-only"></span>
								<i className="icon-bars"></i>
							</button>
						</div>
						<div className="col-lg-2 header-left mr-2">
							<VerticalMenuTwo />
						</div>
						<div className="header-center col-lg-10">
							<HorizontalMenu />
						</div>
					</div>
				</div>
			</div>
			<YandexMapModal />
		</header>
	);
}

function mapStateToProps(state) {
	return {
		settings: state.marketData.settings,
		phoneNumber: state.marketData.phoneNumber,
		user: state.user.user,
		token: state.user.tokenData,
		location: state.user.location
	};
}

export default connect(mapStateToProps)(HeaderZero);
