import React, { useContext, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { dt } from "../../../../i18n";
import SearchContext from "./SearchContext";

export default function CategoriesModal() {
	const { category, setCategory } = useContext(SearchContext);
	const [childrens, setChildrens] = useState([]);
	const { menuVertical } = useSelector(state => state.menu);
	const { t } = useTranslation();

	useEffect(() => {
		let arr = [];
		menuVertical.forEach(menu => {
			if (menu.children.length) {
				arr.push(...menu.children);
			}
		});
		setChildrens(arr);
	}, []);

	function close(e) {
		e.target.className === "categories_overlay" &&
			setCategory({ ...category, open: false });
	}

	return (
		category.open && (
			<div className="categories_overlay" onClick={e => close(e)}>
				<div className="categories_content">
					<div className="categories_list row">
						<div className="row col-12">
							<div
								className="categories_item col-6"
								onClick={() =>
									setCategory({
										open: false
									})
								}
							>
								<img
									src="/assets/images/vibo/all_categories.svg"
									alt="all"
								/>
								<p>{t("btn-every")}</p>
							</div>
						</div>
						{childrens.map(menu => {
							return (
								<div
									key={menu.id}
									className="categories_item col-6"
									onClick={() =>
										setCategory({
											open: false,
											menu: menu
										})
									}
								>
									{menu.image ? (
										<img
											src={menu.image.urls["50x_"]}
											alt={menu.image.originalName}
										/>
									) : null}
									<p>{dt(menu.name)}</p>
								</div>
							);
						})}
					</div>
				</div>
			</div>
		)
	);
}
