import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import { useDispatch } from "react-redux";
import regions from "./regions";
import { setUserLocation } from "../../../actions/user";
import { useTranslation } from "react-i18next";
import { debounce } from "../../../utils/debounce";
import { getCoordinatesByAddress } from "../../../api/mapAPI";

function MapModalDetails({
	center,
	setAddress,
	setCenter,
	history,
	address,
	closeModal,
	activePanel,
	setActivePanel,
	activeSearchPanel,
	setActiveSearchPanel
}) {
	const [disable, setDisable] = useState(false);
	const dispatch = useDispatch();
	const { t } = useTranslation();
	const [searchResults, setSearchResults] = useState([]);
	const [loading, setLoading] = useState(false);
	function onChangeInput(e) {
		setLoading(true);
		const query = e.target.value;
		debounce(
			async () => {
				await getCoordinatesByAddress(query).then(data => {
					setSearchResults(data);
				});
				setLoading(false);
			},
			"searchapi",
			1000
		);
	}
	useEffect(() => {
		if (!address) {
			setDisable(true);
		} else {
			setDisable(false);
		}
		if (activeSearchPanel) {
			document.getElementById("auto-focus-input").focus();
		}
	}, [address, activeSearchPanel]);

	function selectFn(value) {
		regions.map(r =>
			r.value === value
				? setCenter({ center: [r.latitude, r.longitude] })
				: null
		);
		setAddress({ ...address, region: value });
	}
	return (
		<>
			<div className="map-modal-details">
				<p className="location-address">
					{address ? address.name : ""}
				</p>
				{history?.length > 0 && (
					<ul className="address-history">
						{history.map((h, i) => (
							<li
								key={i}
								onClick={() =>
									setCenter({
										...center,
										center: [h.latitude, h.longitude]
									})
								}
							>
								{h.name}
							</li>
						))}
					</ul>
				)}

				<select
					value={address ? address.region : "Ташкент"}
					className="map-select form-control"
					onChange={e => selectFn(e.target.value)}
				>
					{regions.map((r, i) => (
						<option key={i} value={r.value}>
							{r.name}
						</option>
					))}
				</select>

				<button
					disabled={disable}
					className="btn btn-primary btn-address"
					onClick={() => {
						dispatch(closeModal());
						const h = {
							name: address.name,
							latitude: center.center[0],
							longitude: center.center[1]
						};
						const nh =
							history?.length > 0
								? history.filter(q => q.name !== h.name)
								: [];
						if (nh.length > 6) {
							nh.pop();
						}
						nh.unshift(h);

						const location = {
							coordinate: {
								latitude: center.center[0],
								longitude: center.center[1]
							},
							address,
							history: nh
						};
						dispatch(setUserLocation({ ...location }));
					}}
				>
					{t("confirm-address")}
				</button>
			</div>

			<div
				className={`user-location ${activePanel ? `activePanel` : ``}`}
			>
				<button
					className={`btn opener`}
					onClick={() => setActivePanel(!activePanel)}
				>
					<img src="/assets/images/border.svg" alt="" />
				</button>

				{history?.length > 0 && (
					<ul className="address-history">
						{history.map((h, i) => (
							<li
								key={i}
								onClick={() =>
									setCenter({
										...center,
										center: [h.latitude, h.longitude]
									})
								}
							>
								{h.name}
							</li>
						))}
					</ul>
				)}

				<button
					className="btn search-button d-flex justify-content-between align-items-center"
					onClick={() => setActiveSearchPanel(!activeSearchPanel)}
				>
					<span>
						{address ? address.name : t("address-not-selected")}
					</span>
					<span className="mr-1">{">"}</span>
				</button>
				<form>
					<div className="user-address-info">
						<div className="row">
							<div className="col-4">
								<input
									type="text"
									placeholder={t("entrance")}
									className="form-control"
								/>
							</div>
							<div className="col-4">
								<input
									type="text"
									placeholder={t("floor")}
									className="form-control"
								/>
							</div>
							<div className="col-4">
								<input
									type="text"
									placeholder={t("flat")}
									className="form-control"
								/>
							</div>
						</div>
					</div>
					<div className="orientr">
						<input
							type="text"
							placeholder={t("orintir")}
							className="form-control"
						/>
					</div>

					<button
						disabled={disable}
						className="add-address-button btn"
						onClick={() => {
							dispatch(closeModal());
							const h = {
								name: address.name,
								latitude: center.center[0],
								longitude: center.center[1]
							};
							const nh =
								history?.length > 0
									? history.filter(q => q.name !== h.name)
									: [];
							if (nh.length > 6) {
								nh.pop();
							}
							nh.unshift(h);

							const location = {
								coordinate: {
									latitude: center.center[0],
									longitude: center.center[1]
								},
								address,
								history: nh
							};
							dispatch(setUserLocation({ ...location }));
						}}
					>
						{t("confirm-address")}
					</button>
				</form>
			</div>
			{/* </Resizer> */}

			<div
				className={`map-serach-panel ${
					activeSearchPanel ? `search-active` : ``
				}`}
			>
				<div className="serarch-panel">
					<div className="button-title d-flex align-items-center">
						<button
							className="back-button"
							onClick={() =>
								setActiveSearchPanel(!activeSearchPanel)
							}
						>
							<img
								src="/assets/images/leftarrow.svg"
								className="m-auto"
								alt=""
							/>
						</button>
						<h2 className="title">{t("address-not-selected")}</h2>
					</div>
					<form>
						<input
							type="text"
							id="auto-focus-input"
							placeholder={t("search")}
							className="form-control search-input"
							onChange={e => {
								onChangeInput(e);
							}}
						/>
					</form>
				</div>
				<div className="search-results">
					{loading ? (
						<div className="search-loader" />
					) : searchResults.length > 0 ? (
						searchResults.map((searchResult, i) => {
							return (
								<div
									className="result"
									key={i}
									onClick={() => {
										setActiveSearchPanel(
											!activeSearchPanel
										);
										setCenter({
											center: [
												searchResult.coordinate
													.latitude,
												searchResult.coordinate
													.longitude
											]
										});
									}}
								>
									<h3 className="title">
										{searchResult.address.addressLine1}
									</h3>
									<h4 className="description">
										{searchResult.address.countryCode}
									</h4>
								</div>
							);
						})
					) : null}
				</div>
			</div>
		</>
	);
}

export default MapModalDetails;
