const { formatterCurrency, formatterCurrencyNative } = require("./string");

export function getPrice(price, formatted = false) {
	if (price) {
		let currency = price.first;
		if (formatted) {
			return formatterCurrency(price[currency], currency);
		}
		return price[currency];
	}
	return 0;
}
export function toTop() {
	window.scroll({
		top: 0,
	});
}

export const removeProps = (propertiesRowSort, filterProps) => {
	const properties = [];

	// filterProps must be array containing only strings
	filterProps = filterProps.reduce((prev, next) => {
		return {
			...prev,
			[next]: next,
		};
	}, {});

	propertiesRowSort.forEach((prop) => {
		if (prop && filterProps[prop.name]) {
			properties.push(prop);
		}
	});

	return properties;
};

export function getAddress(address) {
	let { administrativeArea, locality, addressLine1 } = address;
	return (
		(addressLine1 || "") + " " + (locality || "") + " " + administrativeArea
	);
}

export function calculateYear(year) {
	const currentYear = new Date().getFullYear();
	const r = currentYear - year;
	const lang = localStorage.getItem("lang");
	if (lang === "uz") {
		return r + " yil";
	}
	return r === 1 ? r + " год" : r <= 4 ? r + " года" : r + " лет";
}

export function getImageUrl(image, size = "original") {
	if (!image) {
		return "https://telegra.ph/file/3a01e53662cea1a425220.png";
	}
	return (
		image.urls[size] ||
		image.urls["300x_"] ||
		image.urls["100x_"] ||
		image.urls["150x_"] ||
		image.urls["800x_"] ||
		"https://telegra.ph/file/3a01e53662cea1a425220.png"
	);
}

export function trueFalseReturn(condition, onTrue, onFalse) {
	if (condition) {
		return onTrue;
	}

	return onFalse;
}

export function getPriceDiscount(data) {
	if (data.discount) {
		return data.newPrice;
	}
	return data.oldPrice;
}

export function getBrandAttribute(product, setting) {
	if (setting?.brandAttribute === "supplier") {
		return product?.variation.supplier;
	} else {
		if (setting?.brandAttribute) {
			let prop = product?.variation?.productProperties?.find(
				(prop) => prop.name === setting.brandAttribute
			);
			if (prop) {
				return prop.value;
			}
		}
		return null;
	}
}

export function convertMarketData(marketData) {
	const batr = marketData.settings.brandAttribute;
	if (batr === "supplier") {
		marketData.settings.isSupplier = true;
	}
	return marketData;
}

export function getPropSettings(varProp, productProps) {
	let key = varProp.name + "-" + varProp.value;

	let productProp = productProps[key];

	return productProp && productProp.settings;
}

export function getProductPrice(product) {
	if (product.discount > 0) {
		return formatterCurrencyNative(product.data.newPrice);
	}
	return formatterCurrencyNative(product.data.oldPrice);
}

export function availableSrcBGImage(backgrounds) {
	let background = Object.values(backgrounds).find(
		(background) => background.path
	);
	return background ? background.path : null;
}

export function getTagName(product) {
	if (product.tags?.length) {
		return product.tags[0].name;
	} else {
		return product.name?.split(" ")[0];
	}
}

export function makeLoopId(arr) {
	var idArr = [];
	arr.forEach((item) => {
		idArr.push(`ids[]=${item?.productId}`);
	});

	return idArr.toString().replace(/,/g, "&");
}

export function loadScript(src) {
	let script = document.createElement("script");
	script.append(src);
	document.body.append(script);
}
