import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { closeModal } from "../../../actions/modal";
import YandexMap from "../YandexMap";

function YandexMapLocations({ supplierAdress, pickUp }) {
	const [center, setCenter] = useState({
		center: [41.3112092101035, 69.27965188818546],
		zoom: 16
	});
	const dispatch = useDispatch();
	useEffect(() => {
		if (supplierAdress?.longitude && supplierAdress?.latitude) {
			setCenter({
				...center,
				center: [supplierAdress.latitude, supplierAdress.longitude]
			});
		} else {
			setCenter({
				...center,
				center: [41.3112092101035, 69.27965188818546]
			});
		}
	}, [supplierAdress]);
	function closeMapModal() {
		setTimeout(() => {
			dispatch(closeModal("map-modal"));
		}, 200);
	}

	return (
		<div className="map-body">
			<div className="map_content">
				<button
					type="button"
					className="close closeMap"
					data-dismiss="modal"
					aria-label="Close"
					onClick={closeMapModal}
				>
					<span aria-hidden="true">
						<i className="icon-close"></i>
					</span>
				</button>

				<YandexMap
					address={supplierAdress?.addressLine1}
					location={supplierAdress}
					center={center}
					pickUp={pickUp}
					width={"100%"}
					setCenter={setCenter}
				/>
			</div>
		</div>
	);
}

export default YandexMapLocations;
