export const paymentCart = (
	userId,
	records,
	paymentMethod,
	shippingAddress,
	token
) =>
	mainCaller(
		`/sellgroups/finish?customer=${userId}`,
		"POST",
		{
			sells: records,
			paymentMethod,
			shippingAddress
		},
		{
			Authorization: `Bearer ${token}`
		}
	);

export const finishSell = (
	sells,
	paymentMethod,
	bankCard,
	comment,
	shippingAddress,
	token,
	returnUrl,
	shippingTime
) =>
	mainCaller(
		"/sellgroups/finish",
		"POST",
		{
			sells,
			paymentMethod,
			...bankCard,
			shippingTime,
			shippingAddress,
			note: comment,
			returnUrl
		},
		{
			Authorization: "Bearer " + token
		}
	);

export const sendWithDataPayment = (url, data) => {
	const options = {
		url,
		method: "POST",
		data: jsonToFormData(data),
		withCredentials: true
	};

	return axios(options).then(r => {
		return r.data;
	});
};
