import React, { useState } from "react";
import Modal from "react-modal";
import { useDispatch, useSelector } from "react-redux";
import { closeModal } from "../../../actions/modal";
import MapModalDetails from "./MapModalDetails";
import YandexMap from "../YandexMap";
import regions from "./regions";

const customStyles = {
	content: {
		top: "50%",
		transform: "translateY(-50%)",
		maxWidth: "100%",
		padding: "20px"
	},
	overlay: {
		backgroundColor: "rgba(77,77,77,0.6)",
		zIndex: "10003"
	}
};

function YandexMapModal({ supplierAdress }) {
	const showModal = useSelector(state => state.modal.showModal);
	const modal = useSelector(state => state.modal.modal);
	const { coordinate, address: addressUser, history } = useSelector(
		state => state.user.location
	);
	const dispatch = useDispatch();

	const [center, setCenter] = useState({
		center: coordinate
			? [coordinate.latitude, coordinate.longitude]
			: [41.3112092101035, 69.27965188818546],
		zoom: 16
	});

	const [address, setAddress] = useState(
		addressUser
			? {
					...addressUser,
					name: addressUser.name
			  }
			: null
	);

	function closeMapModal() {
		setTimeout(() => {
			dispatch(closeModal("map-modal"));
		}, 200);
	}

	function selectFn(value) {
		regions.map(r =>
			r.value === value
				? setCenter({ center: [r.latitude, r.longitude] })
				: null
		);
		setAddress({ ...address, region: value });
	}

	return (
		<Modal
			isOpen={showModal && "map" === modal}
			onRequestClose={closeMapModal}
			style={customStyles}
			className="modal-dialog map-modal map-modal-three"
			id="map-modal"
		>
			<div className="modal-content">
				<div className="modal-body">
					<div className="map_content">
						<div
							className={`location-wrapper ${supplierAdress &&
								"d-none"}`}
						>
							<img
								src="assets/images/pin.png"
								alt="location"
								width="22"
								height="22"
							/>
						</div>
						{!supplierAdress && (
							<MapModalDetails
								address={address}
								setAddress={setAddress}
								center={center}
								setCenter={setCenter}
								history={history}
								closeModal={closeModal}
							/>
						)}

						<YandexMap
							address={supplierAdress}
							center={center}
							width={"100%"}
							setCenter={setCenter}
							onChange={address => {
								setAddress(address);
							}}
						/>
					</div>
					<div className="regions">
						<button
							type="button"
							className="close closeMap"
							data-dismiss="modal"
							aria-label="Close"
							onClick={closeMapModal}
						>
							<span aria-hidden="true">
								<i className="icon-close"></i>
							</span>
						</button>
						<h3>Выберите регион</h3>
						<ul className="region_list">
							{regions.map((r, i) => (
								<li
									onClick={() => selectFn(r.value)}
									className="region_item"
									key={i}
									value={r.value}
								>
									{r.name}
								</li>
							))}
						</ul>
					</div>
				</div>
			</div>
		</Modal>
	);
}

export default YandexMapModal;
