import React, { useContext } from "react";
import SearchContext from "./SearchContext";
import Loader from "../../../features/loaders/loader";
import { useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
import market from "../../../../config/market";
import { dt } from "../../../../i18n";

export default function SearchForm() {
	const {
		searchFn,
		renderSearchPage,
		result,
		setResult,
		setHidden,
		value,
		category,
		setCategory,
		inputId,
	} = useContext(SearchContext);
	const { t } = useTranslation();

	const { menuVertical } = useSelector((state) => state.menu);
	const menus = [];
	if (menuVertical) {
		for (let i = 0; i < menuVertical.length; i++) {
			menus.push(menuVertical[i].name);
		}
	}

	return (
		<div className="search-form-wrapper">
			<form
				className="search-form"
				method="get"
				onSubmit={(e) => renderSearchPage(e)}
			>
				<div
					onClick={() => setCategory({ ...category, open: true })}
					className="btn btn-everywhere"
				>
					<span>
						{category.menu
							? dt(category.menu.name)
							: t("btn-every")}
					</span>
					<i className="icon-angle-down"></i>
				</div>
				<input
					onFocus={() => setHidden(true)}
					onBlur={() => {
						setTimeout(() => {
							setHidden(false);
							setResult({
								loading: false,
								data: [],
								menu: [],
							});
						}, 500);
					}}
					defaultValue={value}
					onChange={(e) => {
						searchFn(e.target.value.trim());
					}}
					type="search"
					className="form-control"
					autoComplete="off"
					id={inputId}
					placeholder={`${t("search_in")} ${market.subdomain}`}
				/>
				<button
					disabled={result.loading}
					className="btn btn-primary search-btn"
					type="submit"
				>
					{result.loading ? (
						<Loader />
					) : (
						<>
							<i className="icon-search"></i>
							<span>{t("search")}</span>
						</>
					)}
				</button>
			</form>
		</div>
	);
}
