import React from "react";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import SearchFormTwo from "../../../common/partials/SearchFormTwo";
import { dt } from "../../../../i18n";
import market from "../../../../config/market";
import { LinkComponent } from "./LinkComponent";

function VerticalMenu(props) {
	const { menu, verticalMenuChilds, setVerticalMenuChilds } = props;

	return (
		<div className="vertical-menu-content">
			<div className="container">
				<div className="vertical-menu-body row">
					<div className="col-md-3">
						<ul className="vertical-menu-list">
							{menu?.map((menuChild) => (
								<li
									key={menuChild.id}
									onMouseEnter={
										menuChild.children.length > 0
											? () =>
													setVerticalMenuChilds(
														menuChild
													)
											: null
									}
								>
									<Link
										to={`/category/${menuChild.slug}`}
										className="vertical-menu-child"
									>
										{dt(menuChild.name)}
									</Link>
								</li>
							))}
						</ul>
					</div>
					{verticalMenuChilds ? (
						<div className="menu-submenus col-md-9">
							<VerticalMenuChilds
								verticalMenuChilds={verticalMenuChilds}
							/>
						</div>
					) : null}
				</div>
			</div>
		</div>
	);
}

function VerticalMenuChilds({ verticalMenuChilds }) {
	return (
		<ul className="vertical-menu-childrens-list row">
			{verticalMenuChilds?.children?.map((submenu, i) => (
				<li className="submenu col-md-4" key={i}>
					<Link
						to={`/category/${submenu.slug}`}
						className="submenu-title"
					>
						{dt(submenu.name)}
					</Link>
					<ul className="row">
						{submenu.children?.map((submenuChild, i) => (
							<li className="col-md-12 submenu-childs" key={i}>
								<Link
									to={`/category/${submenuChild.slug}`}
									className="submenu-child"
								>
									{dt(submenuChild.name)}
								</Link>
							</li>
						))}
					</ul>
				</li>
			))}
		</ul>
	);
}

function MainMenu({ setIsInputClicked }) {
	const allMenuData = useSelector((state) => state.menu);
	const horizontal = allMenuData?.menuHorizontal.sort((a, b) =>
		a.sort > b.sort ? 1 : b.sort > a.sort ? -1 : 0
	);
	const [verticalMenu, setVerticalMenu] = useState(false);
	const [verticalMenuChilds, setVerticalMenuChilds] = useState();
	const [search, setSearch] = useState(false);
	const { t } = useTranslation();

	return (
		<div className="menu-search row display-flex">
			<div className="serach-and-category col-md-3 display-flex">
				<button
					className="search-button display-flex"
					onClick={() => setSearch(!search)}
				>
					<img
						src="/assets/images/baby_center/Search.svg"
						alt="search"
					/>
					<span>Поиск</span>
				</button>

				<button
					className="desktop-category-btn display-flex"
					onClick={(e) => {
						e.stopPropagation();
						setVerticalMenu(!verticalMenu);
					}}
				>
					{market.config.home === 7 ? (
						<img
							src="/assets/images/baby_center/but.svg"
							alt="category"
						/>
					) : (
						<img
							src={market.config.icons.category}
							alt="category"
						/>
					)}
					<span>{t("catalog_products")}</span>
				</button>
			</div>
			<div className="horizontal-menu col-md-9">
				<ul className="first-menu-list display-flex">
					{horizontal.map((firstMenu, index) => (
						<li
							key={index}
							className={`first-menu-child ${
								(firstMenu.id === 2 &&
									market.config.home === 8) ||
								(firstMenu.id === 7 && market.config.home === 8)
									? `with-soon`
									: ``
							}`}
						>
							<LinkComponent menu={firstMenu} />
							<div className="disabled-link d-none" />
							<span className="soon d-none">{t("soon")}</span>
						</li>
					))}
				</ul>
			</div>
			{search ? (
				<div
					className="search-form"
					// onClick={() => setSearch(!search)}
				>
					<div className="search-content">
						<div className="container">
							<div className="search-body">
								<SearchFormTwo
									inputId={"searchInputDesktop"}
									setSearch={setSearch}
									search={search}
									setIsInputClicked={setIsInputClicked}
								/>
							</div>
						</div>
					</div>
				</div>
			) : null}
			{verticalMenu ? (
				<div
					className="vertical-menu"
					onClick={() => setVerticalMenu(false)}
				>
					<VerticalMenu
						menu={
							allMenuData?.menuVertical.length > 0
								? allMenuData?.menuVertical
								: allMenuData.other
						}
						verticalMenuChilds={verticalMenuChilds}
						setVerticalMenuChilds={setVerticalMenuChilds}
					/>
				</div>
			) : null}
		</div>
	);
}

export default MainMenu;
