import React from "react";
import { setUserForm } from "../../../actions/user";
import { connect } from "react-redux";
import RegistrationForm from "../forms/registrationForm";
import { setPhoneToGetVerifyCode } from "../../../api/verification";

function RegFormTab({ onSuccessSubmit, dispatch, modalData }) {
	const submit = values => {
		dispatch(setUserForm(values));
		setPhoneToGetVerifyCode(values.phoneNumber);
		onSuccessSubmit();
	};
	return (
		<RegistrationForm
			modalData={modalData}
			onSubmit={submit}
			initialValues={{
				phoneNumber: modalData?.phoneNum || "+998"
			}}
		/>
	);
}

export default connect(null, null)(RegFormTab);
