import mainCaller, { getUserOx, oxAuthUser } from "./mainCaller";

export function createUser({
	phoneNumber,
	firstName,
	lastName,
	gender,
	passwordFirst,
	// passwordSecond,
	personType
}) {
	const user = {
		profile: {
			phoneNumber,
			firstName,
			lastName,
			gender
		},
		personType: personType,
		password: {
			first: passwordFirst,
			second: passwordFirst
		}
	};
	return mainCaller("/customer", "POST", user);
}

export function updateUser(user) {
	return mainCaller("/customer", "POST", user);
}

export function getUserById(userId) {
	return mainCaller("/customer", "POST", userId);
}

export function getUserSellList(page = 1, token) {
	return mainCaller(`/customer-sell?page=${page}`, "GET", null, {
		Authorization: `Bearer ${token}`
	});
}
export function getQRCode(token) {
	return mainCaller(`/customer/activation-code`, "GET", null, {
		Authorization: `Bearer ${token}`
	});
}
export function getDeliveryGroups(token, size = 2) {
	return mainCaller(
		`/customer/delivery-groups?size=${size}&page=1`,
		"GET",
		null,
		{
			Authorization: `Bearer ${token}`
		}
	);
}

export function buyOneClick(
	name,
	phone,
	stock,
	count,
	shippingAddress,
	shippingService
) {
	return mainCaller(`/fast-checkout`, "POST", {
		name,
		phone,
		stock,
		count,
		shippingAddress,
		shippingService: shippingService
	});
}

export function getUserByToken(token) {
	return getUserOx(token);
}

export function authUser({ password, username }) {
	return oxAuthUser(password, username);
}

export function addNewShippingAddress(address, token) {
	return mainCaller(
		"/customers/add-address",
		"POST",
		{
			countryCode: address.countryCode,
			administrativeArea: address.administrativeArea,
			locality: address.region,
			dependentLocality: address.dependentLocality,
			addressLine1: address.addressLine1,
			latitude: address.latitude,
			longitude: address.longitude,
			postalCode: address.postalCode,
			type: "shipping"
		},
		{ Authorization: `Bearer ${token}` }
	);
}

export function removeShippingAddress(id, token) {
	return mainCaller(`/address/${id}`, "DELETE", null, {
		Authorization: `Bearer ${token}`
	});
}

export function getUserCard(token) {
	return mainCaller("/bank-cards", "GET", null, {
		Authorization: `Bearer ${token}`
	});
}
export function addUserCard(cardNumber, cardExpiry, token) {
	return mainCaller(
		"/bank-cards",
		"POST",
		{
			cardNumber: cardNumber.replace(/ /g, ""),
			cardExpiry
		},
		{
			Authorization: `Bearer ${token}`
		}
	);
}

export function deleteUserCard(id, token) {
	return mainCaller(`/bank-cards/${id}`, "DELETE", null, {
		Authorization: `Bearer ${token}`
	});
}

export function verifyCard(code, cardId, token) {
	return mainCaller(
		`/bank-cards/${cardId}/confirm`,
		"POST",
		{
			otp: code
		},
		{
			Authorization: `Bearer ${token}`
		}
	);
}

export function confirmCard(sellId, code, token) {
	return mainCaller(
		`/sellgroups/${sellId}/multicard/confirm-payment`,
		"PUT",
		{
			otp: code
		},
		{
			Authorization: `Bearer ${token}`
		}
	);
}

export function requestPassword(phoneNumber) {
	return mainCaller(`/request-password`, "POST", phoneNumber);
}

export function requestPasswordChange({ code, pass1 }) {
	return mainCaller(`/request-password/change`, "PUT", {
		code: code,
		password: {
			first: pass1,
			second: pass1
		}
	});
}
