import React from "react";
import { withRouter } from "react-router-dom";
import market from "../../config/market";
function dNone(on) {
	return on ? " d-none" : "";
}

function BackButton({ history, location, icon, parentMenu }) {
	function goBack() {
		location.pathname.split("/")[1] === "category"
			? parentMenu
				? history.push(`/category/${parentMenu.slug}`)
				: history.push("/")
			: history.goBack();
	}

	function isBackButtonHidden() {
		return location.pathname === "/";
	}

	return (
		<div
			className={
				"header-left only-mobile back-block" +
				dNone(isBackButtonHidden())
			}
		>
			<a
				className={`exit-product-page ${
					market.config.home === 6
						? `align-items-center justify-content-center`
						: ``
				}`}
				href="/"
				onClick={e => {
					e.preventDefault();
					goBack();
				}}
			>
				{icon ? (
					<img
						src={
							icon ? icon : "/assets/images/icons/arrow-left.svg"
						}
						alt="closer"
						width="30"
						height="10"
					/>
				) : (
					<i className={"icon-arrow-left"}></i>
				)}
			</a>
		</div>
	);
}

export default withRouter(BackButton);
