import React, {
	Fragment,
	//  useState
} from "react";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import { Field, reduxForm } from "redux-form";
import market from "../../../config/market";
// import ReCAPTCHA from "react-google-recaptcha";

const otherField = (props) => {
	let {
		input,
		// label,
		type,
		className,
		placeholder,
		meta: { touched, error },
	} = props;
	if (touched) {
		if (error) {
			className = className + " warning";
		}
	}

	let phoneNumber = "+998";

	return (
		<Fragment>
			{type === "tel" ? (
				<input
					{...input}
					placeholder={placeholder}
					className={className}
					value={input.value ? input.value : phoneNumber}
					onChange={(e) => {
						phoneNumber = `+${e.target.value.match(/\w/)}`;
						input.onChange(e);
					}}
					type={type}
				/>
			) : (
				<input
					{...input}
					placeholder={placeholder}
					className={className}
					value={input.value}
					onChange={(e) => {
						input.onChange(e);
					}}
					type={type}
				/>
			)}
			{touched && error && <span>{error}</span>}
		</Fragment>
	);
};

function AuthForm(props) {
	const { t } = useTranslation();
	const { handleSubmit, setOpen, login, setLogin } = props;
	// const [
	// isVerified
	//  setIsVerified
	// ] = useState(false);
	// const [sitekey] = useState(
	// 	window.location.hostname === "localhost"
	// 		? "6LfssCwdAAAAADaZB0sfbd6wZwTMxvsfcfQ09WCi"
	// 		: market.config.sitekey
	// );

	// function handleOnChange() {
	// 	setIsVerified(true);
	// }
	return (
		<form onSubmit={handleSubmit}>
			<div className="form-group">
				<label htmlFor="username">
					{t(
						market.config.b2b_auth
							? "username-email"
							: "phone-number"
					)}
				</label>
				<Field
					type={`${market.config.b2b_auth ? "text" : "tel"}`}
					className="form-control"
					id="username"
					// placeholder={"+998"}
					component={otherField}
					name="username"
					required
				/>
			</div>
			<div className="form-group">
				<label htmlFor="singin-password-2">{t("pass")}</label>
				<Field
					type="password"
					className="form-control"
					placeholder={t("Введите пароль")}
					id="singin-password-2"
					required
					component={otherField}
					name="password"
				/>
			</div>
			{props.error ? (
				<div>
					<span className="error-text">{props.error}</span>
				</div>
			) : null}
			<div className="form-footer">
				{/* {sitekey ? (
					<ReCAPTCHA
						className="reCaptcha d-block w-100 my-3"
						sitekey={sitekey}
						onChange={handleOnChange}
					/>
				) : null} */}
				<div className="custom-control custom-checkbox">
					<input
						type="checkbox"
						className="custom-control-input"
						id="signin-remember-2"
						defaultChecked={true}
						defaultValue={true}
					/>
					<label
						className="custom-control-label"
						htmlFor="signin-remember-2"
					>
						{t("remember-me")}
					</label>
				</div>
				<div className="forgot-regis-btn ml-auto">
					<Link
						onClick={() => setOpen(true)}
						to="#"
						className="forgot-link text-danger"
					>
						{t("forgot-password")}
					</Link>
					<button
						className="d-none"
						onClick={() => setLogin && setLogin(!login)}
					>
						{!login ? t("login") : t("registration")}
					</button>
				</div>
			</div>

			<button
				type="submit"
				className="btn btn-outline-primary-2"
				// disabled={sitekey ? !isVerified : false}
			>
				<span>{t("enter")}</span>
				<i className="icon-long-arrow-right"></i>
			</button>
		</form>
	);
}

export default reduxForm({ form: "signinForm" })(AuthForm);
