import { compareProductTypes } from "../constants/action-types";
import { myReducerPersist } from "../store/managerLocalStorage";

const initialState = {
	productListCompare: [],
};

const comparisonProducts = (state = initialState, action) => {
	switch (action.type) {
		case compareProductTypes.ADD_TO_COMPARE:
			if (
				state.productListCompare.find(
					(p) => p.stock.id === action.newProduct.stock.id
				)
			) {
				return {
					productListCompare: [
						...state.productListCompare.filter(
							(p) => p.stock.id !== action.newProduct.stock.id
						),
					],
				};
			} else {
				return {
					productListCompare: [
						...state.productListCompare,
						action.newProduct,
					],
				};
			}
		case compareProductTypes.REMOVE_FROM_COMPARE:
			return {
				productListCompare: [
					...state.productListCompare.filter(
						(p) => p.stock.id !== action?.newProduct?.stock.id
					),
				],
			};
		case compareProductTypes.RESET_COMPARE:
			return {
				productListCompare: [],
			};
		default:
			return state;
	}
};

export default myReducerPersist(
	{
		key: "ox-comparisonProducts",
	},
	comparisonProducts
);
