import React, { Fragment } from "react";
import { useTranslation } from "react-i18next";
import { Field, reduxForm } from "redux-form";
import Loader from "../../../components/features/loaders/loader";

const otherField = props => {
	let {
		input,
		label,
		type,
		className,
		meta: { touched, error }
	} = props;

	if (error && touched) {
		className = className + " warning";
		// setLoadingPassword(false);
	}

	return (
		<Fragment>
			<input
				{...input}
				placeholder={label}
				className={className}
				type={type}
			/>
			{touched && error && <span>{error}</span>}
		</Fragment>
	);
};

function NewPassword({
	handleSubmit,
	openPass,
	// setLoadingPassword,
	loadingPassword
}) {
	const { t } = useTranslation();

	return (
		<div className={openPass ? "forget-password_overlay" : "d-none"}>
			<form className="change-password" onSubmit={handleSubmit}>
				<label htmlFor="code">{t("enter-code")}</label>
				<Field
					type="number"
					className="form-control"
					id="code"
					name="code"
					component={otherField}
					required
				/>
				<label htmlFor="pass1">{t("new-password")}</label>
				<Field
					type="password"
					className="form-control"
					id="pass1"
					name="pass1"
					component={otherField}
					required
				/>
				{/* <label htmlFor="pass2">{t("new-password-two")}</label>
				<Field
					type="password"
					className="form-control"
					id="pass2"
					name="pass2"
					component={otherField}
					required
				/> */}
				<button
					className="btn btn-outline-primary-2"
					disabled={loadingPassword}
				>
					{loadingPassword ? <Loader /> : "OK"}
				</button>
			</form>
		</div>
	);
}

export default reduxForm({ form: "newPassword" })(NewPassword);
