import React from "react";
import market from "../../../config/market";
import FooterZero from "./footer-zero";
import FooterOne from "./footer-one";
import FooterTwo from "./footer-two";
import FooterThree from "./footer-three";
import FooterFour from "./footer-four";
import FooterFive from "./footer-five";
import FooterSix from "./footer-six";
export default function Footer() {
	switch (market.config.footer) {
		case 0:
			return <FooterZero />;
		case 1:
			return <FooterOne />;
		case 2:
			return <FooterTwo />;
		case 3:
			return <FooterThree />;
		case 4:
			return <FooterFour />;
		case 5:
			return <FooterFive />;
		case 6:
			return <FooterSix />;
		default:
			return <FooterOne />;
	}
}
