import React, { useEffect } from "react";
import {
	refreshStore,
	setBanners,
	setBlogs,
	setCollections,
	setSlides,
	setSubdomain,
} from "../actions/any";
import { getMenuAction } from "../actions/menu";
import { setBlocks } from "../actions/blocks";
import { marketData } from "../actions/marketData";
// UTILS
import { initFunctions } from "./index";
import LoaderOverlay from "../components/features/loaders/LoaderOverlay";
import { useDispatch, useSelector } from "react-redux";
import {
	getMarketData,
	getMenus,
	getOtherData,
	getBlocks,
	isUpdate,
} from "../api/cache/main";
import cacheClear from "./cacheClear";
import updateCache from "./updateCache";
import market from "../config/market";
import { autoUpdateCache } from "./autoUpdateCache";
import { saveDictionary } from "../actions/dictionary";
import { getState } from "../store";

function parseTranslationFromServer(translations) {
	try {
		return JSON.parse(translations);
	} catch (error) {
		console.error("TRANSLATEION PARSE ERROR\n", error);
		return {};
	}
}

const InitialLoadData = ({ children }) => {
	const {
		any: { subdomain },
	} = getState();
	const hasMenu = useSelector((state) => state.menu?.menuList?.length);
	const dispatch = useDispatch();
	const dictionaryLoading = useSelector((state) => state.dictionary.loading);

	useEffect(() => {
		const fetch = async () => {
			initFunctions();
			cacheClear();
			updateCache();
			autoUpdateCache();

			const time = localStorage.getItem("clut");
			let isUpdated = { updated: true };

			if (time) {
				isUpdated = await isUpdate(
					new Date(parseInt(time)).toISOString()
				);
			}

			if (
				!hasMenu ||
				subdomain !== market.subdomain ||
				isUpdated.updated
			) {
				const data = await getMarketData();
				dispatch(marketData(data));

				const menus = await getMenus();
				dispatch(getMenuAction(menus));

				const blocks = await getBlocks();
				dispatch(setBlocks(blocks));

				const {
					news,
					collections,
					banners,
					slides,
				} = await getOtherData();
				dispatch(setBlogs(news));
				dispatch(setCollections(collections));
				dispatch(setBanners(banners));
				dispatch(setSlides(slides));

				data.settings.scripts &&
					document
						.querySelector("body")
						.insertAdjacentHTML("beforeend", data.settings.scripts);

				data.settings.styles &&
					document
						.querySelector("head")
						.insertAdjacentHTML("afterbegin", data.settings.styles);

				dispatch(
					saveDictionary(
						parseTranslationFromServer(
							data.settings.translation_msg
						)
					)
				);

				localStorage.setItem("clut", Date.now());
				dispatch(setSubdomain(market.subdomain));
			}

			if (getState().modal.current !== 3) dispatch(refreshStore(3));
		};
		fetch();
	}, []);

	if (!hasMenu || dictionaryLoading) {
		return <LoaderOverlay wrapper={true} />;
	}

	return <div>{children}</div>;
};

export default InitialLoadData;
