import React from "react";
import { getImageUrl } from "../../../utils/oxMethods";
import { useTranslation } from "react-i18next";
import market from "../../../config/market";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
export default function ToastyGlobal({ product, setMessage }) {
	const { t } = useTranslation();
	const { token } = useSelector((state) => state.user.tokenData);
	return (
		<div className="taosty">
			<div className="content">
				<div className="product-img">
					<img
						src={getImageUrl(
							product.data.variation.images[0],
							"50x_"
						)}
						alt={product.data.variation.name}
					/>
				</div>
				<div className="product-name">
					<h3>
						<b>{t("added_cart_successfuly")}</b>
					</h3>
					<h3>{product.data.variation.name}</h3>
				</div>
				<div className="close-with-pay">
					<button
						className="closer"
						onClick={() => setMessage({ product: {}, message: "" })}
					>
						<img src={market.config.icons.closer} alt="closer" />
					</button>
					<Link
						to={`${token ? "/checkout" : "/cart"}`}
						onClick={() => setMessage({ product: {}, message: "" })}
						className="pay-btn"
					>
						{t("pay")}
					</Link>
				</div>
			</div>
		</div>
	);
}
