import * as types from "../constants/action-types";
import { myReducerPersist } from "../store/managerLocalStorage";

const initialState = {
	current: 0,
	modal: "login",
	data: {},
	showModal: false,
	newsletterModal: false
};

function modalReducer(state = initialState, action) {
	switch (action.type) {
		case types.SHOW_MODAL:
			return {
				...state,
				showModal: true,
				data: action.data,
				modal: action.modal
			};

		case types.CLOSE_MODAL:
			return {
				...state,
				showModal: false,
				data: null,
				modal: action.modal
			};
		case types.modalTypes.CLEAR_MODAL_DATA:
			return {
				...state,
				data: null
			};

		case types.REMOVE_NEWSLETTER:
			return {
				...state,
				newsletterModal: false
			};

		case types.REFRESH_STORE:
			return { state: initialState, current: action.current };

		default:
			return state;
	}
}

export default myReducerPersist({ key: "ox-modal" }, modalReducer);
