import React from "react";
import { Link } from "react-router-dom";
import { dt } from "../../../../i18n";

export function LinkComponent({ menu, className }) {
	switch (menu.type) {
		case "static_page":
			return (
				<Link
					to={`/page/${menu.slug}`}
					className={className ? className : ""}
				>
					{dt(menu.name)}
				</Link>
			);
		case "link_page":
			return (
				<a
					href={menu.link}
					className={className ? className : ""}
					target={`${menu.newTab ? `_blank` : `_self`}`}
					rel="noreferrer"
				>
					{dt(menu.name)}
				</a>
			);
		case "collection_page":
			return (
				<Link
					to={`/category/${menu.slug}`}
					className={className ? className : ""}
				>
					{dt(menu.name)}
				</Link>
			);
		default:
			return (
				<Link to="#" className={className ? className : ""}>
					{dt(menu.name)}
				</Link>
			);
	}
}
