import React, { useEffect, useState } from "react";
import Modal from "react-modal";
import { useDispatch, useSelector } from "react-redux";
import { closeModal } from "../../../actions/modal";
import MapModalDetails from "./MapModalDetails";
import YandexMap from "../YandexMap";
import YandexMapLocations from "./YandexMapLocations";

const customStyles = {
	content: {
		top: "50%",
		transform: "translateY(-50%)",
		maxWidth: "100%",
		padding: "20px"
	},
	overlay: {
		backgroundColor: "rgba(77,77,77,0.6)",
		zIndex: "10003"
	}
};

function YandexMapModal({ selectedLocation }) {
	const showModal = useSelector(state => state.modal.showModal);
	const modal = useSelector(state => state.modal.modal);
	const [activePanel, setActivePanel] = useState(false);
	const [activeSearchPanel, setActiveSearchPanel] = useState(false);

	const { coordinate, address: addressUser, history } = useSelector(
		state => state.user.location
	);
	const dispatch = useDispatch();

	const [center, setCenter] = useState({
		center: [41.3112092101035, 69.27965188818546],
		zoom: 18
	});

	const [address, setAddress] = useState(
		addressUser
			? {
					...addressUser,
					name: addressUser.name
			  }
			: null
	);

	useEffect(() => {
		if (coordinate) {
			setCenter({
				center: [coordinate.latitude, coordinate.longitude],
				zoom: 18
			});
		}
	}, [coordinate]);

	function closeMapModal() {
		setTimeout(() => {
			dispatch(closeModal("map-modal"));
		}, 200);
	}

	return (
		<Modal
			isOpen={showModal && "map" === modal}
			onRequestClose={closeMapModal}
			style={customStyles}
			className="modal-dialog map-modal"
			id="map-modal"
		>
			<div className="modal-content">
				{selectedLocation ? (
					<YandexMapLocations
						supplierAdress={selectedLocation?.address}
					/>
				) : (
					<div className="modal-body">
						<div className="map_content">
							<button
								type="button"
								className="close closeMap"
								data-dismiss="modal"
								aria-label="Close"
								onClick={closeMapModal}
							>
								<span aria-hidden="true">
									<i className="icon-close"></i>
								</span>
							</button>

							<div className="location-wrapper">
								<img
									src="assets/images/markerSearch.svg"
									alt="location"
									width="25"
									height="25"
								/>
							</div>

							<MapModalDetails
								address={address}
								setAddress={setAddress}
								center={center}
								setCenter={setCenter}
								history={history}
								closeModal={closeModal}
								activePanel={activePanel}
								setActivePanel={setActivePanel}
								activeSearchPanel={activeSearchPanel}
								setActiveSearchPanel={setActiveSearchPanel}
							/>
							<YandexMap
								center={center}
								activePanel={activePanel}
								setActivePanel={setActivePanel}
								setCenter={setCenter}
								onChange={address => {
									setAddress(address);
								}}
							/>
						</div>
					</div>
				)}
			</div>
		</Modal>
	);
}

export default YandexMapModal;
