import { anyTypes } from "../constants/action-types";
import { myReducerPersist } from "../store/managerLocalStorage";

const initialState = {
	any: {},
	searchHistory: [],
	collections: {},
	banners: [],
	blogs: null,
	slides: [],
	categoryPath: null,
	subdomain: null,
	isFirstTime: true
};

const anyReducer = (state = initialState, action) => {
	switch (action.type) {
		case anyTypes.SET_FILTERS_DATA:
			return {
				...state,
				filtersData: action.filtersData
			};
		case anyTypes.IS_FIRST:
			return {
				...state,
				isFirstTime: false
			};

		case anyTypes.SUBDOMAIN:
			return {
				...state,
				subdomain: action.name
			};

		case anyTypes.SET_SEARCH_HISTORY:
			return {
				...state,
				searchHistory: action.searchHistory
			};

		case anyTypes.SET_PAGES:
			return {
				...state,
				pages: action.pages
			};
		case anyTypes.SET_COLLECTIONS:
			return {
				...state,
				collections: action.collections
			};

		case anyTypes.SET_BANNERS:
			return {
				...state,
				banners: action.banners
			};
		case anyTypes.SET_SLIDES:
			return {
				...state,
				slides: action.slides
			};
		case anyTypes.SET_BLOGS:
			return {
				...state,
				blogs: action.blogs.map(blog => {
					return { ...blog, hidden: false };
				})
			};
		case anyTypes.HIDDEN_BLOG:
			return {
				...state,
				blogs: state.blogs.map(blog => {
					return blog.id === action.id
						? { ...blog, hidden: true }
						: blog;
				})
			};
		case anyTypes.SET_CATEGORY_PATH:
			return {
				...state,
				categoryPath: action.path
			};
		default:
			return state;
	}
};

export default myReducerPersist({ key: "ox-any" }, anyReducer);
