import {
	setBanners,
	setBlogs,
	setCollections,
	setSlides,
} from "../actions/any";
import { getMenuAction } from "../actions/menu";
import { marketData } from "../actions/marketData";
import store from "../store";
import { getMarketData, getMenus, getOtherData } from "../api/cache/main";
const { dispatch } = store;
const CACHE_UPDATE_TIME_MINUTES = 30;

async function fetchAndUpdateCache() {
	const data = await getMarketData();
	dispatch(marketData(data));
	const menus = await getMenus();
	dispatch(getMenuAction(menus));
	const { news, collections, banners, slides } = await getOtherData();
	dispatch(setBlogs(news.items));
	dispatch(setCollections(collections));
	dispatch(setBanners(banners));
	dispatch(setSlides(slides));
}

export const autoUpdateCache = () => {
	setInterval(() => {
		let lastUpdateTimeCache = parseInt(localStorage.getItem("clut") || "0");
		if (
			lastUpdateTimeCache - Date.now() >
			1000 * 60 * CACHE_UPDATE_TIME_MINUTES
		)
			fetchAndUpdateCache();
	}, 1000 * 60 * 2);
};
