import mainCaller from "./mainCaller";
import { getTagName } from "../utils/oxMethods";
import QueryString from "qs";

export function getVariation(productId) {
	return mainCaller(`/${productId}/product`, "GET").then((variation) => {
		variation.variations.forEach((v) => {
			let po = {};
			v.properties = v.properties.forEach((prop) => {
				po[prop.name] = prop.value;
			});
			v.properties = po;
		});
		return variation;
	});
}

export function getProductPromotion(stockId) {
	return mainCaller(`/stocks/${stockId}/promotions`);
}

export async function getProductsPromotions(ids) {
	return mainCaller(
		`/products/promotion?${QueryString.stringify({ id: ids })}`
	);
}

export function getProductCategoryPath(productId) {
	return mainCaller(`/category-path/${productId}`, "GET");
}
export function getProductCategoryPaths(ids) {
	return mainCaller(`/category/path/by-ids?${ids}`, "GET");
}

export function getProductsPage(menuId, query) {
	return mainCaller(`/${menuId}/products?${query}`, "GET");
}

export function getSaleProducts(menuId) {
	return mainCaller(`/${menuId}/products?size=${5}&page=${1}`, "GET").then(
		(page) => page.variations
	);
}

export function getSaleCollections(id) {
	return mainCaller(`/${id}/collection-products`);
}

export const searchApi = async function (q, query, menuId) {
	return await mainCaller(
		`/search?q=${q}&${query}${menuId ? `&menuItem=${menuId}` : ""}`,
		"GET"
	).then((res) => res);
};

export const searchByMenu = async function (name, query) {
	return await mainCaller(
		`/menu-items?name=${name}&${query}&active=true`,
		"GET"
	).then((res) => res);
};

export const searchByTags = async function (tags) {
	return await mainCaller(`/search?${tags}`, "GET").then((res) => res);
};

export const getRelatedProducts = async function (product, query) {
	return await searchApi(getTagName(product), query);
};
