const otherField = props => {
	let {
		input,
		placeholder,
		type,
		className,
		minLength,
		title,
		pattern,

		id,
		meta: { touched, error }
	} = props;

	if (touched) {
		if (error) {
			className = className + " warning";
		}
	}

	return (
		<Fragment>
			<input
				{...input}
				placeholder={placeholder}
				className={className}
				id={id}
				minLength={minLength}
				type={type}
				pattern={pattern}
				title={title}
			/>
			{touched && error && <span>{error}</span>}
		</Fragment>
	);
};

