import React from "react";
import { Link } from "react-router-dom";
import { connect, useSelector } from "react-redux";
import HorizontalMenuThree from "../Menus/Desktop/HorizontalMenuThree";
import CartMenuTwo from "../partials/cart-menu-two";
import SearchForm from "../partials/SearchForm";
import LoginModal from "../../features/modal/login-modal";
import YandexMap from "../../features/modal/YandexMapModal";
import { openModal } from "../../../actions/modal";
import getLocation from "../../features/getAddress";
import ChangeLanguage from "../../features/ChangeLanguage";
import Logo from "../Logo";
import { useTranslation } from "react-i18next";

function Header(props) {
	const {
		openModal,
		user,
		mainLogo,
		phoneNumbers,
		location: { address }
	} = props;

	const settings = useSelector(state => state.marketData.settings);
	const { productList } = useSelector(state => state.choosenProducts);
	const { t } = useTranslation();
	function openLoginModal(e) {
		openModal("login");
		e.preventDefault();
	}

	function openMapModal(e) {
		openModal("map");
		e.preventDefault();
	}

	if (!address) {
		getLocation();
	}

	return (
		<header className="header-three">
			<div className="header-top">
				<div className="container">
					<div className="header-left">
						{phoneNumbers?.slice(0, 2).map((number, i) => (
							<a key={i} href={`tel:${number}`}>
								<p
									style={{
										marginBottom: "0px",
										marginRight: "10px"
									}}
								>
									<i className="i-icon icon-phone"></i>{" "}
									{number}
								</p>
							</a>
						))}
						<div className="ml-5 social-icons social-icons-color">
							<a
								href={settings?.socialLinks?.facebook}
								className="social-icon social-facebook"
								rel="noopener noreferrer"
								title="Facebook"
								target="_blank"
							>
								<i className="icon-facebook-f" />
							</a>
							<a
								href={settings?.socialLinks?.instagram}
								className="social-icon social-instagram"
								rel="noopener noreferrer"
								title="Instagram"
								target="_blank"
							>
								<i className="icon-instagram" />
							</a>
							<a
								href={settings?.socialLinks?.telegram}
								className="social-icon social-telegram"
								rel="noopener noreferrer"
								title="Telegram"
								target="_blank"
							>
								<i className="icon-telegram" />
							</a>

							<a
								href={settings?.socialLinks?.odnoklassniki}
								className="social-icon social-youtube"
								rel="noopener noreferrer"
								title="Youtube"
								target="_blank"
							>
								<i className="icon-youtube" />
							</a>
						</div>
					</div>
					<div className="header-right">
						<ChangeLanguage />
						<div
							className="location"
							style={{ padding: "10px 20px 10px" }}
						>
							<Link
								className="location-link"
								to={`${process.env.PUBLIC_URL}`}
								style={{ color: "black" }}
							>
								{" "}
								<p
									className="d-flex justify-content-between align-items-center"
									onClick={openMapModal}
									style={{ marginBottom: "0px" }}
								>
									<i className="i-icon icon-map-marker"></i>
									{address ? (
										<span>
											{address.administrativeArea},{" "}
											{address.name}
										</span>
									) : (
										<span>{t("address-not-selected")}</span>
									)}
								</p>
							</Link>
						</div>
					</div>
				</div>
			</div>
			<div className="header-middle sticky-header">
				<div className="container">
					<div className="header-left">
						<Logo
							alt={"logo"}
							logo={mainLogo}
							width={"250px"}
							height={"65px"}
						/>
					</div>
					<div className="header-center">
						<SearchForm />
					</div>
					<div className="header-right">
						<Link
							to="/choosen-products-list"
							className="choosen-products-icon"
							style={{
								fontSize: "24px"
							}}
						>
							<i className="icon icon-heart"></i>
							{productList.length ? (
								<span>{productList.length}</span>
							) : null}
						</Link>
						<div className="dropdown cart-dropdown">
							{user.id ? (
								<Link className="profile-link" to="/profile">
									<i
										style={{
											fontSize: "25px",
											color: "black"
										}}
										className="icon-user"
									></i>
								</Link>
							) : (
								<Link
									className="profile-link"
									to="#signin-modal"
									onClick={openLoginModal}
								>
									<i
										style={{
											fontSize: "25px",
											color: "black"
										}}
										className="icon-user"
									></i>
								</Link>
							)}
						</div>
						<CartMenuTwo />
					</div>
				</div>
			</div>
			<div className="sticky-wrapper">
				<div className="header-bottom sticky-header">
					<div className="container">
						<div className="header-left">
							<Link
								to="/category"
								className="mobile-menu-toggler"
							>
								<span className="sr-only"></span>
								<i className="icon-bars" />
							</Link>
							<HorizontalMenuThree />
						</div>
						<div className="row search-mobile">
							<SearchForm />
						</div>
					</div>
				</div>
			</div>
			<LoginModal />
			<YandexMap />
		</header>
	);
}

function mapStateToProps(state) {
	return {
		mainLogo: state.marketData.settings.mainLogo,
		phoneNumbers: state.marketData.settings.phoneNumbers,
		user: state.user.user,
		location: state.user.location
	};
}

export default connect(mapStateToProps, { openModal })(Header);
