import React, { useEffect } from "react";

let ws = ["Word1", "Word2"];

function startDynamicPH(id, props) {
	if (window.inter) return;

	let changeLettersInterval = props.changeLettersInterval || 80;
	let words = (props.words || ws).map((w) => w + "  ");
	let input = document.getElementById(id);
	let word = 0;
	let index = 0;
	let k = 1;
	window.inter = setInterval(() => {
		let ph = words[word].slice(0, index);
		if (words[word].length === index) k = 0;
		if (ph.length === 0) k = 1;

		if (k) {
			index++;
		} else {
			index = index - 1;
			if (index === 0) {
				word = word + 1;
				if (word === words.length) {
					word = 0;
				}
			}
		}

		input.placeholder = ph;
	}, changeLettersInterval);
}

export default function DynamicPlaceholder(props) {
	let id = props.id || "searchInput";

	function onStop(id) {
		clearInterval(window.inter);
		document.getElementById(id).placeholder = "";
		window.inter = undefined;
	}

	useEffect(() => {
		startDynamicPH(id, props);
		return () => {
			onStop(id);
		};
	}, []);
	return (
		<input
			id={id}
			{...props}
			onFocus={(e) => {
				onStop(id);
				if (props.onFocus) {
					props.onFocus(e);
				}
			}}
			onBlur={(e) => {
				if (!e.target.value) {
					startDynamicPH(id, props);
				}
				if (props.onBlur) {
					props.onBlur(e);
				}
			}}
		/>
	);
}
