export function yandex_data(res) {
	const dataMap = res.data.response;
	const geoObject = dataMap.GeoObjectCollection.featureMember[0].GeoObject;
	const data = {
		addressLine1:
			geoObject.metaDataProperty.GeocoderMetaData.AddressDetails.Country
				.addressLine1 || "-",
		countryCode:
			dataMap.GeoObjectCollection.featureMember[0].GeoObject
				.metaDataProperty.GeocoderMetaData.Address.country_code || "-",
		region:
			geoObject.metaDataProperty.GeocoderMetaData.Address.Components.find(
				f => f.kind === ("district" || "provines" || "locality")
			)?.name || "-",
		dependentLocality:
			geoObject.metaDataProperty.GeocoderMetaData.Address.Components.find(
				f => f.kind === "locality"
			)?.name || "-",

		name: geoObject.name || "-",
		administrativeArea:
			geoObject.metaDataProperty.GeocoderMetaData.AddressDetails.Country
				.AdministrativeArea.AdministrativeAreaName || "-",
		country:
			geoObject.metaDataProperty.GeocoderMetaData.Address.Components.find(
				f => f.kind === "country"
			) || "-",
		latitude: geoObject.Point.pos.split(" ")[1] || "-",
		longitude: geoObject.Point.pos.split(" ")[0] || "-"
	};
	return data;
}

export function nominatium_data(res) {
	const dataMap = res.data;
	const data = {
		addressLine1: dataMap.display_name || "-",
		countryCode: dataMap.address.country_code.toUpperCase() || "-",
		region: dataMap.address.county || "-",
		dependentLocality: dataMap.address.country || "-",
		name: dataMap.display_name || "-",
		administrativeArea: dataMap.address.road || "-",
		country: dataMap.address.country || "-",
		postalCode: dataMap.address.postcode || "-",
		latitude: dataMap.lat || "-",
		longitude: dataMap.lon || "-"
	};

	return data;
}

export function yandex_data_byaddress(res) {
	var result = [];
	res.data.response.GeoObjectCollection.featureMember.forEach(element => {
		const data = {
			coordinate: {
				latitude: element.GeoObject.Point.pos.split(" ")[1] || "-",
				longitude: element.GeoObject.Point.pos.split(" ")[0] || "-"
			},
			address: {
				countryCode: element.GeoObject.metaDataProperty.GeocoderMetaData.Address.country_code.toUpperCase(),
				addressLine1: `${element.GeoObject.description}, ${element.GeoObject.name}`
			}
		};

		result.push(data);
	});
	return result;
}
export function nominatium_data_byaddress(res) {
	var result = [];
	res.data.forEach(element => {
		const data = {
			coordinate: {
				latitude: element.lat,
				longitude: element.lon
			},
			address: {
				countryCode: element.address.country_code.toUpperCase(),
				addressLine1: element.display_name
			}
		};
		result.push(data);
	});
	return result;
}
