import React, { useEffect, useState } from "react";
import { Link, withRouter } from "react-router-dom";
import { debounce } from "../../utils/debounce";
import { main, menu, profile, cart, choosen } from "./MobileBottomNavbarData";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { getCartCount } from "../../services";
import market from "../../config/market";

export function checkPathAndActivateMenu(path, setActive) {
	if (path === "/") {
		setActive(1);
	} else if (path === "/category") {
		setActive(2);
	} else if (path === "/choosen-products-list") {
		setActive(3);
	} else if (path === "/checkout" || path === "/cart") {
		setActive(4);
	} else if (path === "/profile" || path === "/login") {
		setActive(5);
	} else {
		setActive(0);
	}
}

const MobileBottomNavbar = ({ home, location, history }) => {
	const cartlist = useSelector((state) => state.cartlist.cart);
	const { productList } = useSelector((state) => state.choosenProducts);
	const { token } = useSelector((state) => state.user.tokenData);
	const { t } = useTranslation();
	const [scrollY, setScrollY] = useState(0);
	const [active, setActive] = useState(1);

	const [styleNav, setStyleNav] = useState({
		transform: "translateY(0)",
	});

	useEffect(() => {
		const body = document.querySelector("body");
		const path = location.pathname;
		checkPathAndActivateMenu(path, setActive);
		if (path === "/category") {
			body.classList.add("category-active");
		}
		return () => {
			body.classList.remove("category-active");
		};
	}, [location.pathname]);

	const scrollToBottom =
		window.innerHeight + window.scrollY - document.body.scrollHeight;

	if (home === 3) {
		window.addEventListener("scroll", () => {
			debounce(
				() => {
					if (window.scrollY > scrollY) {
						setStyleNav({
							transform: "translateY(85px)",
						});
						setScrollY(window.scrollY - 1);
					} else {
						setStyleNav({
							transform: "translateY(0)",
						});
						setScrollY(window.scrollY + 1);
					}
					if (-scrollToBottom < 30) {
						setStyleNav({
							transform: "translateY(0)",
						});
					}
				},
				"scrollWindow",
				10
			);
		});
	}

	return (
		<div
			className="mobile_btm_navbar_box"
			style={{ ...styleNav, transition: "0.3s", zIndex: "9999" }}
		>
			<ul>
				<Link to={main.path}>
					<li
						key={main.key}
						onClick={() => setActive(1)}
						className={active === 1 ? `active` : ``}
					>
						<img
							alt="icon"
							src={
								active === 1
									? market.config.icons.homeActive
									: market.config.icons.home
							}
						/>
						<span>{t(main.key)}</span>
					</li>
				</Link>
				<Link to="/category">
					<li
						key={menu.key}
						onClick={() => setActive(2)}
						className={active === 2 ? `active` : ``}
					>
						<img
							alt="icon"
							src={
								active === 2
									? market.config.icons.categoryActive
									: market.config.icons.category
							}
						/>
						<span>{t(menu.key)}</span>
					</li>
				</Link>
				<Link to={choosen.path}>
					<li
						key={choosen.key}
						onClick={() => setActive(3)}
						className={active === 3 ? `active` : ``}
					>
						<div className="choosen-product-menu">
							<img
								alt="icon"
								src={
									active === 3
										? market.config.icons.favouritesActive
										: market.config.icons.favourites
								}
							/>
							<span className="choosen-products-count">
								{productList.length}
							</span>
						</div>
						<span>{t(choosen.key)}</span>
					</li>
				</Link>
				<Link to={token ? "/checkout" : cart.path}>
					<li
						key={cart.key}
						onClick={() => setActive(4)}
						className={active === 4 ? `active` : ``}
					>
						<div className="choosen-product-menu">
							<img
								alt="icon"
								src={
									active === 4
										? market.config.icons.cartActive
										: market.config.icons.cart
								}
							/>
							<span className="choosen-products-count">
								{getCartCount(cartlist)}
							</span>
						</div>
						<span>{t(cart.key)}</span>
					</li>
				</Link>
				<Link
					to={{
						pathname: profile.path,
						state: {
							prevPathname: history.location.pathname,
						},
					}}
				>
					<li
						key={profile.key}
						onClick={() => setActive(5)}
						className={active === 5 ? `active` : ``}
					>
						<img
							alt="icon"
							src={
								active === 5
									? market.config.icons.userActive
									: market.config.icons.user
							}
						/>
						<span>{t(profile.key)}</span>
					</li>
				</Link>
			</ul>
		</div>
	);
};

export default withRouter(MobileBottomNavbar);
