import axios from "axios";
import { isDevServer } from "../utils/project";

const { security_key, subdomain, url } = require("../config/market");

const getMainUrl = () => {
	if (isDevServer()) {
		return `http://wooz.ox.local`;
	}

	// enterprice company
	if (url) {
		return url;
	}

	return `https://${subdomain}.ox-sys.com`;
};

function getSecurityKey() {
	if (isDevServer()) {
		return "hDR574PEFaXtXsHHQPg1_oYWnLdsIFDCO5ElcrJSRKU=";
	}

	return security_key;
}

export const requestAbort = {
	controllers: {},
	clearControllerTimeout: 1000 * 5,
	setController(path, controller) {
		this.controllers[path] = controller;

		setTimeout(() => {
			delete this.controllers[path];
		}, this.clearControllerTimeout);
	},
	getController(requestPath) {
		return this.controllers[requestPath];
	},
	abort(requestPath) {
		const controller = this.getController(requestPath);

		if (controller) {
			controller.cancel();
		} else {
			console.error(
				new Error("No controller with this path: " + requestPath)
			);
		}
	},
};

async function mainCaller(path, method, data, headers = {}) {
	const controller = axios.CancelToken.source();

	const options = {
		url: `${getMainUrl()}/market-api${path}`,
		headers: {
			"Content-type": "application/json",
			"security-key": getSecurityKey(),
			...headers,
		},
		cancelToken: controller.token,
		method,
		withCredentials: true,
	};

	requestAbort.setController(path, controller);

	if (data) {
		options.data = data;
	}

	const res = await axios(options);
	return res.data;
}

export async function oxAuthUser(password, username) {
	const { data } = await axios.post(
		getMainUrl() + "/security/auth_check",
		`_username=${username}&_password=${password}&_subdomain=${subdomain}`,
		{
			headers: {
				"content-type": "application/x-www-form-urlencoded",
			},
		}
	);
	return data;
}

export async function getUserOx(token) {
	const data = await mainCaller("/profile", "GET", null, {
		Authorization: `Bearer ${token}`,
		"security-key": security_key,
	});
	return data;
}

export const driverApi = async (id, token) => {
	const options = {
		url: `https://oxdriver.ox-sys.com/api/deliveries/tracking/${id}`,
		headers: {
			"X-AUTH-TOKEN": token,
		},
		method: "GET",
	};

	const { data } = await axios(options);
	return data;
};

export const distanceApi = async (id, token) => {
	const options = {
		url: `https://oxdriver.ox-sys.com/api/deliveries/until-arrive-distance/${id}`,
		headers: {
			"X-AUTH-TOKEN": token,
		},
		method: "GET",
	};

	const { data } = await axios(options);
	return data;
};

export default mainCaller;
