import React, { useState } from "react";
import SearchContext from "./SearchContext";
import SearchForm from "./SearchForm";
import SearchResult from "./SearchResult";
import SearchHistory from "./SearchHistory";
import { debounce } from "../../../../utils/debounce";
import { useDispatch, useSelector } from "react-redux";
import { setSearchHistory } from "../../../../actions/any";
import CategoriesModal from "./CategoriesModal";
import { useHistory } from "react-router";

export default ({ inputId = "searchInput" }) => {
	const [hidden, setHidden] = useState(false);
	const [result, setResult] = useState({ loading: false, data: [] });
	const [query, setQuery] = useState("");
	const [value, setValue] = useState();
	const [category, setCategory] = useState({ open: false });
	const dispatch = useDispatch();
	const searchHistory = useSelector((state) => state.any.searchHistory);
	const h = useHistory();

	function searchFn(value) {
		debounce(
			() => {
				setQuery(value);
				setResult({
					loading: false,
					data: [],
					menu: [],
				});
			},
			"inputQuery",
			500
		);
	}

	function renderSearchPage(e) {
		e.preventDefault();
		if (result.data && result.data.length > 0) {
			let nh = searchHistory.filter((q) => q !== query);
			setHidden(true);
			nh.unshift(query);
			dispatch(setSearchHistory(nh));
			h.push(`/search/${query}`);
		}
	}

	return (
		<SearchContext.Provider
			value={{
				hidden: hidden,
				setHidden: setHidden,
				result: result,
				setResult: setResult,
				query: query,
				setQuery: setQuery,
				value: value,
				inputId: inputId,
				category: category,
				setCategory: setCategory,
				setValue: setValue,
				searchFn: searchFn,
				renderSearchPage: renderSearchPage,
			}}
		>
			<div className="d-flex flex-column div search-wrapper">
				<div className="search-box">
					<CategoriesModal />
					<SearchForm />
					<div className="search-result-wrapper">
						<SearchResult />
						<SearchHistory />
					</div>
				</div>
			</div>
		</SearchContext.Provider>
	);
};
