import axios from "axios";
import { getState } from "../store";
import {
	nominatium_data,
	nominatium_data_byaddress,
	yandex_data,
	yandex_data_byaddress
} from "./data-controller";

const mapApis = {
	yandex: {
		async getCoordinatesByAddress(address, token) {
			return await axios
				.get(
					`https://geocode-maps.yandex.ru/1.x/?apikey=${token}&format=json&lang=ru&geocode=${address}`
				)
				.then(yandex_data_byaddress);
		},
		async getAddressByCoordinate({ latitude, longitude }, token) {
			return await axios
				.get(
					`https://geocode-maps.yandex.ru/1.x/?apikey=${token}&format=json&lang=ru&geocode=${longitude},${latitude}`
				)
				.then(yandex_data);
		}
	},
	mapbox: {
		async getCoordinatesByAddress(address) {
			return await axios
				.get(
					`https://nominatim.openstreetmap.org/search?q=${address}&format=json&polygon_geojson=1&addressdetails=1`
				)
				.then(nominatium_data_byaddress);
		},
		async getAddressByCoordinate({ latitude, longitude }) {
			return await axios
				.get(
					`https://nominatim.openstreetmap.org/reverse?lat=${latitude}&lon=${longitude}&format=json`
				)
				.then(nominatium_data);
		}
	}
};

export async function getCoordinatesByAddress(address) {
	try {
		const { type, token } = getState().marketData.companyOption.map;

		return await mapApis[type].getCoordinatesByAddress(address, token);
	} catch (error) {
		console.error(error);
		return {
			coordinate: {
				latitude: 0,
				longitude: 0
			},
			address: {
				countryCode: "",
				addressLine1: ""
			}
		};
	}
}

export async function getAddressByCoordinate(latitude, longitude) {
	try {
		const { type, token } = getState().marketData.companyOption.map;
		const data = await mapApis[type].getAddressByCoordinate(
			{ latitude, longitude },
			token
		);

		return {
			addressLine1: data.addressLine1,
			countryCode: data.countryCode,
			region: data.region,
			postalCode: data.postalCode,
			name: data.name,
			dependentLocality: data.dependentLocality,
			administrativeArea: data.administrativeArea,
			country: data.country,
			latitude: data.latitude,
			longitude: data.longitude
		};
	} catch (error) {
		console.error(error);
		return {
			addressLine1: "Error",
			countryCode: "",
			region: "",
			postalCode: "",
			dependentLocality: "",
			name: "",
			administrativeArea: "",
			country: "",
			latitude: 10,
			longitude: 10
		};
	}
}
