import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { dt } from "../../../../i18n";
import { MenuChildList } from "../../../../views/home/components/HomeMenu";

function HeaderMenu({ setMenuCatalog }) {
	const { t } = useTranslation();
	const { menuVertical } = useSelector(state => state.menu);
	const [menuChild, setMenuChild] = useState();
	const [active, setActive] = useState(0);
	const [allMenuActive, setAllMenuActive] = useState(false);

	function click(e, menu) {
		e.stopPropagation();
		setAllMenuActive(false);
		if (menu.children.length > 0) {
			setActive(menu.id);
			setMenuChild(menu.children);
		} else setMenuCatalog(false);
	}

	return (
		<div
			className="catalog-menu"
			onClick={e =>
				e.target.className === "catalog-menu"
					? setMenuCatalog(false)
					: ""
			}
		>
			<div className={`catalog-menu-body ${menuChild ? "active" : ""}`}>
				<div className="catalog-header">
					<h3>{t("category")}</h3>
					<i
						className="icon-close"
						onClick={() => setMenuCatalog(false)}
					></i>
				</div>
				<div className="container">
					<ul className="col-3 p-0">
						{menuVertical.map(menu => (
							<li
								key={menu.id}
								className={`menu-item ${
									active === menu.id ? "active" : ""
								}`}
							>
								<Link
									className="menu-link"
									to={
										menu.children.length > 0
											? "#"
											: `/category/${menu.slug}`
									}
									onClick={e => click(e, menu)}
								>
									<span>{dt(menu.name)}</span>
									{menu.children.length > 0 ? (
										<i className="icon-angle-right"></i>
									) : null}
								</Link>
							</li>
						))}
						<li className={`menu-item`}>
							<Link
								className="menu-link"
								onClick={() => {
									setMenuCatalog && setMenuCatalog(false);
									allMenuActive && allMenuActive(false);
								}}
								to={`/all-categories`}
							>
								<span>{t("all_categories")}</span>

								<i className="icon-angle-right"></i>
							</Link>
						</li>
					</ul>
					{menuChild ? (
						<div className="col-9">
							<MenuChildList
								setMenuCatalog={setMenuCatalog}
								menuChild={menuChild}
								seeMore={true}
								setMenuAllActive={setAllMenuActive}
								menuAllActive={allMenuActive}
							/>
						</div>
					) : null}
				</div>
			</div>
		</div>
	);
}

export default HeaderMenu;
