import React from "react";
import { useRive } from "rive-react";
import market from "../../../config/market";

const RiveLoader = () => {
	const riveParams = {
		src: process.env.PUBLIC_URL + market.config.loader,
		artboard: "New Artboard",
		autoplay: true
	};
	const { RiveComponent } = useRive(riveParams);

	return (
		<div
			style={{
				width: "100%",
				height: "100%",
				margin: "50px auto 0"
			}}
		>
			<RiveComponent style={{ height: "500px" }} />
		</div>
	);
};

export default RiveLoader;
