import React from "react";
import { connect, useDispatch } from "react-redux";
import Modal from "react-modal";
import Login from "../../../views/login/Login";
import { closeModal } from "../../../actions/modal";

const customStyles = {
	content: {
		top: "50%",
		transform: "translateY(-50%)"
	},
	overlay: {
		backgroundColor: "rgba(77,77,77,0.6)",
		zIndex: "10000"
	}
};

Modal.setAppElement("#root");

function LoginModal(props) {
	const { modal, showModal, modalData } = props;
	const dispatch = useDispatch();

	function closeModalLogin() {
		dispatch(closeModal("login"));
	}
	return (
		<Modal
			isOpen={showModal && (modal === "login" || modal === "reg")}
			onRequestClose={closeModalLogin}
			style={customStyles}
			contentLabel="Login Modal"
			className="modal-dialog modal-dialog-centered"
			id="login-modal"
		>
			<div
				className="modal-content"
				style={{ boxShadow: "none", zIndex: "9999" }}
			>
				<div className="modal-body">
					<Login modal={modal} modalData={modalData} />
				</div>
			</div>
		</Modal>
	);
}

function mapStateToProps(state) {
	return {
		showModal: state.modal.showModal,
		modal: state.modal.modal,
		modalData: state.modal.data
	};
}

export default connect(mapStateToProps, { closeModal })(LoginModal);
