import { debounce } from "./debounce";
import { updateCacheApi } from "../api/cache/main";
import { toast } from "react-toastify";

function update() {
	toast.success("Start updating cache");
	updateCacheApi()
		.then(r => {
			toast.success("Cache succsessfully updated");
			localStorage.clear();
			setTimeout(() => {
				window.location.reload();
			}, 1500);
		})
		.catch(err => {
			toast.success("Error updating cache");
		});
}

function updateCache() {
	let i = 0;
	window.addEventListener("keypress", e => {
		if (e.key === "1" && e.ctrlKey) debounce(() => update(), "updatacache");
		if (e.key === "u") {
			i++;
			debounce(
				() => {
					if (i === 3) {
						update();
					} else {
						i = 0;
					}
				},
				"updatecashe",
				300
			);
		}
	});
}

export default updateCache;
