import React from "react";
import { useSelector } from "react-redux";
import { YMInitializer } from "react-yandex-metrika";

export default function YandexMetrika() {
	const { settings } = useSelector(state => state.marketData);

	return settings &&
		settings.yandexMetric &&
		window.location.hostname !== "localhost" ? (
		<YMInitializer
			accounts={[settings.yandexMetric]}
			options={{
				clickmap: true,
				trackLinks: true,
				accurateTrackBounce: true,
				webvisor: true,
				trackHash: true
			}}
			version="2"
		/>
	) : (
		<span></span>
	);
}
