import React from "react";
import market from "../../../config/market";
import HeaderZero from "./header-zero";
import HeaderOne from "./header";
import HeaderTwo from "./header-two";
import HeaderThree from "./header-three";
import HeaderFour from "./header-four";
import HeaderFive from "./header-five";
import HeaderSix from "./header-six";
import HeaderSeven from "./header-seven";
import HeaderEight from "./header-eight";

export default function Header() {
	switch (market.config.header) {
		case 0:
			return <HeaderZero />;
		case 1:
			return <HeaderOne />;
		case 2:
			return <HeaderTwo />;
		case 3:
			return <HeaderThree />;
		case 4:
			return <HeaderFour />;
		case 5:
			return <HeaderFive />;
		case 6:
			return <HeaderSix />;
		case 7:
			return <HeaderSeven />;
		case 8:
			return <HeaderEight />;
		default:
			return <HeaderOne />;
	}
}
