import React, { useEffect, useState, Fragment } from "react";
import { Link } from "react-router-dom";
import { divideArray } from "../../../../utils/array";
import { getImageUrl } from "../../../../utils/oxMethods";
import { LazyLoadImage } from "react-lazy-load-image-component";
import { useSelector } from "react-redux";
import { dt } from "../../../../i18n";

const megaMenuChildren = (path, children) => {
	if (children.length > 1 && children.length < 6) {
		return (
			<div className="row">
				{divideArray(children)
					.sort((a, b) => a.sort - b.sort)
					.map((columnChildren, i) => {
						return (
							<div key={i} className="d-flex col menu-two">
								{columnChildren.map(menu => {
									return (
										<Fragment key={menu.id}>
											<div className="menu-title">
												<Link
													to={`/category/${menu.slug}`}
												>
													{dt(menu.name)}
												</Link>
												{menuList(menu.children)}
											</div>
										</Fragment>
									);
								})}
							</div>
						);
					})}
			</div>
		);
	}
	return menuList(children);
};

function menuList(children) {
	return (
		<ul>
			{children
				.sort((a, b) => a.sort - b.sort)
				.map(menu => {
					return (
						<li key={menu.id}>
							<Link
								to={`/category/${menu.slug}`}
								style={{
									display: "flex",
									alignItems: "center"
								}}
							>
								{menu.image ? (
									<img
										className="sub-menu-img"
										src={getImageUrl(menu.image, "150x_")}
										alt=""
									/>
								) : null}
								<span className="sub-menu">
									{dt(menu.name)}
								</span>
							</Link>
						</li>
					);
				})}
		</ul>
	);
}

const megaMenuBody = (path, menu) => {
	return (
		<div
			className="megamenu megamenu-md scrollbar force-overflow-mgmenu"
			id="style-5"
		>
			<div className="row no-gutters">
				<div className="col-md-auto">
					<div className="menu-col">
						{megaMenuChildren(path, menu.children)}
					</div>
				</div>

				<div className="col-md-auto">
					{menu.backgroundImage ? (
						<div className="banner banner-overlay">
							<Link
								to={`/category/${menu.slug}`}
								className="banner banner-menu"
							>
								<LazyLoadImage
									src={getImageUrl(menu.backgroundImage)}
									effect="blur"
									alt={menu.slug}
								/>
							</Link>
						</div>
					) : null}
				</div>
			</div>
		</div>
	);
};

const mainHorizontalMenuOne = (path, menu) => (
	<li key={menu.id} className={path.indexOf(menu.slug) > -1 ? "active" : ""}>
		<Link
			to={`/category/${menu.slug}`}
			className={menu.children.length > 0 ? "sf-with-ul" : "sf"}
		>
			{dt(menu.name)}
		</Link>
		{menu.children.length > 0 ? megaMenuBody(path, menu) : null}
	</li>
);

function HorizontalMenuTwo(props) {
	const allMenuData = useSelector(state => state.menu);
	const [path, setPath] = useState("");
	useEffect(() => {
		setPath(window.location.href);
	});

	return (
		<nav className="main-nav">
			<ul className="menu sf-arrows">
				{allMenuData.menuHorizontal
					.sort((a, b) => a.sort - b.sort)
					.map(menu => mainHorizontalMenuOne(path, menu))}
			</ul>
		</nav>
	);
}

export default HorizontalMenuTwo;
