import React, { useState } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import HorizontalMenuTwo from "../Menus/Desktop/HorizontalMenuTwo";
import CartMenuTwo from "../partials/cart-menu-two";
import Logo from "../Logo";
import SearchForm from "../partials/SearchForm";
import YandexMap from "../../features/modal/YandexMapModal";
import { openModal } from "../../../actions/modal";
import getLocation from "../../features/getAddress";
import ChangeLanguage from "../../features/ChangeLanguage";
import { useTranslation } from "react-i18next";

function Header({
	container = "container",
	openModal,
	user,
	logo,
	phoneNumber,
	location: { address }
}) {
	const { t } = useTranslation();

	function openMapModal(e) {
		openModal("map");
		e.preventDefault();
	}

	if (!address) {
		getLocation();
	}

	const [search, setSearch] = useState(false);

	return (
		<header className="header header-4 osto-header">
			<div className="header-top">
				<div
					className={container}
					style={{ padding: "8px 5px", color: "#4e4e4e" }}
				>
					<div className="header-left mr-auto">
						<a href={`tel:${phoneNumber}`}>
							<p style={{ marginBottom: "0px" }}>
								<i className="i-icon icon-phone"></i>
								{phoneNumber}
							</p>
						</a>
					</div>
					<div className="header-right">
						<ChangeLanguage />
						<Link
							className="location-link"
							to={process.env.PUBLIC_URL}
							style={{ color: "black" }}
						>
							<p
								className="d-flex justify-content-between align-items-center"
								onClick={openMapModal}
								style={{ marginBottom: "0px" }}
							>
								<img
									className="mr-1"
									src="./assets/images/osto-icons/location-icon.png"
									alt="location img"
									width="25"
									height="22"
								/>
								{address
									? `${address.administrativeArea}, ${address.name}`
									: t("address-not-selected")}
							</p>
						</Link>
					</div>
				</div>
			</div>
			<div className="wrap-container sticky-header">
				<div className="header-middle d-flex align-items-center">
					<div className={container}>
						<div className="header-left">
							<button className="mobile-menu-toggler">
								<span className="sr-only">
									Toggle mobile menu
								</span>
								<i className="icon-bars"></i>
							</button>
							<Logo logo={logo} />
						</div>

						{search ? (
							<>
								<div
									className="header-center mr-2 ml-2"
									style={{ flexGrow: "1" }}
								>
									<SearchForm
										header={2}
										setSearch={setSearch}
									/>
								</div>
							</>
						) : (
							<>
								<div className="header-center ml-0 mr-0">
									<HorizontalMenuTwo />
								</div>
								<div
									className="header-search-img header-center ml-auto mr-2"
									style={{ width: "25px", height: "25px" }}
								>
									<img
										style={{
											cursor: "pointer",
											width: "100%",
											display: "block",
											objectFit: "cover"
										}}
										onClick={() => setSearch(true)}
										className="icon-search"
										src="./assets/images/osto-icons/search-icon.png"
										alt="search icon"
										width="25"
										height="25"
									/>
								</div>
							</>
						)}

						<div className="header-right d-flex justify-content-between mr-5">
							<div className="top-menu top-link-menu header-text mr-3">
								{user.id ? (
									<Link
										className="profile-link"
										to="/profile"
										style={{
											display: "block",
											width: "25px",
											height: "25px"
										}}
									>
										<img
											style={{
												width: "100%",
												display: "block",
												objectFit: "cover"
											}}
											className="user-circle"
											src="./assets/images/osto-icons/user.png"
											alt="user profile"
											width="25"
											height="25"
										/>
									</Link>
								) : (
									<Link
										className="profile-link"
										to="/login"
										style={{
											width: "25px",
											height: "25px"
										}}
									>
										<img
											style={{
												width: "100%",
												display: "block",
												objectFit: "cover"
											}}
											className="user-circle"
											src="./assets/images/osto-icons/user.png"
											alt="user profile"
											width="25"
											height="25"
										/>
									</Link>
								)}
							</div>

							<CartMenuTwo />
						</div>
					</div>
				</div>
			</div>

			<YandexMap />
		</header>
	);
}

function mapStateToProps(state) {
	return {
		logo: state.marketData.logo,
		phoneNumber: state.marketData.phoneNumber,
		user: state.user.user,
		location: state.user.location
	};
}

export default connect(mapStateToProps, { openModal })(Header);
