import React, { useEffect, useContext } from "react";
import SearchContext from "./SearchContext";
import { formatterCurrency } from "../../../../utils/string";
import { searchApi, searchByMenu } from "../../../../api/products";
import { useTranslation } from "react-i18next";
import { dt } from "../../../../i18n";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import { getMenuById } from "../../../../views/checkout/helpers";
import MediaImage from "../../../features/product/MediaImage";

const queryString = require("qs");

export default function SearchResult(isInputClicked) {
	const { query, result, setResult } = useContext(SearchContext);
	const { menuList } = useSelector((state) => state.menu);
	const { t } = useTranslation();

	useEffect(() => {
		if (query?.length >= 2 && isInputClicked) {
			setResult({ loading: true, data: [], menu: [] });
			const strQuery = queryString.stringify({ page: 1, size: 5 });
			Promise.all([
				searchApi(query, strQuery),
				searchByMenu(query, strQuery),
			])
				.then(([variationsResponse, menusResponse]) => {
					const { variations } = variationsResponse;
					const menuItems = menusResponse.items;
					setResult({
						loading: false,
						data: variations.length > 0 ? variations : false,
						menu: menuItems.length > 0 ? menuItems : false,
					});
				})
				.catch((e) => {
					setResult({ loading: false, data: [], menu: [] });
				});
		}
	}, [query]);

	return (
		<>
			{query.length >= 2 &&
				(result.menu.length > 0 || result.data.length > 0) && (
					<ul className="search-result p-3 m-0">
						{result.menu.length > 0 && (
							<div className="menus">
								{result.menu.map((item) => (
									<div key={item.id} className="menu-item">
										<div className="img">
											{item.image ? (
												<MediaImage
													image={item.image}
													largeSize="600x_"
													mediumSize="300x_"
													smallSize="300x_"
													alt={item.name}
												/>
											) : null}
										</div>
										<div>
											<li
												key={item.id}
												className="menu-result-item"
											>
												<Link
													to={`/category/${item.slug}`}
												>
													{dt(item.name)}
												</Link>
											</li>
											<ul className="menu-breadcrumb">
												{item.path
													.split(",")
													.slice(0, -2)
													.map((item) => (
														<Link
															to={`/category/${
																getMenuById(
																	menuList,
																	item
																)?.slug
															}`}
															className="breadcrumb-item"
															key={item}
														>
															{
																getMenuById(
																	menuList,
																	item
																)?.name
															}
														</Link>
													))}
											</ul>
										</div>
									</div>
								))}
							</div>
						)}
						{result.data.length > 0 && (
							<div className="products">
								{result.data.map((variation) => (
									<li
										key={variation.variation.id}
										className="search-result-item text-dark"
									>
										<a
											href={`product/${variation.variation.id}`}
											className="d-flex align-items-center"
										>
											<div className="img">
												<img
													src={
														variation.variation
															.images.length >
															0 &&
														variation.variation
															.images[0].urls
															.original
													}
													alt="img"
												/>
											</div>
											<div>
												<p className="result-variation-name ml-3 mb-0 p-0 text-dark">
													{variation.variation.name}
												</p>

												<div
													className="flex-grow-1 ml-3 text-dark"
													style={{
														width: "max-content",
														whiteSpace: "nowrap",
														textAlign: "right",
													}}
												>
													{variation.discount > 0 ? (
														<div className="search-result-price">
															<span className="old-price text-through">
																{formatterCurrency(
																	variation.oldPrice
																)}
															</span>
															<span className="new-price">
																{formatterCurrency(
																	variation.newPrice
																)}
															</span>
														</div>
													) : (
														<div>
															<span className="price">
																{formatterCurrency(
																	variation.newPrice
																)}
															</span>
														</div>
													)}
												</div>
											</div>
										</a>
									</li>
								))}
							</div>
						)}
					</ul>
				)}

			{result.data === false && result.menu === false && (
				<ul className="search-result p-3 m-0">
					<li className="search-result-item border-0 text-center p-2">
						<p className="m-0">0 {t("results")}</p>
					</li>
				</ul>
			)}
		</>
	);
}
