import React from "react";
import { connect } from "react-redux";
import Logo from "../Logo";
import { LinkComponent } from "../Menus/Desktop/LinkComponent";

function Footer(props) {
	const { container = "container", footer, settings } = props;
	const footerLogo = settings.footerLogo;
	return (
		<footer className="footer">
			<div className="footer-middle">
				<div className={container}>
					<div className="row">
						<div className="col-sm-6 col-lg-2">
							<div className="widget widget-about">
								<Logo
									logo={footerLogo}
									alt="Footer Logo"
									width="105px"
									height="25px"
								/>
							</div>
						</div>

						{footer &&
							footer.map((footer, index) => (
								<div key={index} className="col-sm-6 col-lg-3">
									<div className="widget">
										<h4 className="widget-title">
											{footer.name}
										</h4>
										<ul className="widget-list">
											{footer.children &&
												footer.children.map(
													(child, index) => (
														<li key={index}>
															<LinkComponent
																menu={child}
															/>
														</li>
													)
												)}
										</ul>
									</div>
								</div>
							))}
					</div>
				</div>
			</div>

			<div className="footer-bottom">
				<div className={container}>
					<p className="footer-copyright">
						Цены указаны с учетом НДС, без учета стоимости доставки
						заказа. Copyright © {new Date().getFullYear()} Все права
						защищены.
					</p>
					<figure className="footer-payments">
						<img
							src={`${process.env.PUBLIC_URL}/assets/images/payments.png`}
							alt="Payment methods"
							width="272"
							height="20"
						/>
					</figure>
				</div>
			</div>
		</footer>
	);
}

function mapStateToProps(state) {
	return {
		footer: state.menu.footer,
		settings: state.marketData.settings
	};
}

export default React.memo(connect(mapStateToProps)(Footer));
