import { cartTypes } from "../constants/action-types";
import { addToCartApi } from "../api/cart";
import { getVariation } from "../api/products";
import store from "../store";

export const emptyCart = () => ({
	type: cartTypes.EMPTY_CART
});

// remove item from cart
export const removeFromCartState = (productId, toast) => ({
	type: cartTypes.REMOVE_FROM_CART,
	productId,
	toast
});

// change item's qty
export const changeQty = (productId, count, token) => ({
	type: cartTypes.CHANGE_QTY,
	count,
	productId,
	token
});

// close cartlist modal
export const closeCartModal = () => ({
	type: cartTypes.CLOSE_CART_MODAL
});

export const syncCarts = async token => {
	const {
		cartlist: { cart: stateCarts }
	} = store.getState();

	if (stateCarts.length > 0) {
		for (let carts of stateCarts) {
			await addToCartApi(carts.count, carts.data.stock.id, token);
		}
	}
};

// add item to cart
export const addToCart = (product, count, id) => dispatch => {
	dispatch({
		type: cartTypes.ADD_TO_CART,
		product,
		count,
		id
	});
};

async function getVariationRether(variationId, cartlist) {
	const storedVariationData = cartlist.find(
		cart => cart.data.variation.id === variationId
	);
	return storedVariationData || getVariation(variationId);
}

export const setUserCart = sells => async dispatch => {
	if (sells) {
		if (sells.length > 0) {
			let myCart = store.getState().cartlist?.cart;
			let cart = [];
			for (let record of sells) {
				for (let product of record.sellRecords) {
					if (!product.deleted) {
						let rec = await getVariationRether(
							product.variation,
							myCart
						);
						cart.push({
							...rec,
							id: product.id,
							count: product.count,
							sum: rec.data.newPrice * product.count
						});
					}
				}
			}
			dispatch({
				type: cartTypes.SET_USER_CART,
				cart
			});
		} else {
			let cart = [];
			dispatch({
				type: cartTypes.SET_USER_CART,
				cart
			});
		}
	}
};
