import React, { Fragment, useState } from "react";
import { Tabs, TabList, Tab, TabPanel } from "react-tabs";
import { Link } from "react-router-dom";
import RegFormTab from "./formTabs/regFormTab";
import VerFormTab from "./formTabs/verFormTab";
import AuthFormTab from "./formTabs/authFormTab";
import { useTranslation } from "react-i18next";

function Login({ modal, modalData }) {
	const { t } = useTranslation();
	const [verification, setVerification] = useState(false);

	return (
		<div className="login">
			<div className="form-box" style={{ boxShadow: "none" }}>
				<div className="form-tab">
					<Tabs
						defaultIndex={modal === "login" ? 1 : 0}
						selectedTabClassName="show"
					>
						<TabList className="nav nav-pills nav-fill">
							{verification ? (
								<Tab className="nav-item">
									<span className="nav-link">
										{t("confirmation")}
									</span>
								</Tab>
							) : (
								<Fragment>
									<Tab className="nav-item">
										<span className="nav-link">
											{t("enter")}
										</span>
									</Tab>

									<Tab className="nav-item">
										<span className="nav-link">
											{t("registration")}
										</span>
									</Tab>
								</Fragment>
							)}
						</TabList>
						<div className="tab-content">
							{verification ? (
								<TabPanel style={{ paddingTop: "2rem" }}>
									<VerFormTab />
								</TabPanel>
							) : (
								<Fragment>
									<TabPanel style={{ paddingTop: "2rem" }}>
										<AuthFormTab />
									</TabPanel>
									<TabPanel>
										<RegFormTab
											modalData={modalData}
											onSuccessSubmit={() => {
												setVerification(true);
											}}
										/>
										<div
											className="form-choice"
											style={{
												marginTop: "10px",
												display: "none"
											}}
										>
											<hr />
											<p className="text-center">
												{t("using-social-network")}
											</p>
											<div className="row">
												<div className="col-sm-6">
													<Link
														to="#"
														className="btn btn-login btn-g"
													>
														<i className="icon-google"></i>
														{t("with-google")}
													</Link>
												</div>
												<div className="col-sm-6">
													<Link
														to="#"
														className="btn btn-login  btn-f"
													>
														<i className="icon-facebook-f"></i>
														{t("with-facebook")}
													</Link>
												</div>
											</div>
										</div>
									</TabPanel>
								</Fragment>
							)}
						</div>
					</Tabs>
				</div>
			</div>
		</div>
	);
}

export default React.memo(Login);
