import React, { useEffect, useState } from "react";
import InitialLoadData from "./utils/InitialLoadData";
import { BrowserRouter } from "react-router-dom";
// REDUX
import { Provider } from "react-redux";
import store from "./store";
import MainView from "./views";
import ToastyGlobal from "./components/features/modal/ToastyGlobal";

function ToastGlobal() {
	const [message, setMessage] = useState({ product: {}, message: "" });

	useEffect(() => {
		function callback(data) {
			setMessage({ product: data.product, message: data.message });

			setTimeout(() => {
				setMessage({ product: {}, message: "" });
			}, 4000);
		}

		window.toast = callback;
		return () => {
			delete window.toast;
		};
	}, []);

	return (
		Object.entries(message.product).length > 0 && (
			<ToastyGlobal product={message.product} setMessage={setMessage} />
		)
	);
}
export default function App() {
	return (
		<Provider store={store}>
			<InitialLoadData>
				<BrowserRouter basename={"/"}>
					<ToastGlobal />
					<MainView />
				</BrowserRouter>
			</InitialLoadData>
		</Provider>
	);
}
