import { combineReducers } from "redux";

// Import custom components
import cartReducer from "./cart";
import choosenProducts from "./choosenProducts";
import comparisonProducts from "./comparisonProducts";
import modalReducer from "./modal";
import menuReducer from "./menu";
import userReducer from "./user";
import blockReducer from "./blocks";
import supplierReducer from "./suppliers";
import marketDataReducer from "./marketData";
import anyReducer from "./any";
import dictionaryReducer from "./dictionary";

import { reducer as formReducer } from "redux-form";

const rootReducer = combineReducers({
	cartlist: cartReducer,
	modal: modalReducer,
	menu: menuReducer,
	choosenProducts: choosenProducts,
	comparisonProducts: comparisonProducts,
	user: userReducer,
	marketData: marketDataReducer,
	any: anyReducer,
	blocks: blockReducer,
	suppliers: supplierReducer,
	form: formReducer,
	dictionary: dictionaryReducer,
});

export default rootReducer;
