import React, { useState, useEffect, useMemo } from "react";
import { ToastContainer } from "react-toastify";

// import Custom Components
import Header from "../common/headers";
import Footer from "../common/footers";
// import MobileMenu from "../common/Menus/";

// import Utils
import {
	preventProductDefault,
	removePreventProductDefault,
	stickyHeaderHandler
} from "../../utils";

import { Helmet } from "react-helmet";
import market from "../../config/market";
import MobileBottomNavbar from "../mobile/";
import { useDispatch, useSelector } from "react-redux";
import { setUserData } from "../../actions/user";
import { setCurrentDictionary, setCurrentLang } from "../../i18n";
import reciveMessageFromApp from "./reciveMessageFromApp";
import OnBoarding from "./OnBoarding";
import { setIsFirstTime } from "../../actions/any";
import { getUserByToken } from "../../api/user";

function Layout(props) {
	const [container] = useState("container");
	const [prevPath] = useState("");
	const { isFirstTime } = useSelector(state => state.any);
	const favicon = useSelector(state => state.marketData.favicon);
	const dispatch = useDispatch();

	useMemo(() => {
		setCurrentLang();
		setCurrentDictionary();

		// if token in query
		if (window.location.search.startsWith("?token=")) {
			try {
				window.token = window.location.search.split("?token=")[1];
				window.webview = "App";
			} catch (error) {
				console.error(error);
			}
		}

		window.sendMessageToApp = message => {
			try {
				window.chat.postMessage(message);
			} catch (error) {
				console.error(error);
			}
		};

		window.sendMessageToWebView = reciveMessageFromApp;

		if (window.webview) {
			if (window.token) {
				getUserByToken(window.token).then(data => {
					dispatch(setUserData(data, { token: window.token }));
				});
			}
		}
		if (window.innerWidth > 500) {
			dispatch(setIsFirstTime());
		}
	}, []);

	useEffect(() => {
		// set sticky header
		stickyHeaderHandler();
		window.addEventListener("scroll", stickyHeaderHandler, true);

		// prevent product thumb icons
		preventProductDefault();

		return () => {
			window.removeEventListener("scroll", stickyHeaderHandler);

			// remove listeners of prevent product
			removePreventProductDefault();
		};
	});

	return isFirstTime &&
		market.subdomain === "vibo" &&
		process.env.REACT_APP_PLATFORM === "mobile" ? (
		<OnBoarding />
	) : (
		<>
			<Helmet>
				<link
					rel="icon"
					type="image/png"
					sizes="16x16"
					href={favicon}
				/>
				<link
					rel="icon"
					type="image/png"
					sizes="32x32"
					href={favicon}
				/>
			</Helmet>

			<div className="page-wrapper">
				<Header container={container} urls={prevPath} />
				{props.children}

				<Footer container={container} urls={prevPath} />
				<ToastContainer autoClose={3000} className="toast-container" />
				<MobileBottomNavbar />
			</div>

			{/* <MobileMenu home={market.config.home} /> */}
		</>
	);
}

export default Layout;
