import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import { saveDictionary } from "../actions/dictionary";
import { dispatch, getState } from "../store";

const resources = {
	uz: {
		translation: require("./locales/uz.json"),
	},
	ru: {
		translation: require("./locales/ru.json"),
	},
};

export function getLang() {
	return window.lang || localStorage.getItem("lang") || "ru";
}

i18n.use(initReactI18next).init({
	resources,
	lng: getLang(),
	interpolation: {
		escapeValue: false,
	},
});

export const setCurrentLang = () => {
	let lang = getLang();
	localStorage.setItem("lang", lang);
	i18n.changeLanguage(lang);
};

export function changeLanguage(lang) {
	localStorage.setItem("lang", lang);
	i18n.changeLanguage(lang);
	changeDynamicDictinary();
}

let current_dictionary = getState().dictionary[getLang()] || {};

export function setCurrentDictionary(dict) {
	let dictionary = getState().dictionary;
	current_dictionary = dictionary[getLang()] || {};
	dispatch(saveDictionary({ ...dictionary }));
}

export function changeDynamicDictinary() {
	dispatch(saveDictionary({ loading: true }));
	setTimeout(() => {
		let dictionary = getState().dictionary;
		current_dictionary = dictionary[getLang()] || {};
		dispatch(saveDictionary({ ...dictionary, loading: false }));
	}, 200);
}

/**
 * Dynamic translation data. Translate word by choosen language
 * @param {string} word Word which is should translate
 */

export function dt(word) {
	return current_dictionary[word] || word;
}

export default i18n;
