import QueryString from "qs";
import mainCaller from "./mainCaller";

export function getEverySupplier(ids) {
	return mainCaller(`/supplier?${QueryString.stringify({ id: ids })}`, "GET");
}

export default function supplierProducts(supplierId, query) {
	return mainCaller(`/${supplierId}/supplier-products?${query}`);
}

export function getSuppliers() {
	return mainCaller(`/supplier`);
}
