import React, { useState } from "react";
import SearchContext from "./SearchContext";
import SearchForm from "./SearchForm";
import SearchResult from "./SearchResult";
import SearchHistory from "./SearchHistory";
import { debounce } from "../../../../utils/debounce";
import { useDispatch, useSelector } from "react-redux";
import { setSearchHistory } from "../../../../actions/any";
import { useHistory } from "react-router";

export default ({ header, setSearch, inputId = "searchInput" }) => {
	const h = useHistory();
	const [hidden, setHidden] = useState(false);
	const [result, setResult] = useState({
		loading: false,
		data: [],
		menu: [],
	});
	const [query, setQuery] = useState("");
	const [value, setValue] = useState();
	const dispatch = useDispatch();
	const searchHistory = useSelector((state) => state.any.searchHistory);
	function searchFn(value) {
		debounce(
			() => {
				setQuery(value);
				setResult({
					loading: false,
					data: [],
					menu: [],
				});
			},
			"inputQuery",
			300
		);
	}
	function renderSearchPage(e) {
		e.preventDefault();
		let nh = searchHistory.filter((q) => q !== query);
		setHidden(true);
		nh.unshift(query);
		dispatch(setSearchHistory(nh));
		h.push(`/search/${query}`);
	}

	return (
		<SearchContext.Provider
			value={{
				hidden: hidden,
				setHidden: setHidden,
				result: result,
				setResult: setResult,
				query: query,
				setQuery: setQuery,
				value: value,
				inputId: inputId,
				setValue: setValue,
				searchFn: searchFn,
				renderSearchPage: renderSearchPage,
			}}
		>
			<div className="d-flex flex-column div search-box-wrapper">
				<div className="search-box">
					<SearchForm header={header} setSearch={setSearch} />
					<div className="search-result-wrapper">
						<SearchResult />
						<SearchHistory />
					</div>
				</div>
			</div>
		</SearchContext.Provider>
	);
};
