import React from "react";
import { useContext } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setSearchHistory } from "../../../../actions/any";
import SearchContext from "./SearchContext";
import { useTranslation } from "react-i18next";

export default function SearchHistory() {
	const searchHistory = useSelector((state) => state.any.searchHistory);
	const dispatch = useDispatch();
	const { t } = useTranslation();
	const { searchFn, setValue, hidden, query } = useContext(SearchContext);

	return (
		hidden &&
		query?.length === 0 &&
		searchHistory?.length > 0 && (
			<div className="search-result p-3">
				<div className="d-flex justify-content-between">
					<p className="m-0">{t("history")}</p>
					<button
						className="s-clear-but"
						onClick={() => dispatch(setSearchHistory([]))}
					>
						{t("clear")}
					</button>
				</div>
				<ul className="history-list">
					{searchHistory?.map((historyValue, i) => (
						<li
							onClick={() => {
								searchFn(historyValue);
								setValue(historyValue);
							}}
							key={i}
							className="p-2 d-flex align-items-center history-item"
						>
							<span className="mr-2 reload">
								<i className="i-icon icon-refresh"></i>
								<img
									className="d-none reload-icon"
									src="/assets/images/icons/baby_center/reload.png"
									alt="reload"
								/>
							</span>
							<p className="m-0">{historyValue}</p>
						</li>
					))}
				</ul>
			</div>
		)
	);
}
