import React, { useState } from "react";
import { changeLanguage, getLang } from "../../i18n";

export default function ChangeLanguageTwo() {
	const [language, setLanguage] = useState(getLang());
	const [isOpen, setIsOpen] = useState(false);

	const onChange = lang => {
		changeLanguage(lang);
	};

	return (
		<div
			onClick={() => setIsOpen(!isOpen)}
			className="position-relative change-lang"
		>
			<div className="d-flex" style={{ cursor: "pointer" }}>
				<img
					src={
						language === "uz"
							? "/assets/images/uzbekistan.svg"
							: "/assets/images/russia.svg"
					}
					alt=""
				/>
				<p className="m-0">
					{language === "uz" ? "O'zbek" : "Русский"}
				</p>
				<i className="icon-angle-down"></i>
			</div>
			<div
				className="lang-selector flex-column position-absolute"
				style={
					isOpen
						? { display: "flex", zIndex: 5 }
						: { display: "none" }
				}
			>
				<div
					className={"langBut " + (language === "uz" ? "active" : "")}
				>
					<button
						className="text-white d-flex align-items-center"
						onClick={() => {
							onChange("uz");
							setLanguage("uz");
							setIsOpen(!isOpen);
						}}
					>
						<img src="/assets/images/uzbekistan.svg" alt="" />
						<span>O'zbek</span>
					</button>
				</div>
				<div
					className={"langBut " + (language === "ru" ? "active" : "")}
				>
					<button
						className="text-white d-flex align-items-center"
						onClick={() => {
							onChange("ru");
							setLanguage("ru");
							setIsOpen(!isOpen);
						}}
					>
						<img src="/assets/images/russia.svg" alt="" />
						<span>Русский</span>
					</button>
				</div>
			</div>
		</div>
	);
}
