import mainCaller from "./mainCaller";

let canSend = true;

export function setPhoneToGetVerifyCode(phoneNumber) {
	if (!canSend) return Promise.resolve();

	canSend = false;
	setTimeout(() => {
		canSend = true;
	}, 1000 * 60);
	return mainCaller("/sent-verification", "POST", { phoneNumber });
}

export function verifyCode(phoneNumber, code) {
	return mainCaller("/verify-register-code", "POST", { phoneNumber, code });
}
