import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { LazyLoadImage } from "react-lazy-load-image-component";
import { getImageUrl } from "../../../../utils/oxMethods";
import { connect } from "react-redux";
import { dt } from "../../../../i18n";
import MediaImage from "../../../features/product/MediaImage";

function menuList(children) {
	return (
		<ul>
			{children
				.sort((a, b) => a.sort - b.sort)
				.map(menu => {
					return (
						<li key={menu.id}>
							<Link
								to={`/category/${menu.slug}`}
								className="mega-menu-links"
								style={{ lineHeight: 2.5 }}
							>
								{menu.image ? (
									<div className="menu-icon">
										<MediaImage
											image={menu.image}
											alt={menu.name}
											largeSize="800x_"
											mediumSize="300x_"
											smallSize="150x_"
										/>
									</div>
								) : null}
								{dt(menu.name)}
							</Link>
						</li>
					);
				})}
		</ul>
	);
}

const megaMenuBody = (path, menu, setMenuActive) => {
	return (
		<div
			className="megamenu"
			onMouseEnter={() => setMenuActive(menu)}
			onMouseLeave={() => setMenuActive(false)}
		>
			<div className="row no-gutters">
				<div
					className={`${
						menu.backgroundImage ? "col-md-6" : "col-md-12"
					}`}
				>
					<div className="menu-col">
						<div className="row">
							{menu.children
								.sort((a, b) => a.sort - b.sort)
								.map(subMenu => (
									<div
										key={subMenu.id}
										className="col-12 pb-2"
									>
										<Link
											to={`/category/${subMenu.slug}`}
											className={`menu-title ${
												subMenu.children?.length > 0
													? `font-weight-bold`
													: `font-weight-normal`
											}`}
										>
											{subMenu.image ? (
												<div className="menu-icon">
													<MediaImage
														image={subMenu.image}
														alt={subMenu.name}
														largeSize="800x_"
														mediumSize="300x_"
														smallSize="150x_"
													/>
												</div>
											) : null}
											{dt(subMenu.name)}
										</Link>
										{menuList(subMenu.children)}
									</div>
								))}
						</div>
					</div>
				</div>
				{menu.backgroundImage ? (
					<div className="col-md-6">
						<div className="banner">
							<Link
								to={`/category/${menu.slug}`}
								className="banner banner-menu"
							>
								<LazyLoadImage
									src={getImageUrl(menu.backgroundImage)}
									effect="blur"
									alt={menu.slug}
								/>
							</Link>
						</div>
					</div>
				) : null}
			</div>
		</div>
	);
};
const mainVerticalMenuOne = (path, menu, menuActive, setMenuActive) => (
	<li key={menu.id} className="megamenu-container">
		<Link
			className={`w-100 d-flex align-items-center ${
				menu.children.length > 0 ? "sf-with-ul" : "sf"
			} ${menuActive.id && menuActive.id === menu.id ? `active` : ``}`}
			to={`/category/${menu.slug}`}
		>
			{menu.image ? (
				<img
					className="mr-3"
					src={getImageUrl(menu.image, "50x_")}
					alt=""
					width="18"
					height="18"
				/>
			) : null}
			{dt(menu.name)}
		</Link>
		{menu.children.length > 0
			? megaMenuBody(path, menu, setMenuActive)
			: null}
	</li>
);

function MainMenu(props) {
	const allMenuData = props.menu;
	const [path, setPath] = useState("");
	const [menuActive, setMenuActive] = useState(false);

	useEffect(() => {
		setPath(window.location.href);
	});
	return (
		<div
			className={`mobile-menu-container-two`}
			onClick={e => {
				document
					.querySelector("body")
					.classList.remove("mmenu-active-two");
			}}
		>
			<div className="mobile-menu-wrapper">
				<div className="dropdown category-dropdown">
					<Link to={`#`}>
						<span></span>
					</Link>
					<div
						className="my-menu"
						style={{ backgroundColor: "#000000" }}
					>
						<div className="dropdown-menu">
							<nav className="side-nav">
								<ul className="menu-vertical sf-arrows sf-js-enabled">
									{allMenuData.menuVertical
										.sort((a, b) => a.sort - b.sort)
										.map(menu =>
											mainVerticalMenuOne(
												path,
												menu,
												menuActive,
												setMenuActive
											)
										)}
								</ul>
							</nav>
						</div>
					</div>
				</div>
			</div>
			<span
				className="mobile-menu-close"
				onClick={e => {
					document
						.querySelector("body")
						.classList.remove("mmenu-active-two");
				}}
			>
				<i className="icon-close"></i>
			</span>
		</div>
	);
}

function mapStateToProps(state) {
	return {
		menu: state.menu
	};
}

export default React.memo(connect(mapStateToProps)(MainMenu));
