import { suppliers } from "../constants/action-types";
import { myReducerPersist } from "../store/managerLocalStorage";

const initialState = {
	suppliers: undefined
};

const anyReducer = (state = initialState, action) => {
	switch (action.type) {
		case suppliers.SET_SUPPLIERS:
			return {
				suppliers: action.suppliers
			};

		default:
			return state;
	}
};

export default myReducerPersist({ key: "ox-suppliers" }, anyReducer);
