import React, { useState } from "react";
import SearchContext from "./SearchContext";
import SearchResult from "./SearchResult";
import SearchHistory from "./SearchHistory";
import { debounce } from "../../../../utils/debounce";
import { useDispatch, useSelector } from "react-redux";
import { setSearchHistory } from "../../../../actions/any";
import SearchFormTwo from "./SearchFormTwo";
import { useHistory } from "react-router";
import { requestAbort } from "../../../../api/mainCaller";

export default ({
	header,
	setSearch,
	search,
	inputId,
	isInputClicked,
	setIsInputClicked,
}) => {
	const [hidden, setHidden] = useState(false);
	const [result, setResult] = useState({
		loading: false,
		data: [],
		menu: [],
	});
	const [query, setQuery] = useState("");
	const [value, setValue] = useState();
	const dispatch = useDispatch();
	const searchHistory = useSelector((state) => state.any.searchHistory);
	const h = useHistory();

	function searchFn(value) {
		debounce(
			() => {
				setQuery(value);
				setResult({
					loading: false,
					data: [],
					menu: [],
				});
			},
			"inputQuery",
			500
		);
	}

	function renderSearchPage(e) {
		if (!query?.length) return;

		requestAbort.abort(
			`/menu-items?name=${query}&page=1&size=5&active=true`
		);
		requestAbort.abort(`/search?q=${query}&page=1&size=5`);

		e.preventDefault();
		let nh = searchHistory.filter((q) => q !== query);
		setHidden(true);
		nh.unshift(query);
		dispatch(setSearchHistory(nh));
		h.push(`/search/${query}`);
		setIsInputClicked(false);
	}

	return (
		<SearchContext.Provider
			value={{
				hidden: hidden,
				setHidden: setHidden,
				result: result,
				setResult: setResult,
				query: query,
				setQuery: setQuery,
				value: value,
				setValue: setValue,
				searchFn: searchFn,
				inputId: inputId,
				renderSearchPage: renderSearchPage,
			}}
		>
			<div
				className="d-flex flex-column div search-box-wrapper"
				// onClick={() =>
				// 	isInputClicked ? setIsInputClicked(!isInputClicked) : null
				// }
			>
				<SearchFormTwo
					header={header}
					setSearch={setSearch}
					isInputClicked={isInputClicked}
					setIsInputClicked={setIsInputClicked}
					search={search}
					query={query}
				/>
				<div className="search-result-wrapper">
					<SearchResult isInputClicked={isInputClicked} />
					<SearchHistory />
				</div>
			</div>
		</SearchContext.Provider>
	);
};
