import { menuTypes } from "../constants/action-types";
import { myReducerPersist } from "../store/managerLocalStorage";

const initialState = {
	footer: [],
	menuHorizontal: [],
	menuVertical: [],
	other: [],
	menuList: []
};

const menuReducer = (state = initialState, action) => {
	switch (action.type) {
		case menuTypes.RECEIVE_MENUS:
			return {
				...state,
				footer: action.footer,
				top: action.top,
				menuHorizontal: action.menuHorizontal,
				menuVertical: action.menuVertical,
				other: action.other,
				menuList: action.menuList
			};

		default:
			return state;
	}
};

export default myReducerPersist(
	{
		key: "ox-menu"
	},
	menuReducer
);
