import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { useEffect, useState } from "react";
import { getImageUrl } from "../../../../utils/oxMethods";
import { useTranslation } from "react-i18next";
import { LazyLoadImage } from "react-lazy-load-image-component";
import { dt } from "../../../../i18n";
function menuList(children) {
	return (
		<ul>
			{children
				.sort((a, b) => a.sort - b.sort)
				.map(menu => {
					return (
						<li key={menu.id}>
							<Link to={`/category/${menu.slug}`}>
								{dt(menu.name)}
							</Link>
						</li>
					);
				})}
		</ul>
	);
}

const megaMenuBody = (path, menu) => {
	return (
		<div className="megamenu">
			<div className="row no-gutters">
				<div
					className={`${
						menu.backgroundImage ? "col-md-8" : "col-md-12"
					}`}
				>
					<div className="menu-col">
						<div className="row">
							{menu.children
								.sort((a, b) => a.sort - b.sort)
								.map(subMenu =>
									menu.children.length <= 4 ? (
										<div
											key={subMenu.id}
											className="col-md-6"
										>
											<Link
												to={`/category/${subMenu.slug}`}
												className="menu-title"
											>
												{dt(subMenu.name)}
											</Link>
											{menuList(subMenu.children)}
										</div>
									) : (
										<div
											key={subMenu.id}
											className="col-md-12"
										>
											<div className="menu-title">
												<Link
													to={`/category/${subMenu.slug}`}
												>
													{dt(subMenu.name)}
												</Link>
											</div>
										</div>
									)
								)}
						</div>
					</div>
				</div>
				{menu.backgroundImage ? (
					<div className="col-md-4">
						<div className="banner banner-overlay">
							<Link
								to={`/category/${menu.slug}`}
								className="banner banner-menu"
							>
								<LazyLoadImage
									src={getImageUrl(menu.backgroundImage)}
									effect="blur"
									alt={menu.slug}
								/>
							</Link>
						</div>
					</div>
				) : null}
			</div>
		</div>
	);
};

const mainVerticalMenuTwo = (path, menu) => (
	<li key={menu.id} className="megamenu-container">
		<Link
			className={`d-flex align-items-center ${
				menu.children.length > 0 ? "sf-with-ul" : "sf"
			}`}
			to={`/category/${menu.slug}`}
		>
			{menu.image ? (
				<img
					className="mr-3"
					src={getImageUrl(menu.image, "50x_")}
					alt=""
					width="18"
					height="18"
				/>
			) : null}
			{dt(menu.name)}
		</Link>
		{menu.children.length > 0 ? megaMenuBody(path, menu) : null}
	</li>
);

function MainMenu(props) {
	const allMenuData = props.menu;
	const [path, setPath] = useState("");
	const { t } = useTranslation();

	useEffect(() => {
		setPath(window.location.href);
	});
	return (
		<div className="dropdown category-dropdown">
			<Link
				to={`#`}
				className="dropdown-toggle"
				role="button"
				data-toggle="dropdown"
				aria-haspopup="true"
				aria-expanded="false"
				data-display="static"
			>
				{t("catalog_products")}
			</Link>

			<div className="dropdown-menu">
				<nav className="side-nav">
					<ul className="menu-vertical sf-arrows sf-js-enabled">
						{allMenuData.menuVertical
							.sort((a, b) => a.sort - b.sort)
							.map(menu => mainVerticalMenuTwo(path, menu))}
					</ul>
				</nav>
			</div>
		</div>
	);
}
function mapStateToProps(state) {
	return {
		menu: state.menu
	};
}

export default React.memo(connect(mapStateToProps)(MainMenu));
