import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { connect, useSelector } from "react-redux";
import CartMenuTwo from "../partials/cart-menu-two";
import LoginModal from "../../features/modal/login-modal";
import YandexMap from "../../features/modal/YandexMapModal";
import { openModal } from "../../../actions/modal";
import getLocation from "../../features/getAddress";
import Logo from "../Logo";
import SearchForm from "../partials/SearchFormTwo/";
import ChangeLanguageTwo from "../../features/ChangeLanguageTwo";
import { useTranslation } from "react-i18next";
import market from "../../../config/market";
import HorizontalMenuHeaderBottomTwo from "../Menus/Desktop/HorizontalMenuHeaderBottomTwo";
import HorizontalMenuHeaderBottom from "../Menus/Desktop/HorizontalMenuHeaderBottom";
import { LinkComponent } from "../Menus/Desktop/LinkComponent";

function HeaderSeven(props) {
	const {
		openModal,
		user,
		marketData,
		location: { address },
	} = props;
	const logo = marketData.settings.mainLogo;
	const { t } = useTranslation();
	const { productList } = useSelector((state) => state.choosenProducts);
	const { productListCompare } = useSelector(
		(state) => state.comparisonProducts
	);

	const { token } = useSelector((state) => state.user?.tokenData);
	const topMenu = useSelector((state) => state?.menu.top);
	const icons = market.config.icons;
	const [isInputClicked, setIsInputClicked] = useState(false);
	const [darkMode, setDarkMode] = useState(false);
	const mode = window.localStorage.getItem("mode");
	function openLoginModal(e) {
		e.preventDefault();
		openModal("login");
	}

	function changeMode() {
		if (mode) {
			window.localStorage.removeItem("mode");
		} else window.localStorage.setItem("mode", "dark-mode");
		setDarkMode(!darkMode);
	}
	function openMapModal(e) {
		if (window.webview) {
			window.sendMessageToApp("OPEN_LOCATION");
		} else {
			openModal("map");
			e.preventDefault();
		}
	}
	useEffect(() => {
		const body = document.querySelector("body");
		if (mode) {
			body.classList.add("dark-mode");
		} else body.classList.remove("dark-mode");
	}, [mode]);

	if (!address) {
		getLocation();
	}
	const allMenuData = useSelector((state) => state.menu);
	return (
		<header className="header-seven">
			<div className="top-header">
				<div className="container">
					<div className="header-top-left">
						<div
							className="location"
							style={{ padding: "10px 20px 10px" }}
						>
							<Link
								className="location-link"
								to={`${process.env.PUBLIC_URL}`}
								style={{ color: "black" }}
							>
								<p
									className="d-flex align-items-center"
									onClick={openMapModal}
									style={{ marginBottom: "0px" }}
								>
									<img
										src={market.config.icons.location}
										alt="location"
									/>
									{address ? (
										<span>
											{address.administrativeArea},{" "}
											{address.name}
										</span>
									) : (
										<span>{t("no-address-selected")}</span>
									)}
								</p>
							</Link>
						</div>
					</div>
					<div className="header-top-right">
						<button
							className="change-mode d-none"
							onClick={changeMode}
						>
							<img
								src={
									mode
										? market.config.icons.darkMode
										: market.config.icons.lightMode
								}
								alt="mode"
							/>
						</button>
						{topMenu.map((item, i) => (
							<a href={`${item.link}`} key={i} className="info">
								{item.name}
							</a>
						))}
						<ChangeLanguageTwo />
					</div>
				</div>
			</div>
			<div className="header-middle sticky-header">
				<div className="container">
					<div className="row">
						<div className="left-header col-lg-3 col-12">
							<Logo logo={logo} />
							<div
								className={`search only-mobile ${
									!isInputClicked ? "" : "long-input"
								}`}
							>
								<SearchForm
									inputId={"searchInputDesktop"}
									isInputClicked={isInputClicked}
									setIsInputClicked={setIsInputClicked}
								/>
							</div>
						</div>
						<div className="center-header col-lg-7 col-12">
							<div
								className={`search only-desktop ${
									!isInputClicked ? "" : "long-input"
								}`}
							>
								<SearchForm
									inputId={"searchInputDesktop"}
									isInputClicked={isInputClicked}
									setIsInputClicked={setIsInputClicked}
								/>
							</div>
							<ul
								className={`${
									isInputClicked && window.innerWidth > 991
										? "d-none"
										: "header-menu-list"
								}`}
							>
								{allMenuData?.menuHorizontal
									?.slice(0, 4)
									.map((firstMenu, index) => (
										<li
											key={index}
											className="first-menu-child"
										>
											<LinkComponent menu={firstMenu} />
										</li>
									))}
							</ul>
						</div>
						<div className="right-header col-lg-2">
							<div className="dropdown">
								{user.id ? (
									<Link
										className="profile-link"
										to="/profile"
									>
										<img
											src={icons.user}
											alt=""
											style={{
												fontSize: "20px",
												height: "20px",
											}}
										/>
										<h6>
											{token ? t("profile") : t("login")}
										</h6>
									</Link>
								) : (
									<Link
										className="profile-link"
										to="#signin-modal"
										onClick={openLoginModal}
									>
										<img
											src={icons.user}
											alt="profile"
											style={{
												width: "20px",
												height: "20px",
											}}
										/>
										<h6>{t("login")}</h6>
									</Link>
								)}
							</div>
							<Link
								to="/choosen-products-list"
								className="choosen-products-icon"
								style={{
									fontSize: "24px",
								}}
							>
								<div className="position-relative">
									<img
										src={icons.favourites}
										alt="heart"
										style={{
											width: "22px",
											height: "22px",
										}}
									/>
									{productList.length > 0 ? (
										<span>{productList.length}</span>
									) : null}
								</div>
								<h6>{t("favourites")}</h6>
							</Link>
							{market.config.compare ? (
								<Link
									to="/compare-products-list"
									className="compare-products-icon"
									style={{
										fontSize: "24px",
									}}
								>
									<div className="position-relative">
										<img
											src={icons.compare}
											alt="compare"
											style={{
												width: "22px",
												height: "22px",
											}}
										/>
										{productListCompare.length > 0 ? (
											<span>
												{productListCompare.length}
											</span>
										) : null}
									</div>
									<h6>{t("compare")}</h6>
								</Link>
							) : null}
							<CartMenuTwo />
						</div>
					</div>
				</div>
			</div>
			<LoginModal />
			<YandexMap />
			<div className="header-bottom-one container">
				<HorizontalMenuHeaderBottom
					setIsInputClicked={setIsInputClicked}
				/>
			</div>
			<div className="header-bottom-two container">
				<HorizontalMenuHeaderBottomTwo />
			</div>
			{/* </div> */}
		</header>
	);
}

function mapStateToProps(state) {
	return {
		marketData: state.marketData,
		phoneNumber: state.marketData.phoneNumber,
		user: state.user.user,
		location: state.user.location,
	};
}

export default connect(mapStateToProps, { openModal })(HeaderSeven);
