import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import { connect, useSelector } from "react-redux";
import { getCartCount, getCartTotal } from "../../../services";
import { closeCartModal } from "../../../actions/cart";
import { getImageUrl } from "../../../utils/oxMethods";
import { formatterCurrency } from "../../../utils/string";
import { useTranslation } from "react-i18next";

function getNameProductOrVariation(item) {
	if (item.product) {
		return item.product.name;
	}
	return item.data.variation.name;
}

function CartMenu(props) {
	const { cartlist, closeCartModal, showCartList, icon = false } = props;
	const { token } = useSelector((state) => state.user.tokenData);
	const { t } = useTranslation();

	let total = getCartTotal(cartlist);

	useEffect(() => {
		setTimeout(() => {
			closeCartModal();
		}, 2000);
	}, [showCartList]);

	return (
		<div className="dropdown cart-dropdown">
			<Link
				to={`${process.env.PUBLIC_URL}${token ? "/checkout" : "/cart"}`}
				className="dropdown-toggle"
				role="button"
				data-toggle="dropdown"
				aria-haspopup="true"
				aria-expanded="false"
				data-display="static"
			>
				<div className="p-2">
					{icon ? (
						icon
					) : (
						<i className="icon-shopping-cart text-center"></i>
					)}
					<p>{t("cart")}</p>
					<span className="cart-count">{getCartCount(cartlist)}</span>
				</div>
			</Link>

			<div
				className={`dropdown-menu dropdown-menu-right${
					!cartlist.length ? " text-center" : ""
				}${showCartList ? " visible-cart" : ""}`}
			>
				{!cartlist.length ? (
					<p>{t("empty-cart")}</p>
				) : (
					<>
						<div className="dropdown-cart-products">
							{cartlist.map((item, index) => {
								return (
									<div className="product" key={index}>
										<div className="product-cart-details">
											<h4 className="product-title">
												<Link
													to={
														`${process.env.PUBLIC_URL}/product/` +
														item.data.variation.id
													}
												>
													{getNameProductOrVariation(
														item
													)}
												</Link>
											</h4>

											<span className="cart-product-info">
												<span className="cart-product-qty">
													{item.count}
												</span>{" "}
												x{" "}
												{item.data.discount
													? item.data.newPrice
													: item.data.oldPrice}{" "}
												UZS
											</span>
										</div>
										<figure className="product-image-container">
											<Link
												to={
													`${process.env.PUBLIC_URL}/product/` +
													item.data.variation.id
												}
												className="product-image"
											>
												<img
													src={getImageUrl(
														item.data.variation
															.images[0]
													)}
													data-oi={
														item.data.variation
															.images[0]
													}
													alt="product"
												/>
											</Link>
										</figure>
									</div>
								);
							})}
						</div>
						<div className="dropdown-cart-total">
							<span>{t("subtotal")}</span>

							<span className="cart-total-price">
								{formatterCurrency(total)}
							</span>
						</div>

						<div className="dropdown-cart-action">
							<Link
								to={`${process.env.PUBLIC_URL}${
									token ? "/checkout" : "/cart"
								}`}
								style={{ width: "100%" }}
								className="btn btn-primary"
							>
								{t("buy")}
							</Link>
						</div>
					</>
				)}
			</div>
		</div>
	);
}

function mapStateToProps(state) {
	return {
		cartlist: state.cartlist.cart ? state.cartlist.cart : [],
		showCartList: state.cartlist.showCartList,
	};
}

export default connect(mapStateToProps, {
	closeCartModal,
})(CartMenu);
