import React, { useState } from "react";
import { changeLanguage, getLang } from "../../i18n";
import market from "../../config/market";
export default function ChangeLanguageThree() {
	const [language, setLanguage] = useState(getLang());
	const [isOpen, setIsOpen] = useState(false);

	const onChange = lang => {
		changeLanguage(lang);
	};

	return (
		<div
			onMouseEnter={() =>
				window.innerWidth < 991 ? false : setIsOpen(!isOpen)
			}
			onMouseLeave={() =>
				window.innerWidth < 991 ? false : setIsOpen(!isOpen)
			}
			onClick={() =>
				window.innerWidth >= 991 ? false : setIsOpen(!isOpen)
			}
			className="position-relative change-lang-three"
		>
			<div className="d-flex" style={{ cursor: "pointer" }}>
				<img
					src={
						language === "uz"
							? "/assets/images/uz.svg"
							: "/assets/images/ru.svg"
					}
					alt=""
				/>
				<img
					className="arrow"
					src={market.config.icons.rArrow}
					alt="language"
				/>
			</div>
			<div
				className="lang-selector flex-column position-absolute"
				style={
					isOpen
						? { display: "flex", zIndex: 5 }
						: { display: "none" }
				}
			>
				<div className="lang-body">
					<p className="choose-ragion">Выберите язык</p>
					<div
						className={
							"langBut " + (language === "uz" ? "active" : "")
						}
					>
						<button
							className="text-white d-flex align-items-center"
							onClick={() => {
								onChange("uz");
								setLanguage("uz");
								setIsOpen(!isOpen);
							}}
						>
							<span>Узбекский</span>
							<img src="/assets/images/uz.svg" alt="" />
						</button>
					</div>
					<div
						className={
							"langBut " + (language === "ru" ? "active" : "")
						}
					>
						<button
							className="text-white d-flex align-items-center"
							onClick={() => {
								onChange("ru");
								setLanguage("ru");
								setIsOpen(!isOpen);
							}}
						>
							<span>Русский</span>
							<img src="/assets/images/ru.svg" alt="" />
						</button>
					</div>
				</div>
			</div>
		</div>
	);
}
