import React, { useEffect, useState } from "react";
import {
	GeolocationControl,
	Map,
	Placemark,
	SearchControl,
	YMaps,
	ZoomControl
} from "react-yandex-maps";
import { getAddressByCoordinate } from "../../api/mapAPI";

export default function YandexMap({
	zoom = true,
	center,
	onChange,
	// pickUp,
	setCenter,
	address,
	location,
	height = "100%",
	width = "100%",
	bottom = 50,
	right = 10,
	search = true,
	activePanel
}) {
	const [selectedAdress, setSelectedAdress] = useState();

	useEffect(() => {
		if (address) {
			getAddress();
		}
	}, [address]);

	function onBoundsChange(e) {
		getAddressByCoordinate(
			e.get("target").getCenter()[0],
			e.get("target").getCenter()[1]
		)
			.then(data => {
				// setCenter({
				// 	center: [data.latitude, data.longitude]
				// });
				if (data.countryCode === "uz" || "UZ") {
					onChange(data);
				} else {
					onChange(null);
				}
			})
			.catch(e => console.log(e));
	}
	async function getAddress() {
		if (location?.latitude && location?.longitude) {
			setSelectedAdress({
				latitude: location.latitude || 0,
				longitude: location.longitude || 0
			});
		} else {
			setCenter({
				center: [41.3112092101035, 69.27965188818546]
			});
		}
		if (selectedAdress) {
			setCenter({
				center: [selectedAdress.latitude, selectedAdress.longitude]
			});
		}
	}

	return (
		<YMaps>
			<Map
				width={width}
				height={height}
				defaultState={center}
				state={{
					center: center.center,
					zoom: 16
				}}
				onBoundsChange={e => {
					!address && onBoundsChange(e);
				}}
				// defaultGeometry={address}
			>
				{search ? <SearchControl id="search-map" /> : null}
				<GeolocationControl
					options={{
						float: "left",
						position: {
							bottom: activePanel ? bottom : 380,
							right: right
						}
					}}
				/>
				{zoom ? (
					<ZoomControl id="zoom" options={{ float: "right" }} />
				) : null}
				{selectedAdress ? (
					<Placemark
						geometry={[
							selectedAdress.latitude,
							selectedAdress.longitude
						]}
					/>
				) : null}
			</Map>
		</YMaps>
	);
}
