import { DICTIONARY_TYPES } from "../constants/action-types";
import { getLang } from "../i18n";
import { myReducerPersist } from "../store/managerLocalStorage";

const initialState = {
	loading: false,
	lang: getLang()
};

const dictionaryReducer = (state = initialState, action) => {
	switch (action.type) {
		case DICTIONARY_TYPES.SET_DICTIONARY:
			return {
				...state,
				...action.data
			};
		default:
			return state;
	}
};

export default myReducerPersist(
	{
		key: "ox-dictionary"
	},
	dictionaryReducer
);
