import React from "react";
import { Link } from "react-router-dom";
import MediaImage from "../features/product/MediaImage";

export default function Logo({ logo, width = "auto", height = "auto", alt }) {
	return (
		<Link to={process.env.PUBLIC_URL} className="logo">
			<div
				style={{
					width: width,
					height: height
				}}
			>
				<MediaImage
					image={logo}
					alt={alt}
					largeSize="800x_"
					mediumSize="800x_"
					smallSize="800x_"
				/>
			</div>
		</Link>
	);
}
