import { modalTypes } from "../constants/action-types";

export const openModal = (modalName, data) => dispatch => {
	dispatch({
		type: modalTypes.SHOW_MODAL,
		modal: modalName,
		data: data
	});
};

export const closeModal = modalName => dispatch => {
	dispatch({
		type: modalTypes.CLOSE_MODAL,
		modal: modalName
	});
};

export const clearModalData = () => dispatch => {
	dispatch({
		type: modalTypes.CLEAR_MODAL_DATA
	});
};
