import React, { Fragment } from "react";
import { useTranslation } from "react-i18next";
import { Field, reduxForm } from "redux-form";
import TimerButton from "../../../components/TimerButton";

const renderField = props => {
	let {
		input,
		// label,
		placeholder,
		type,
		className,
		meta: { touched, error }
	} = props;

	if (touched) if (error) className = className + " warning";

	return (
		<Fragment>
			<input
				{...input}
				placeholder={placeholder}
				className={className}
				type={type}
			/>
			{touched && error && <span className="error-text">{error}</span>}
		</Fragment>
	);
};

function VerificationForm(props) {
	const { handleSubmit } = props;
	const { t } = useTranslation();
	return (
		<form onSubmit={handleSubmit}>
			<div className="form-group">
				<label htmlFor="register-email-2">
					{t("enter-code") + " " + props.phoneNumber}
				</label>
				<Field
					type="phone"
					className="form-control"
					id="register-phonenumber"
					component={renderField}
					placeholder={t("enter_code3")}
					name="code"
					required
				/>
			</div>

			<div className="d-flex">
				<button
					type="submit"
					className="btn btn-outline-primary-2"
					style={{
						marginTop: "10px",
						marginRight: "5px",
						width: "100%"
					}}
				>
					<span>{t("confirmation2")}</span>
					<i className="icon-long-arrow-right"></i>
				</button>
				<TimerButton
					resendText={t("resend-code")}
					onClick={props.onResend}
				/>
			</div>
		</form>
	);
}

export default reduxForm({ form: "verificationForm" })(VerificationForm);
