import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import MenuBodyTwo from "./MenuBodyTwo";

function HorizontalMenuThree() {
	const allMenuData = useSelector(state => state.menu);
	const [activeMenu, setActiveMenu] = useState(0);

	useEffect(() => {
		if (window.location.pathname === "/") setActiveMenu(0);
	});

	return (
		<nav className="main-nav horizontal-menu">
			<ul className="menu sf-arrows">
				{allMenuData.menuVertical
					.sort((a, b) => a.sort - b.sort)
					.map(menu => MenuBodyTwo(menu, activeMenu, setActiveMenu))}
			</ul>
		</nav>
	);
}

export default HorizontalMenuThree;
