import { cartTypes } from "../constants/action-types";
import { toast } from "react-toastify";
import i18n from "../i18n";
import { myReducerPersist } from "../store/managerLocalStorage";

const toastOptions = { position: "top-left" };
const initialState = {
	cart: [],
	shipping: "free",
	showCartList: false
};

function cartReducer(state = initialState, action) {
	switch (action.type) {
		case cartTypes.SET_USER_CART:
			if (action.cart.length < 1) {
				return { ...state, cart: state.cart };
			} else {
				return { ...state, cart: action.cart };
			}

		case cartTypes.EMPTY_CART:
			return { ...state, cart: [] };

		case cartTypes.ADD_TO_CART:
			return {
				...state,
				cart: [
					{
						...action.product,
						count: action.count,
						sum: action.product.data.newPrice * action.count,
						id: action.id ? action.id : 0
					},
					...state.cart.filter(
						product =>
							product.data.stock.id !==
							action.product.data.stock.id
					)
				]
			};

		case cartTypes.REMOVE_FROM_CART:
			if (!action.toast)
				toast.error(i18n.t("remove_cart_sucsessfuly"), toastOptions);

			return {
				...state,
				cart: state.cart.filter(
					record => record.data.variation.id !== action.productId
				)
			};

		case cartTypes.CLOSE_CART_MODAL:
			return { ...state, showCartList: false };

		case cartTypes.CHANGE_QTY:
			let newCart = [];
			for (let cart of state.cart) {
				if (cart.data.variation.id === action.productId) {
					if (
						parseInt(cart.data.stock.count) >=
						parseInt(action.count)
					) {
						newCart.push({
							...cart,
							count: parseInt(action.count),
							sum: parseInt(action.count) * cart.data.newPrice
						});
					} else {
						toast.error(i18n.t("amount-balance"), toastOptions);
						return state;
					}
				} else {
					newCart.push(cart);
				}
			}
			return {
				...state,
				cart: newCart
			};

		case cartTypes.REFRESH_STORE:
			return { ...state, cart: [], shipping: "free" };

		case cartTypes.CHANGE_SHIPPING:
			return { ...state, shipping: action.shipping };

		default:
			return state;
	}
}

export default myReducerPersist(
	{
		key: "ox-cartlist"
	},
	cartReducer
);
