import { debounce } from "./debounce";
function update() {
	localStorage.clear();
	window.location.reload();
	console.log("cache cleared");
}
function cacheClear() {
	let i = 0;

	window.addEventListener("keypress", e => {
		if (e.key === "2" && e.ctrlKey) debounce(() => update(), "iiis");
		if (e.key === "+") {
			i++;
			debounce(
				() => {
					if (i === 3) {
						localStorage.clear();
						window.location.reload();
						console.log("cache cleared");
					} else {
						i = 0;
					}
				},
				"clearcashe",
				300
			);
		}
	});
}

export default cacheClear;
