import { blocks } from "../constants/action-types";
import { myReducerPersist } from "../store/managerLocalStorage";

const initialState = {
	blocks: undefined
};

const anyReducer = (state = initialState, action) => {
	switch (action.type) {
		case blocks.SET_BLOCK:
			return {
				blocks: action.blocks
			};

		default:
			return state;
	}
};

export default myReducerPersist(
	{
		key: "ox-blocks"
	},
	anyReducer
);
