import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { connect, useSelector } from "react-redux";
import CartMenuTwo from "../partials/cart-menu-two";
import LoginModal from "../../features/modal/login-modal";
import YandexMap from "../../features/modal/YandexMapModal";
import { openModal } from "../../../actions/modal";
import getLocation from "../../features/getAddress";
import Logo from "../Logo";
import SearchForm from "../partials/SearchFormTwo/";
import ChangeLanguageTwo from "../../features/ChangeLanguageTwo";
import DesktopMenuModal from "../Menus/Desktop/DesktopMenuModal";
import SearchFormTwo from "../partials/SearchFormTwo/";
import HorizontalMenuHeaderBottom from "../../common/Menus/Desktop/HorizontalMenuHeaderBottom";
import { useTranslation } from "react-i18next";
import market from "../../../config/market";
import SearchModal from "../../features/modal/SearchModal";
import AuthFormTwo from "../../../views/login/forms/AuthFormTwo";
import { LinkComponent } from "../Menus/Desktop/LinkComponent";

function HeaderSix(props) {
	const {
		openModal,
		user,
		marketData,
		location: { address },
	} = props;
	const logo = marketData.settings.mainLogo;
	const { t } = useTranslation();
	const { productList } = useSelector((state) => state.choosenProducts);
	const { settings } = useSelector((state) => state.marketData);
	const [isInputClicked, setIsInputClicked] = useState(false);
	const { token } = useSelector((state) => state.user?.tokenData);
	const topMenu = useSelector((state) => state?.menu?.top);
	const icons = market.config.icons;
	const [search, setSearch] = useState(false);
	function openLoginModal(e) {
		e.preventDefault();
		openModal("login");
	}
	function openMapModal(e) {
		if (window.webview) {
			window.sendMessageToApp("OPEN_LOCATION");
		} else {
			openModal("map");
			e.preventDefault();
		}
	}

	useEffect(() => {
		const body = document.querySelector("body");
		if (!token && market.config.b2b_auth) {
			body.classList.add("b2b-auth");
		}
		if (!address) {
			getLocation();
		}
		return () => {
			body.classList.remove("b2b-auth");
		};
	}, [token, address]);

	function openMenu(e) {
		e.preventDefault();
		const body = document.querySelector("body");
		body.classList.add("mmenu-active-two");
	}

	return (
		<header className="header-six">
			<div className="header-top">
				<div className="for-container container">
					<div className="header-left">
						<div
							className="location"
							style={{ padding: "10px 20px 10px" }}
						>
							<Link
								className="location-link"
								to={process.env.PUBLIC_URL}
								style={{ color: "black" }}
							>
								{" "}
								<p
									className="d-flex justify-content-between align-items-center"
									onClick={openMapModal}
									style={{ marginBottom: "0px" }}
								>
									<img
										src={market.config.icons.location}
										alt="location"
										className="mr-2"
									/>
									{address ? (
										<span>{address.addressLine1}</span>
									) : (
										<span>{t("no-address-selected")}</span>
									)}
								</p>
							</Link>
						</div>
					</div>
					<div className="header-right">
						{topMenu.map((item, i) => (
							<LinkComponent
								menu={item}
								key={i}
								className="info"
							/>
						))}
						<ChangeLanguageTwo />
					</div>
				</div>
			</div>
			{/* <div className="container"> */}
			<div className="header-middle sticky-header">
				<div className="only-desktop container">
					<div className="header-left d-flex justify-content-between">
						<Logo logo={logo} />
						<div className="header-six-ver-menu">
							<DesktopMenuModal />
							<Link to="" onClick={openMenu}>
								<li className="menu-burger" />
							</Link>
						</div>
					</div>
					<div className="header-center">
						<SearchForm
							inputId={"searchInputDesktop"}
							setIsInputClicked={setIsInputClicked}
							isInputClicked={isInputClicked}
						/>
					</div>
					<div className="header-right">
						<div className="dropdown cart-dropdown">
							{user.id ? (
								<Link className="profile-link" to="/profile">
									<img
										src={icons.user}
										alt="profile"
										style={{
											fontSize: "20px",
											width: "20px",
											// height: "20px",
										}}
									/>
									<h6>{token ? t("profile") : t("login")}</h6>
								</Link>
							) : (
								<Link
									className="profile-link"
									to="#signin-modal"
									onClick={openLoginModal}
								>
									<img
										src={icons.user}
										alt="profile"
										style={{
											width: "20px",
											// height: "20px",
										}}
									/>
									<h6>{t("login")}</h6>
								</Link>
							)}
						</div>
						<Link
							to="/choosen-products-list"
							className="choosen-products-icon"
							style={{
								fontSize: "24px",
							}}
						>
							<div className="position-relative">
								<img
									src={icons.favourites}
									alt="heart"
									style={{
										width: "22px",
										// height: "22px",
									}}
								/>
								{productList.length > 0 ? (
									<span>{productList.length}</span>
								) : null}
							</div>
							<h6>{t("favourites")}</h6>
						</Link>
						<CartMenuTwo />
					</div>
				</div>
				<div className="only-mobile home-page-header for-container">
					{market.config.home === 5 && window.webview ? null : (
						<div className="container mobile-middle">
							<div
								className={`row px-2 w-100 align-items-center`}
							>
								<div className="col-8 px-0">
									<div className="location">
										<Link
											className="location-link"
											to={`${process.env.PUBLIC_URL}`}
											style={{ color: "#ffffff" }}
										>
											<p
												className="d-flex align-items-center"
												onClick={openMapModal}
												style={{ marginBottom: "0px" }}
											>
												<img
													src={
														market.config.icons
															.location
													}
													alt="location"
												/>
												{address ? (
													<span>
														{address.addressLine1 ||
															address.addressLine1}
													</span>
												) : (
													<span>
														{t(
															"no-address-selected"
														)}
													</span>
												)}
											</p>
										</Link>
									</div>
								</div>
								<div className="col-4 d-flex px-0 align-items-end flex-column">
									<div className="only-desktop">
										<Logo logo={logo} />
									</div>
									<div className="only-mobile">
										{settings && settings.footerLogo ? (
											<Logo
												logo={
													marketData.settings
														.footerLogo
												}
											/>
										) : null}
									</div>
								</div>
							</div>
						</div>
					)}
					<div
						className="container search"
						onClick={() =>
							market.config.home === 5 ? setSearch(!search) : null
						}
					>
						<div className="row searcher-row">
							<div className="col-12 only-mobile px-0">
								{!search && (
									<SearchFormTwo
										search={search}
										setSearch={setSearch}
										setIsInputClicked={setIsInputClicked}
										isInputClicked={isInputClicked}
									/>
								)}
							</div>
						</div>
					</div>
				</div>
			</div>
			<LoginModal />
			<YandexMap />
			{!token && market.config.b2b_auth ? <AuthFormTwo /> : null}

			{/* </div> */}
			<div className="header-bottom-one container">
				<HorizontalMenuHeaderBottom
					setIsInputClicked={setIsInputClicked}
				/>
			</div>

			<SearchModal search={search} setSearch={setSearch} />
		</header>
	);
}

function mapStateToProps(state) {
	return {
		marketData: state.marketData,
		phoneNumber: state.marketData.phoneNumber,
		user: state.user.user,
		location: state.user.location,
	};
}

export default connect(mapStateToProps, { openModal })(HeaderSix);
