export function divideArray(array) {
	const half = Math.round(array.length / 2);
	return [array.slice(0, half), array.slice(half)];
}

export const toMatrix = (arr, width = 3) =>
	arr &&
	arr.reduce(
		(rows, key, index) =>
			(index % width === 0
				? rows.push([key])
				: rows[rows.length - 1].push(key)) && rows,
		[]
	);

export const numbericalAlphabeticalSort = (arr) => {
	var isCyrillic = function (s) {
		return s && /^[\u0400-\u04FF ]+$/.test(s);
	};
	const filtered = arr.reduce((acc, cv) => {
		const firstLetter = cv.value.charAt(0);

		const key = !isNaN(firstLetter)
			? "0 - 9"
			: isCyrillic(firstLetter)
			? "А - Я"
			: firstLetter;
		if (!acc.hasOwnProperty(key)) {
			acc[key] = [cv.value];
		} else {
			acc[key].push(cv.value);
		}
		return acc;
	}, {});

	const sorted = Object.keys(filtered)
		.sort()
		.reduce((obj, key) => {
			obj[key] = filtered[key];
			return obj;
		}, {});
	return sorted;
};
export function groupBy(list, keyGetter) {
	const map = new Map();
	list &&
		list.forEach((item) => {
			const key = keyGetter(item);
			const collection = map.get(key);
			if (!collection) {
				map.set(key, [item]);
			} else {
				collection.push(item);
			}
		});
	return map;
}

export function unflatten(arr) {
	var tree = [],
		mappedArr = {},
		arrElem,
		mappedElem;

	// First map the nodes of the array to an object -> create a hash table.
	for (var i = 0, len = arr?.length; i < len; i++) {
		arrElem = arr[i];
		mappedArr[arrElem.id] = arrElem;
		mappedArr[arrElem.id]["children"] = [];
	}

	for (var id in mappedArr) {
		if (mappedArr.hasOwnProperty(id)) {
			mappedElem = mappedArr[id];
			// If the element is not at the root level, add it to its parent array of children.
			if (mappedElem.parent) {
				let parent = mappedArr[mappedElem["parent"]];
				if (parent) {
					parent["children"].push(mappedElem);
				}
			}
			// If the element is at the root level, add it to first level elements array.
			else {
				tree.push(mappedElem);
			}
		}
	}
	return tree;
}

export function deepEqual(object1, object2) {
	const keys1 = Object.keys(object1);
	const keys2 = Object.keys(object2);

	if (keys1.length !== keys2.length) {
		return false;
	}

	for (const key of keys1) {
		const val1 = object1[key];
		const val2 = object2[key];
		const areObjects = isObject(val1) && isObject(val2);
		if (
			(areObjects && !deepEqual(val1, val2)) ||
			(!areObjects && val1 !== val2)
		) {
			return false;
		}
	}

	return true;
}

export function isObject(object) {
	return object != null && typeof object === "object";
}

