import { anyTypes } from "../constants/action-types";

export const setSubdomain = subdomain => dispatch => {
	dispatch({
		type: anyTypes.SUBDOMAIN,
		name: subdomain
	});
};
export const setFiltersData = filtersData => dispatch => {
	dispatch({
		type: anyTypes.SET_FILTERS_DATA,
		filtersData: filtersData
	});
};

export const setSearchHistory = searchHistory => dispatch => {
	dispatch({
		type: anyTypes.SET_SEARCH_HISTORY,
		searchHistory: searchHistory
	});
};

export const setIsFirstTime = () => dispatch => {
	dispatch({
		type: anyTypes.IS_FIRST
	});
};

export const setStatePages = pages => dispatch => {
	dispatch({
		type: anyTypes.SET_PAGES,
		pages: pages
	});
};

// refresh local storage
export const refreshStore = current => dispatch => {
	dispatch({
		type: anyTypes.REFRESH_STORE,
		current
	});
};

export const setCollections = collections => dispatch => {
	dispatch({
		type: anyTypes.SET_COLLECTIONS,
		collections
	});
};

export const setSlides = slides => dispatch => {
	dispatch({
		type: anyTypes.SET_SLIDES,
		slides
	});
};

export const setBanners = banners => dispatch => {
	dispatch({
		type: anyTypes.SET_BANNERS,
		banners
	});
};

export const setBlogs = blogs => dispatch => {
	let blog = blogs?.map((b, i) => {
		return {
			...b,
			index: (b.index = i)
		};
	});
	dispatch({
		type: anyTypes.SET_BLOGS,
		blogs: blog
	});
};

export const hiddenBlog = id => dispatch => {
	dispatch({
		type: anyTypes.HIDDEN_BLOG,
		id
	});
};

export const setCategoryPath = path => dispatch => {
	dispatch({
		type: anyTypes.SET_CATEGORY_PATH,
		path
	});
};
