import React, { useEffect } from "react";
import Modal from "react-modal";
import SearchFormTwo from "../../common/partials/SearchFormTwo/";
import { useTranslation } from "react-i18next";
const customStyles = {
	overlay: {
		zIndex: 10001,
		backgroundColor: "#fff"
	}
};

function SearchModal({ search, setSearch }) {
	const { t } = useTranslation();

	useEffect(() => {
		if (search) {
			setTimeout(() => {
				let a = document.querySelector("#searchInput");
				a && a.focus();
			}, 200);
		}
	}, [search]);

	return (
		<Modal
			isOpen={search}
			onRequestClose={() => setSearch(!search)}
			style={customStyles}
			contentLabel="Search Modal"
			className="search-modal"
			id="search-modal"
		>
			<div className="modal-content">
				<div className="modal-body">
					<div className="search">
						<div className="searcher-row">
							<SearchFormTwo
								search={search}
								setSearch={setSearch}
							/>
							<button
								className="btn search-canceler"
								onClick={() => {
									setSearch(false);
								}}
							>
								{t("cancle")}
							</button>
						</div>
					</div>
				</div>
			</div>
		</Modal>
	);
}

export default SearchModal;
