import React, { useEffect } from "react";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import { useTranslation } from "react-i18next";
import { useDispatch } from "react-redux";
import { setIsFirstTime } from "../../actions/any";
import { changeLanguage } from "../../i18n";
import PreLoaderTwo from "../pre-loaders/PreLoaderTwo";
import { useHistory } from "react-router-dom";

export default function OnBoarding() {
	const { t } = useTranslation();
	const history = useHistory();
	const dispatch = useDispatch();

	const langs = [
		{
			text: `O'zbekcha`,
			key: "uz",
			img: "assets/images/vibo/uzbekistan.png"
		},
		{
			text: `Русский`,
			key: "ru",
			img: "assets/images/vibo/russia.png"
		}
	];
	const boards = [
		{
			text: t("onBoardText1"),
			image: "/assets/images/vibo/board1.svg"
		},
		{
			text: t("onBoardText2"),
			image: "/assets/images/vibo/board2.svg"
		},
		{
			text: t("onBoardText3"),
			image: "/assets/images/vibo/board3.svg"
		},
		{
			text: t("onBoardText4"),
			image: "/assets/images/vibo/board4.svg"
		}
	];

	const boardOption = {
		rewind: true,
		perPage: 1,
		arrows: false
	};

	useEffect(() => {
		if (language) {
			const time = setInterval(() => {
				nextStep();
			}, 3000);
			return () => {
				clearInterval(time);
			};
		}
	}, []);

	function nextStep() {
		const splideItems = document.getElementsByClassName("splide__slide");
		if (splideItems[3].className.includes("is-active")) {
			dispatch(setIsFirstTime());
			history.push("/login", history.location.pathname);
		} else {
			window.dispatchEvent(
				new KeyboardEvent("keydown", { key: "ArrowRight" })
			);
		}
	}

	const language = localStorage.getItem("language");

	function addLanguage(lang) {
		localStorage.setItem("language", false);
		changeLanguage(lang);
		history.push("/login");
	}

	return (
		<div
			style={{
				maxWidth: "100%",
				height: "100vh",
				display: "flex"
			}}
			className="on-board"
		>
			{!language ? (
				<>
					<PreLoaderTwo />
					<div className="on_board_lang">
						<div className="text-center">
							<p className="title">Добро пожаловать</p>
							<img
								className="logo"
								src="/assets/images/vibo/preLoaderLogo.svg"
								width={"100%"}
								alt="logo vibo"
							/>
							<div className="languages">
								{langs.map((lang, i) => {
									return (
										<div
											onClick={() =>
												addLanguage(lang.key)
											}
											className="language"
											key={i}
										>
											<div className="lang_img">
												<img
													src={lang.img}
													alt={lang.text}
												/>
											</div>
											<p>{lang.text}</p>
										</div>
									);
								})}
							</div>
						</div>
					</div>
				</>
			) : (
				<>
					<Splide options={boardOption}>
						{boards.map((board, i) => {
							return (
								<SplideSlide
									style={{ padding: "10px" }}
									key={i}
								>
									<img src={board.image} alt={`board ${i}`} />
									<p className="text-center">{board.text}</p>
								</SplideSlide>
							);
						})}
					</Splide>
					<button
						style={{
							position: "absolute",
							bottom: "80px",
							left: "50%",
							transform: "translateX(-50%)",
							margin: "0 auto"
						}}
						onClick={() => {
							nextStep();
						}}
						className="next-btn"
					>
						<i className="icon-arrow-right"></i>
					</button>
				</>
			)}
		</div>
	);
}
