import React, { useEffect, useState } from "react";

export default function PreLoaderTwo() {
	const logo = "/assets/images/vibo/preLoaderLogo.svg";

	const [hidden, setHidden] = useState("flex");

	useEffect(() => {
		const time = setTimeout(() => {
			setHidden("none");
		}, 5000);

		return () => {
			clearTimeout(time);
		};
	}, []);

	return (
		<div
			className="pre-loader-two"
			style={{
				display: hidden
			}}
		>
			<div className="bg">
				<div className="logo">
					<img src={logo} alt="logo" />
				</div>
			</div>
		</div>
	);
}
