import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { connect, useSelector } from "react-redux";
import { getCartCount, getCartTotal } from "../../../services";
import { removeFromCartState, closeCartModal } from "../../../actions/cart";
import { getImageUrl } from "../../../utils/oxMethods";
import { formatterCurrency } from "../../../utils/string";
import { useTranslation } from "react-i18next";
import { removeToCartApi } from "../../../api/cart";
import market from "../../../config/market";

function getNameProductOrVariation(item) {
	if (item.product) {
		return item.product.name;
	}
	return item.data.variation.name;
}

function CartMenuThree(props) {
	const {
		cartlist,
		removeFromCartState,
		closeCartModal,
		showCartList,
	} = props;
	const [dropCart, setDropCart] = useState([]);
	const [cartActive, setCartActive] = useState(false);
	const { token } = useSelector((state) => state.user.tokenData);
	const { t } = useTranslation();
	let total = getCartTotal(cartlist);

	useEffect(() => {
		setDropCart(cartlist);
		setTimeout(() => {
			closeCartModal();
		}, 2000);
	}, [showCartList, cartlist]);

	function removeProduct(id, item) {
		removeFromCartState(id);
		if (token && item.id) {
			removeToCartApi(item.id, token);
		}
	}

	return (
		<div
			className="dropdown cart-dropdown"
			onMouseEnter={() => setCartActive(true)}
			onMouseLeave={() => setCartActive(false)}
		>
			<Link
				style={{ width: "25px", height: "25px" }}
				to={`${process.env.PUBLIC_URL}${token ? "/checkout" : "/cart"}`}
				onClick={() => {
					setCartActive(false);
				}}
				className="dropdown-toggle d-flex flex-column"
				role="button"
				data-toggle="dropdown"
				aria-haspopup="true"
				aria-expanded="false"
				data-display="static"
			>
				<div className="position-relative">
					<img
						src={market.config.icons.cart}
						alt="shopping bag"
						style={{ width: "22px", height: "22px" }}
					/>
					{cartlist.length ? (
						<span className="cart-count">
							{getCartCount(cartlist)}
						</span>
					) : null}
				</div>
				<h6>{t("basket")}</h6>
			</Link>

			<div
				className={`dropdown-menu dropdown-menu-right${
					!dropCart.length ? " text-center" : ""
				}${showCartList || cartActive ? " visible-cart" : ""}`}
			>
				<div className="cart-body">
					<div className="title-closer">
						<span>Корзина</span>
						{/* <button
						className="closer"
						onClick={() => setCartActive(false)}
					>
						<img src={market.config.icons.closer} alt="close" />
					</button> */}
					</div>
					{!dropCart.length ? (
						<p>{t("empty-cart")}</p>
					) : (
						<>
							<div className="products-gradient">
								{dropCart.length > 6 ? (
									<div className="gradient-div" />
								) : null}
								<div className="dropdown-cart-products">
									<div className="content-body">
										{dropCart.map((item, index) => {
											return (
												<div
													className="product"
													key={index}
												>
													<div className="product-detail-left">
														<div className="product-image-container">
															<Link
																to={
																	`${process.env.PUBLIC_URL}/product/` +
																	item.data
																		.variation
																		.id
																}
																className="product-image"
															>
																<img
																	src={getImageUrl(
																		item
																			.data
																			.variation
																			.images[0]
																	)}
																	data-oi={
																		item
																			.data
																			.variation
																			.images[0]
																	}
																	alt="product"
																/>
															</Link>
														</div>
														<div className="title-price">
															<h4 className="product-title">
																<Link
																	to={
																		`${process.env.PUBLIC_URL}/product/` +
																		item
																			.data
																			.variation
																			.id
																	}
																>
																	{getNameProductOrVariation(
																		item
																	)}
																</Link>
															</h4>
															<div className="conut-price">
																<span className="cart-product-qty">
																	{t(
																		"product-conut"
																	)}
																	:&nbsp;
																	{item.count}
																</span>
																<span className="cart-product-info">
																	{item.data
																		.discount
																		? item
																				.data
																				.newPrice
																		: item
																				.data
																				.oldPrice}{" "}
																	UZS
																</span>
															</div>
														</div>
													</div>

													<button
														className="btn-remove"
														style={{
															position:
																"relative",
															width:
																"max-content",
															fontSize: "12px",
														}}
														title="Remove Product"
														onClick={() =>
															removeProduct(
																item.data
																	.variation
																	.id,
																item
															)
														}
													>
														<img
															src={
																market.config
																	.icons
																	.deleteIcon
															}
															alt="delete"
														/>
													</button>
												</div>
											);
										})}
									</div>
								</div>
							</div>
							<div className="dropdown-cart-total">
								<span className="cart-total">
									{t("subtotal")} :
								</span>

								<span className="cart-total-price">
									{formatterCurrency(total)}
								</span>
							</div>

							<div className="dropdown-cart-action">
								<Link
									to={`${process.env.PUBLIC_URL}${
										token ? "/checkout" : "/cart"
									}`}
									onClick={() => {
										setCartActive(false);
									}}
									style={{ width: "100%" }}
									className="btn btn-primary"
								>
									{t("enter-order")}
								</Link>
							</div>
						</>
					)}
				</div>
			</div>
		</div>
	);
}

function mapStateToProps(state) {
	return {
		cartlist: state.cartlist.cart ? state.cartlist.cart : [],
		showCartList: state.cartlist.showCartList,
	};
}

export default connect(mapStateToProps, {
	removeFromCartState,
	closeCartModal,
})(CartMenuThree);
