import React from "react";
import market from "../../config/market";
import MobileBottomNavbarFour from "./MobileBottomNavbarFour";

const MobileBottomNavbar = React.lazy(() => import("./MobileBottomNavbar"));
const MobileBottomNavbarTwo = React.lazy(() =>
	import("./MobileBottomNavbarTwo")
);
const MobileBottomNavbarThree = React.lazy(() =>
	import("./MobileBottomNavbarThree")
);

export default function MobileNavbar() {
	switch (market.config.mobileBottomMenu) {
		case 1:
			return <MobileBottomNavbar home={market.config.home} />;
		case 2:
			return <MobileBottomNavbarTwo home={market.config.home} />;
		case 3:
			return <MobileBottomNavbarThree home={market.config.home} />;
		case 4:
			return <MobileBottomNavbarFour home={market.config.home} />;
		default:
			return <MobileBottomNavbar home={market.config.home} />;
	}
}
