import { choosenProductTypes } from "../constants/action-types";
import { myReducerPersist } from "../store/managerLocalStorage";

const initialState = {
	productList: []
};

const choosenProducts = (state = initialState, action) => {
	switch (action.type) {
		case choosenProductTypes.SET_PRODUCT:
			if (
				state.productList.find(
					p => p.stock.id === action.product.stock.id
				)
			) {
				return {
					productList: [
						...state.productList.filter(
							p => p.stock.id !== action.product.stock.id
						)
					]
				};
			} else {
				return {
					productList: [...state.productList, action.product]
				};
			}
		default:
			return state;
	}
};

export default myReducerPersist(
	{
		key: "ox-choosenProducts"
	},
	choosenProducts
);
