import React, { useState } from "react";
import { changeLanguage, getLang } from "../../i18n";

export default function ChangeLanguage() {
	const [language, setLanguage] = useState(getLang());

	const onChange = lang => {
		changeLanguage(lang);
	};

	return (
		<div className="lang-selector">
			<div className={"langBut " + (language === "uz" ? "active" : "")}>
				<button
					className="text-white"
					onClick={() => {
						onChange("uz");
						setLanguage("uz");
					}}
				>
					UZ
				</button>
			</div>
			<div className={"langBut " + (language === "ru" ? "active" : "")}>
				<button
					className="text-white"
					onClick={() => {
						onChange("ru");
						setLanguage("ru");
					}}
				>
					РУ
				</button>
			</div>
		</div>
	);
}
