import { menuTypes } from "../constants/action-types";
import { unflatten } from "../utils/array";

export const getMenuAction = (menus) => (dispatch) => {
	dispatch({
		type: menuTypes.RECEIVE_MENUS,
		top: unflatten(menus.top ? menus.top : []),
		footer: unflatten(menus.footer),
		menuHorizontal: unflatten(menus.horizontal),
		other: unflatten(menus?.other),
		menuVertical: unflatten(menus.vertical),
		menuList: [
			...menus.horizontal,
			...(menus.other ? menus.other : []),
			...menus.vertical,
			...menus.footer,
			...(menus.top ? menus.top : []),
		],
	});
};
